const soundsList=
[
    {title:"A L’OUEST LE SOLEIL SE COUCHE",words:"A l’Ouest le soleil se couche\nIl arrive à la fin de sa course\nEt je vois combien ce temps est là \nOh le temps du coucher du soleil\n\nChœur : \nC’est la fin (3x) de la course\nC’est la fin (3x) de toute chose\n\nEt voici que la lumière du soir\nA l’Ouest pour moi a paru\nElle manifeste le fils de l’homme \nEt je crois c’est vrai je crois\n\nPèlerin c’est la fin de la course\nIl faut vaincre le monde et partir\nAttention quelle heure est-il\nIl faut vaincre le monde et partir\n\n"},{title:"A LA CROIX AUX PIEDS DU MAITRE",words:"A la croix aux pieds du maître à genoux j’ai fait mon choix\nSon disciple je veux être tout près de lui par la foi\nBrebis de sa bergerie nul ne m’ôte de sa main\nSur les monts dans les prairies oui mon bonheur est divin\n\nChœur :\nA la croix aux pieds du maître à genoux j’ai fait mon choix\nSon disciple je veux être tout près de lui par la foi\n\n\n\nDepuis lors oh choix splendide\nLa joie demeure dans mon cœur \nAdieu mes soucis perfides par Jésus je suis vainqueur\nAdieu vaines convoitises du monde qui me tentèrent\nJ’ai la paix promise dans le sang de Golgotha\n\nBientôt ma tâche accomplie j’entrerai tout droit au port \nOù m’attend douce patrie le grand vainqueur de la mort\nLà je chanterai ta gloire oh toi qui m’a tant aimé\nEt dont l’œuvre expiatoire pour toujours m’a racheté (e)\n\n"},{title:"A TRAVERS LE MONDE",words:"A travers le monde, l’Esprit est en mouvement\nA travers le monde, comme les prophètes l’avaient prédit\nA travers le monde, il y a une puissante révélation\nDe la venue du Seigneur, comme l’eau qui couvre la mer\nAu fond de mon cœur, le Saint-Esprit agit\nAu fond de mon cœur, comme les prophètes l’avaient dit\nAu fond de mon cœur, il y a une puissante révélation\nDe la venue du Seigneur, et de la puissance de l’Esprit\n\n"},{title:"ACCEPTE MON OFFRANDE",words:"Accepte mon offrande\nBien aimé fils de Dieu\nEt que sur moi descende\nLa flamme du Saint lieu\n\nC’est mon joyeux service\nD’offrir à Jésus-Christ\nEn vivant sacrifice \nMon corps et mon esprit\n\n\nJ’abandonne ma vie\nSans regret ni frayeur\nA ta grâce infinie\nO mon libérateur\n\n\nQu’un feu nouveau s’allume \nPar ton amour en moi\nEt dans mon cœur consume \nCe qui n’est pas à toi\n\n\nViens Jésus, sois mon maître \nPar ton sang racheté\nA toi seul je veux être\n\n"},{title:"ALLELUIA NOUS RESSUSCITERONS",words:"Au matin de résurrection\nLa trompette de Dieu sonnera\nNous vivrons alléluia nous vivrons\nLes saints viendront se réjouissant\nAucune larme ne sera trouvée\nNous vivrons alléluia nous vivrons\n\nChœur :  \n\nOui nous ressusciterons alléluia amen\nOui nous ressusciterons au matin de résurrection\nSont brisées les barres de la mort\nOui nous ressusciterons amen\n\n\nAu matin de résurrection\nOh ! Quelle rencontre ça sera\nNous vivrons alléluia nous vivrons\nQuand nos pères et nos mères \nEt bien-aimés nous verrons\nNous vivrons alléluia nous vivrons\n\n\nAu matin de résurrection\nPour moi c’est une pensée bénie\nNous vivrons alléluia nous vivrons\nJe verrai mon sauveur béni\nQui pour moi mourut librement\nNous vivrons alléluia nous vivrons\n\n\nAu matin de résurrection\nDans les airs nous le rencontrerons\nNous vivrons alléluia nous vivrons\nEtre enlevé dans la gloire\nVers notre demeure pure et belle \nNous vivrons alléluia nous vivrons\n\n"},{title:"AMENE-LES",words:"Ecoute la voix du berger\nVenant du désert sombre et sec\nAppelant la brebis perdue\nQui s’est éloignée du troupeau\n\nChœur : \n\nAmène-les amène-les\nEt sors-les de la vie du péché\nAmène-les amène-les\nAmène-les perdus à Jésus\n\nQui aidera ce bon berger\nA retrouver tous les perdus\nA les ramener au troupeau\nLà où ils seront protégés\n\nAu désert écoute leur cri\nVenant des montagnes élevées\nEcoute le maître te dit\nVas me retrouver les brebis\n\n"},{title:"APPARTIENS TU A L’EPOUSE",words:"Appartiens-tu à l’épouse de Jésus notre Seigneur\nSerait-ce pour toi un jour de joie ou un jour de frayeur\nQuand il reviendra ton vêtement sera-t-il pur et blanc\nOui c’est l’instant d’être pour le festin\n\nChœur : \n\nMaranatha, alléluia ! (3x)\nJésus vient et son festin est préparé\n\nPour cela pourrais-tu renoncer au monde et ses plaisirs\nMarches-tu dans la lumière, es-tu prêt pour l’accueillir\nSi tu n’es pas encore libre, alors Jésus te dit : viens\nCar c’est l’instant d’être prêt pour le festin\n\nL’état mondial nous révèle aussi que Jésus vient bientôt\nLorsque tout va s’accomplir ta délivrance viendra d’en haut\nDans les ténèbres profondes brillera l’étoile du matin\nOui c’est l’instant d’être prêt pour le festin\n\nQue mon cœur attend ce règne sans mort et sans destruction\nPassées seront les choses anciennes c’est une nouvelle création\nLà, je pourrai rencontrer Jésus mon Roi et tous les saints\nOui c’est l’instant d’être prêt pour le festin\n\n"},{title:"APPORTANT LES FRUITS",words:"Semant le matin, les semences d’amour\nSemant en pleine marée, la veille de rosée\nAttendant la moisson, le temps de récolte\nNous serons dans la joie en portant les fruits\n\nChœur :\n\nApportant les fruits, apportant les fruits\nNous serons dans la joie apportant les fruits\n\nSemant sous le soleil, semant dans les ombres\nNe craignant ni nuage, ni le froid d’hiver\nBientôt la récolte, et le travail fini\nNous serons dans la joie apportant les fruits\n\nAvançant avec pleurs semant pour le maître\nBien  que perdant la force, esprit affligé\nQuand nos pleurs finiront, il nous accueillera\nNous serons dans la joie apportant les fruits\n\n"},{title:"AU NOM DE JESUS",words:"Au nom de Jésus  (2x)\nNous avons la victoire\nAu nom de Jésus (2x)\nVenez monter vers lui\n\nSi nous avançons au nom de Jésus\nDites-moi qui peut résister\nAu nom de Jésus\nNous avons la victoire\n\nFils de Dieu en avant\nDans la puissance du Saint-Esprit\nFils de Dieu en avant\nNous avons la puissance en son sang\nFils de Dieu en avant\nDans la puissance du Saint-Esprit\n\n"},{title:"AUTREFOIS EPUISE ET SANS ESPOIR",words:"Autrefois épuisé et sans espoir\nJe voyageais dans l’univers des déceptions\nEt puis un jour épuisé dans le désespoir\nJe veux crier au secours dites-moi la solution\n\nOui la solution \nC’est Jésus le Roi des rois\nC’est Jésus le fils de Dieu\nC’est Jésus le Sauveur \nJe l’ai reçu et je peux le dire\nEt quand Jésus visite un cœur \nQuand Jésus reçoit un cœur \nQuand Jésus vient dans un cœur \nLa vie est là\nOh si tu es malheureux\nSi tu es abandonné\nSi tu es méprisé\nJésus t’attend\n\nOh si tu es très malade\nSi tu es possedé (e)\nSi tu es rejeté\nViens à Jésus\nLa solution\n\n"},{title:"AVANCON-NOUS JOYEUX",words:"Avançons-nous joyeux toujours joyeux\nVers le pays de tous les bienheureux\nVers la demeure où Jésus pour nous prie\nMarchons marchons c’est là notre patrie\nAvançons-nous joyeux toujours joyeux\nVers le pays de tous les bienheureux\n\nDes chants d’amour retentissent aux cieux\nQuels doux concerts ! Hymnes des bienheureux\nNous entendrons votre sainte harmonie\nQuand nous aurons atteint notre patrie\nAvançons-nous joyeux toujours joyeux\nVers le pays de tous les bienheureux\n\nLà-haut là-haut ! Tu nous attends Seigneur\nCar c’est à toi qu’appartient notre cœur \nViens ô Jésus c’est le cri de l’Eglise\nRecueille-nous dans la terre promise\nLà nous  serons joyeux toujours joyeux\nC’est le pays de tous les bienheureux\n\nHeureux bientôt dans un monde nouveau\nNous prendrons place au banquet de l’agneau\nLà plus de cris plus de deuil plus de larmes\nPlus de péché de douleurs ni d’alarmes\nLà nous serons joyeux toujours joyeux\nC’est le pays de tous les bienheureux\n\n"},{title:"BEAU JOUR",words:"Oh quel beau jour, jour de mon choix\nSur toi mon sauveur et mon Dieu\nOh mon cœur rayonne de joie\nExprime partout son ravissement\n\nChœur :\n\nQuel beau jour, quel beau jour\nQuand Jésus ôta mes péchés\nIl m’apprit à veiller, prier\nEt vivre chaque jour dans la joie\nQuel beau jour, quel beau jour\nQuand Jésus ôta mes péchés\n\n\nOh quel beau lien qui scelle mes vœux\nA lui qui mérite mon amour\nPuisse les chants remplir sa maison\nPendant que je vais à l’autel\nL’accord est fait avec Jésus\nIl est à moi, je suis à lui\nIl me conduit et je le suis\nBrûlant de confesser sa voix\nOh ! Quel précieux amour\n\nOh quel précieux amour du père\nPour la race déchue d’Adam\nIl a donné son fils unique\nPar grâce il nous rachète\n\n"},{title:"BENI SOIT LE LIEN",words:"Béni soit le lien qui\nUnit nos cœurs en Christ\nLa communion fraternelle\nFaite comme un arc-en-ciel\n\nDevant le trône du père\nNous versons nos prières\nNos cœurs et foi nos vies sont un\nNous nous conformons à ceci\n\nNous partageons nos peines\nAinsi que nos fardeaux\nEt souvent concourrait chacun \nLes larmes de saintes fatigues\n\nQuand nous nous séparerons\nCela coupe la paix (2x)\nMais dans le cœur nous\nSommes unis et c’est à nous revoir\n\n"},{title:"BIENTOT BIENTOT",words:"Bientôt, bientôt quand Jésus viendra\nQuand tous les Saints se rassembleront\nNous chanterons l’histoire de notre salut\nNous comprendrons toutes choses bientôt, bientôt\nBientôt, bientôt (chantre)\n\n"},{title:"BIENTOT DANS LA DOUCEUR",words:"Il y a un endroit plus beau qu’un jour\nEt par la foi nous pouvons le voir\nCar le père nous attend sur le chemin\nPour nous préparer là un palais\n\nChœur : \n\nBientôt dans la douceur\nNous nous rencontrerons sur cette rive\nBientôt dans la douceur\n\nNous nous rencontrerons sur cette rive\n\nNous chanterons sur ce beau rivage\nChants mélodieux de bénédictions\nNos esprits ne s’affligeront plus\nPas un soupir pour les bénédictions\n\nA notre généreux père céleste\nNous offrirons nos hommages et louanges\nPour le glorieux don de son amour\nEt les bénédictions consacrées\n\n"},{title:"BIENTOT L’HEURE SONNERA",words:"Bientôt l’heure sonnera\nPour moi de rendre compte\nA cet instant suprême de ma vie\nQue pourrai-je dire à mon père\n\nSi ma vie n’est que débauche\nCorruption, iniquité\nIl est temps de la changer\nBien avant ce grand jour\n\n"},{title:"C’EST COMME LE CIEL POUR MOI",words:"Nous trouvons de gens qui\nNe peuvent pas comprendre\nPourquoi nous sommes libres heureux\nNous passons le Jourdain pour le beau Canaan\n\nChœur :\nOh c’est comme le ciel pour moi (2x)\nJe passe le Jourdain pour le beau Canaan\nEt c’est comme le ciel pour moi\n\nOh quand je suis content et je chante et je crie\nLe démon ne croit pas je vois\nMais je suis rempli de l’esprit pas de doute\nC’est cela qui est mon affaire\n\n"},{title:"C’EST LA PROMESSE DE LA DERNIER PLUIE",words:"C’est la promesse de la dernière pluie\nLève les yeux et vois le blé mûr\nSignes puissants et miracles\nAu nom de Jésus\nBois ô bois mon peuple\nC’est la dernière pluie\n\n"},{title:"C’EST LE TEMPS DU SOIR",words:"C’est le temps du soir\nLa lumière est apparue\nLa parole parlée a touché mon cœur \nAlléluia je suis né du Saint-Esprit\n\n"},{title:"C’EST PAR LA FOI",words:"C’est par la foi que voyant l’invisible\nIls se sont levés à l’appel du Seigneur\nRien avec lui ne semblait impossible\nQuand sa promesse enflamme notre cœur\n\nChœur :\n\nBrûle en nos âmes flamme sacrée\nEt nous suivons les témoins d’autrefois\nEnvironnés de grandes nuées\nLes regards sur Jésus chef de la foi\n\nC’est par  la foi qu’ils fuyaient l’esclavage\nIls passaient la mer et ils étaient valeureux\nGrâce au Seigneur se frayant un passage\nVers la cité préparée pour eux\n\nC’est par la foi que dans l’ignominie\nIls ont enduré des épreuves sans fin\nSans obtenir ici-bas de patrie\nMais saluant le triomphe lointain\n\nVoix des héros qui combattent sous ce signe\nSuis le saint exemple et partage leur sort\nEux dont le monde ne fut jamais digne\nEux les témoins ils nous parlent encore\n\n"},{title:"çA BOUILLONNE",words:"Ca bouillonne (3x) dans mon cœur \nJe veux chanter, je veux crier\nDepuis que Jésus m’a purifié\nY a des gens, qui ne comprennent pas \nPourquoi je suis comme ça\nCa bouillonne (5x) le jour, la nuit\n\n"},{title:"CE N’EST PAS PAR TA FORCE",words:"Ce n’est pas par ta force \nNi ta puissance\nMais par mon Esprit, dit le Seigneur\n\nCette montagne se déplacera (3x)\nPar mon Esprit dit le Seigneur\n\n"},{title:"CE VIEUX MONDE NE PEUT RIEN SUR MOI",words:"Ce vieux monde ne peut rien sur moi\nCar bientôt je m’en irai\nJe suis déjà consacré\nEt je porte mon habit de noce\n\n"},{title:"CELEBRONS JESUS LE SEIGNEUR",words:"Célébrons Jésus le Seigneur, il est roi (2x)\nIl est roi, il roi, Roi des rois\nCélébrons Jésus le Seigneur il est roi\n\nBénissons le nom de Jésus, il est roi (2x)\nIl est roi, il roi, Roi des rois\nBénissons le nom de Jésus, il est roi\n\nLa paix la joie le bonheur, c’est pour nous (2x)\nC’est pour nous c’est Jésus qui nous les donne\nLa paix la joie le bonheur, c’est pour nous \n\nIl est le Dieu tout puissant\nIl est le grand médecin\nIl est le grand Jéhovah\nIl est la colonne de feu\nIl est la grande nuée\nIl est la lumière du soir\nIl est la parole parlée\nIl est l’étoile du matin\nIl est le rocher des âges etc., etc. \n\n"},{title:"CETTE PAROLE A MOISE",words:"Cette parole a Moïse\nJosué et Caleb l’ont soutenue jusqu’au bout\nOh Seigneur tu m’as envoyé mon Moïse              \nLaisse-moi le soutenir dans la joie\nComme la peine\nC’est cela ma prière\n\nRefrain : Jusqu’à quand Seigneur \n                Cette parole se fera chair en moi\n                Oh Seigneur Jésus\n\nCette parole a Marie, sous la visitation de l’ange\nElle pouvait donner la vie, par une simple salutation\nJe suis sous la même visitation de l’ange\nOh Seigneur Jésus\nLaisse-moi donner la vie à ce monde qui se meurt\nC’est cela ma prière\n\nCette parole a Branham, Jésus-Christ\nLe roi du ciel a été identifié\nMoi j’ai reçu le message, laisse-toi  t’identifier\nAfin que le monde puisse te voir\nC’est cela ma prière\n\n"},{title:"CHERCHEZ D’ABORD LE ROYAUME DES CIEUX",words:"Cherchez avant tout le royaume des cieux\nEt sa justice\nEt toutes choses vous seront données en plus\nAlléluia, alléluia, alléluia\n\nChœur : \n\nAlléluia, alléluia, alléluia\nAlléluia, alléluia, alléluia\n\nL’homme ne vivra pas de pain seulement\nMais de toute parole\nQui sortira de la bouche de Dieu\nAlléluia, alléluia, alléluia\n\n"},{title:"CHRIST EST LA PAIX RECHERCHEE",words:"Christ est la paix recherchée de mon âme\nO Seigneur soutien ma foi\nPrends-moi Seigneur dans mon état\nDieu de Jacob soutiens ma foi\n\n"},{title:"CHRIST EST MA VIE",words:"Je dois voyager au monde comme un esquif sur les eaux\nEt la tempête qui gronde déjà fait mugir les flots\n\nChœur :\nChrist est ma vie il est mon roi\nToujours il prie notre père pour moi\n\nIl m’a donné la boussole qui sans erreur pointe au port\nC’est sa vivante parole avec elle point de mort\n\nSi le vent m’est favorable si le ciel est clair et pur\nCe pilote charitable me dit : veille rein n’est sûr\n\nEt si vient le sombre orage si tout semble menaçant\nMon pilote m’encourage de son regard tout puissant\n\nJe ne crains ni naufrage ni pirate ni récif\nJ’ai déjà reçu le gage du salut de mon esquif\n\n"},{title:"COMBIEN DE TEMPS",words:"Combien de temps faut-il encore au Seigneur\nCombien de temps pour revenir\nEncore de longues années encore de longs mois\nPour moi il ne reste que quelques jours\n\n"},{title:"COMBIEN DE TEMPS PENSEZ-VOUS",words:"Chœur :\nCombien de temps pensez-vous\nNous reste encore mes frères\nIl est plus tard \nQue vous ne le pensez\nOui, le Seigneur revient bientôt\n\nVoyez comment toutes les prophéties \nS’accomplissent  sous nos yeux\nAnnonçant la venue du Seigneur\nOh ! Bientôt s’ouvriront les cieux\n\nLes signes se voient dans les cieux\nSur la terre et partout\nProclamant qu’il n’y a plus de temps \nRepentez-vous ou vous mourrez \n\nLa fin de toute chose est proche\nSoyez alors sages et sobres\nPrenez toutes les armes de Dieu\nVivez la vie de bon chrétien\n\nLes Balaam, les Koré et Datan\nS’élèvent dans ce temps \nReniant que le message est vrai\nFaites attention, la séduction\n\nLa ville Los-Angeles s’écroule \nLa prophétie s’accomplit\nObservez la grande ville s’écroule \nA dit le messager de l’heure\n\nBientôt dans le ciel, enlevés\nNous irons à la maison\nDe celui qui nous a sauvés\nEt avec lui nous règnerons\n\n"},{title:"COMBIEN J’AIME JESUS",words:"Combien j’aime Jésus (3x)\nCar il m’a tant aimé\nOh, puis-je l’oublier (3x)\nJésus m’a tant aimé\nPour moi il est si merveilleux (3)\nCar il m’a tant aimé \n\n"},{title:"COMME UN FLEUVE IMMENSE",words:"Comme un fleuve immense est la paix de Dieu\nParfaite elle avance vainqueur en tout lieu\nParfaite elle augmente constamment son cours\nParfaite sa pente s’abaisse toujours\n\nChœur : \nFondés sur Dieu même nos cœurs à jamais\nOnt pour bien suprême sa parfaite paix\n\nSous ta main meurtrie se brise ô mon roi\nToute arme ennemie qu’on forge pour moi\nRien ne peut me nuire ni troubler mon cœur \nTu veux me conduire tu seras vainqueur\n\nTu traces ma voie j’y marche avec foi\nL’épreuve et la joie me viennent de toi\nCadran de nos vies marque chaque jour\nLes heures bénies du soleil d’amour\n\nChœur : \nSelon ta promesse, ô Jésus mon roi \nJ’ai trouvé sans cesse paix parfaite en toi\n\nOcéan de gloire paix de mon sauveur\nGage de victoire trésor de bonheur\nTa grâce infinie coulant nuit et jouir\nInonde ma vie de vagues d’amour\n\n"},{title:"COMMENT NE PAS TE REMERCIER",words:"J’étais éloigné de la parole\nTu m’as envoyé ton saint prophète\n\nChœur : \nComment ne pourrais-je te louer\nPour tout ce que tu as fait pour moi\nTu as toujours été mon secours\nChaque fois que l’ennemi me combat\n\nQuand les gens parlent en mal de moi\nJ’ai trouvé en toi mon seul appui\n\nJ’étais terrassé par la maladie \nTu as compatis tu m’as guéri\n\nJ’étais sans aucune consolation \nTu m’as rempli de ton Saint-Esprit\n\n"},{title:"CONNAIS-TU CETTE CITE",words:"Connais-tu cette cité la cité céleste\nDans ses murs tout est clarté plus d’ombre funeste\nPèlerins et voyageurs ici-bas dans les douleurs\nToujours ce trésor nous reste la cité céleste\n\nConnais-tu cette cité la cité bénie\nOù tout est félicité sublime harmonie\nL’agneau seul est son soleil éclat sans pareil\nIllumine et vivifie la cité bénie\n\nD’or de perle et d’azur est notre\nConnais-tu le fleuve pur l’arbre de la vie\nPlus de fardeau à jamais, pour les élus tout est paix\nDe Dieu la cité chérie c’est notre patrie\n\nO Salem repos si doux mon cœur te désire\nAprès son chef et son époux l’Eglise soupire\nEtre à toujours devant toi\nMon Seigneur mon divin roi\nPour te chanter et te dire mon cœur te désire\n\n"},{title:"CREE EN MOI UN CŒUR PUR",words:"Oh Dieu crée en moi un cœur pur (2x)\nRenouvelle en moi un esprit bien disposé\nOh Dieu crée en moi un cœur pur\nRenouvelle en moi un esprit bien disposé\nOh Dieu crée en moi un cœur pur\n\n"},{title:"CROIS SEULEMENT",words:"Crains rien petit troupeau de la croix au trône\nDe la mort à la vie il partit pour les siens\nTout pouvoir sur terre tout pouvoir au ciel\nEst donné à toi pour l’amour du troupeau\n\nChœur :\n\nCrois seulement crois seulement\nTout est possible crois seulement\n\nCrains rien petit troupeau, il est parti devant\nTon berger fait le choix, du chemin à suivre\nIl va purifier l’eau du Marra pour toi\nA Gesthémané il a bu l’amertume\n\nCrains rien petit troupeau peu importe ta charge\nIl entre dans les chambres les portes étant fermées\nJamais il n’abandonne et ne peut oublier\nEspère en sa présence dans la nuit et le jour\n\n"},{title:"DANS CETTE CITE",words:"Il y a un pays au-delà des étoiles\nIl y a une cité où il n’y a pas de nuit\nSi nous sommes fidèles nous irons là bientôt\nDans cette cité où l’agneau est la lumière\n\nChœur : \nDans cette cité où l’agneau est la lumière\nCette cité où i n’y a point de nuit\nJ’ai un beau palais là-haut et libéré des soucis\nJe m’en irai où l’agneau est la lumière\n\nIci nous sommes éclairés mais nous savons\nQue ce soleil qui lui sur nous maintenant\nSe changera en nuage quand nous irons\nA la cité où l’agneau est la lumière\n\nLà il y a des fleurs à jamais et le jour \nSera un jour éternel et pas de nuit\nNos larmes seront essuyées à jamais \nDans la cité où l’agneau est la lumière\n\nIci nous avons toujours des déceptions \nEt nos espoirs rencontrent l’opposition\nIci nous pleurons là-bas sera la joie\nDans cette cité où l’agneau est la lumière\n\nQue le soleil faiblisse ou qu’il y ait la nuit\nMon cœur bienheureux n’a peur d’aucun ombrage\nCar je sais là au ciel j’ai une maison\nDans cette cité où l’agneau est la lumière\n\n"},{title:"DANS LES CIEUX ET SUR LA TERRE",words:"Dans les cieux et sur la terre\nIl n’est aucun nom plus doux\nAucun que mon cœur préfère\nAu nom de Christ mort pour moi\n\nChœur : \nQuel beau nom (2x)\nPorte l’Oint de l’éternel\nQuel beau nom (2x)\nQue celui d’Emmanuel\n\nQuelque grand que soit un homme\nQu’il soit prince ou qu’il soit roi\nDe quelque nom qu’on le nomme\nJésus est plus grand pour moi\n\nLes séraphins les archanges \nPortent de noms glorieux\nMais le plus beau nom des anges\nPourrait-il me rendre heureux\n\nDans les maux Jésus soulage\nIl guérit l’esprit froissé\nIl ranime le courage\nDu cœur le plus oppressé\n\n"},{title:"DANS MA MISERE",words:"Dans ma misère et mon péché\nOh mon sauveur tu m’as cherché\nTel que je suis-je viens à toi\nSeigneur, accepte-moi\n\nJ’ai faim, j’ai soif de ton pardon\nEt je l’implore comme un don\nTel que je suis-je viens à toi\nSeigneur pardonne-moi\nJe ne saurai Jésus t’offrir\nQu’un pauvre cœur, las de souffrir\nTel que je suis-je viens à toi\nSeigneur enrichis-moi\n\n"},{title:"DANS MON CŒUR CHANTE UN AMOUR",words:"Chœur : \nDans mon cœur chante un amour\nAmour de mon sauveur\nDans mon cœur chante un amour\nQui fait tout mon bonheur\n\nC’est pour moi qu’il fut tant maltraité\nPour moi qui ne mérite rien\nC’est pour moi qu’il fut tant maltraité\nPour moi oui pour moi\n\nC’est pour moi qu’il fut tant maltraité\nPour moi qui ne mérite rien\nC’est pour moi qu’il fut tant maltraité\nPour moi oui pour moi\n\nC’est pour moi qu’il fut tant maltraité\nPour moi qui ne mérite rien\nC’est pour moi qu’il fut tant maltraité\nPour moi oui pour moi\n\nC’est pour moi qu’il fut tant maltraité\nPour moi qui ne mérite rien\nC’est pour moi qu’il fut tant maltraité\nPour moi oui pour moi\n\nC’est pour moi qu’il fut tant maltraité\nPour moi qui ne mérite rien\nC’est pour moi qu’il fut tant maltraité\nPour moi oui pour moi\n\n"},{title:"DANS PEU DE TEMPS",words:"Nous sommes projetés sur la mer agitée de temps\nLes nuages sombres succèdent à\nL’éclairante lumière\nDans ce pays du jour parfait\nQuand les banches sont enlevées\nOui nous le comprendrons dans peu de temps\n\nChœur :\nDans peu de temps le matin viendra\nQuand tous les saints de Dieu seront rassemblés\nNous allons raconter comment nous vainquions\nCar nous le comprendrons dans peu de temps\n\nNous sommes souvent dépourvus\nDes choses que cette vie demande\nNous sommes malades assoiffés\nAffamés nous cherchons un abri \nNous nous confions au Seigneur\nConformément à sa parole\nOui nous le comprendrons mieux dans peu de temps\n\nNous avons partout des épreuves et \nNous ne comprenons pas \nLa manière dont notre Dieu nous\nConduit vers la terre promise\nSon œil veille sur nous\nEt nous le suivons jusqu’à la mort\nCar nous le comprendrons mieux dans peu de temps\n\n"},{title:"DE L’ORAGE E DE LA TEMPETE",words:"De l’orage e de la tempête\nA l’abri de tout ce qui m’arrive\nDe l’orage et de la tempête\nDans le creux de sa main\n\nChœur :\nDans le creux de sa main (2x)\nA l’abri de tout ce qui m’arrive\nDans le creux de sa main\n\nDe son grand amour \nIl m’enveloppe\nMon espérance se fortifie\nJe suis assuré car il me maintient\nDans le creux de sa main\n\nA jamais il gardera mon âme\nA l’égard de son âme je suis lié\n Et je suis maintenant et à jamais\nDans le creux de sa main\n\n"},{title:"DE LA CRECHE A LA CROIX",words:"On me parla de l’histoire de Jules César\nDe ses guerres, de ses conquêtes de tout\nOn me parla même de sa mort\nMais dans mon cœur rien ne s’est produit\nMais quand on me parla de l’histoire\nDe la crèche, de la crèche à la croix\nDans mon cœur il eut des vibrations\nC’est l’expression de l’amour d’un grand Dieu\n\nChœur : Alléluia - amen\n  Merveilleuse – amen\nCaptivante – amen\nGuérissante – amen\nAlléluia – amen\n              Alléluia – amen alléluia – amen alléluia\n\nOn me parla de l’histoire de Napoléon\nDe ses guerres, de ses conquêtes de tout\nOn me parla même de sa mort\nMais dans mon cœur rien ne s’est produit\nMais quand on me parla de l’histoire\nDe la crèche, de la crèche à la croix\nDans mon cœur il eut des vibrations\nC’est l’expression de l’amour d’un grand Dieu\n\nOn me parla de l’histoire de Socrate\nDe l’immortalité de l’âme on m’a dit\nQu’il est mort empoisonné\nMais dans mon cœur rien ne s’est produit\nMais quand on me parla de l’histoire\nDe la crèche, de la crèche à la croix\nDans mon cœur il eut des vibrations\nC’est l’expression de l’amour d’un grand Dieu\n\n"},{title:"DEBOUT SAINTE COHORTE",words:"Debout sainte cohorte\nSoldats du Roi de rois\nTenez d’une main forte\nL’étendard de la croix\nAu sentier de la gloire\nJésus-Christ vous conduit\nDe victoire en victoire\nIl mène qui le suit\n\nLa trompette résonne\nDebout vaillants  soldats\nL’immortelle couronne\nEts le prix des combats\nSi l’ennemi fait rage\nSoyez fermes et forts \nRedoublez de courage \nS’il redouble d’efforts\n\nDebout pour la bataille\nPartez, n’hésitez plus \nPour que nul ne défaille\nRegardez à Jésus\nDe l’arme invincible\nSoldats revêtez-vous \nLe triomphe est possible \nPour qui lutte à genoux\n\nDebout, debout encore\nLuttez jusqu’au matin\nDéjà brille l’aurore\nA l’horizon lointain\nBientôt jetant nos armes\nAux pieds du Roi des roi\nLes chants après les armes\nLe trône après la croix\n\n"},{title:"DEBOUT SUR LES PROMESSES",words:"Debout sur les promesses de Christ mon roi\nQu’à travers les âges ses louanges soient\nGloire dans les cieux très hauts je chanterai\nDebout sur les promesses de Dieu\n\nChœur :\nDebout debout \nDebout sur les promesses de Dieu mon sauveur\nDebout debout \nJe me tiens sur les promesses de Dieu\n\nDebout sur les promesses infaillibles\nQuand les vents de doute et de peur assaillent\nPar la parole du Dieu vivant je vaincrai\nDebout sur les promesses de Dieu\n\nDebout sur les promesses, moi je peux voir\nDans le sang la purification pour moi\nDebout dans la liberté qui vient de Christ\nDebout sur les promesses de Dieu\n\nDebout sur les promesses de Jésus-Christ \nAttaché à lui par la corde d’amour\nToujours victorieux par l’épée de l’Esprit\nDebout sur les promesses de Dieu\nDebout sur les promesses, je ne peux tomber\nToujours écoutant la voix du Saint-Esprit \nReposé dans mon sauveur mon tout en tout\nDebout sur les promesses de Dieu\n\n"},{title:"DESCENDS DOUX CHARIOT",words:"Je regarde au loin au-delà du Jourdain\nVenant m’amener à la maison\nUne cohorte d’anges qui vient me chercher\nVenant m’amener à la maison\n\nChœur :\n\nDescends,  descends doux chariot\nVenant m’amener à la maison\nDescends doux chariot\nVenant m’amener à la maison\n\nSi tu entres là-bas avant mon arrivée\nVenant m’amener à la maison\nDis à tous mes frères que je viendrai aussi \nVenant m’amener à la maison\n\n"},{title:"DEVANT TOI SEIGNEUR",words:"O mon Dieu je suis à toi\nJe me confie en toi\nConduis-moi dans la foi\nDe l’enlèvement\n\nDevant toi Seigneur (2x)\nJe répands mon âme (2x)\n\nSouviens-toi de moi (2x)\nCar en toi j’espère (2x)\nMontre-moi la voie (2x)\nLe chemin à suivre (2x)\n\nL’éternel est droit (2x)\nIl est bon et juste (2x)\nL’amitié de Dieu (2x)\nEst pour ceux qui l’aiment (2x)\n\n"},{title:"DIEU SOIT AVEC VOUS",words:"Dieu soit avec vous jusqu’à nous revoir\nPar ses conseils vous soutenant\nDans sa bergerie vous gardant\nDieu soit avec vous jusqu’à nous revoir\n\nChœur :\n Au revoir au revoir au revoir aux pieds de Jésus\nAu revoir au revoir\nDieu soit avec vous jusqu’à nous revoir\n\nDieu soit avec vous jusqu’à nous revoir\nVous protégeant sous ses ailes\nVous donnant la manne quotidienne\nDieu soit avec vous jusqu’à nous revoir\nDieu soit avec vous jusqu’à nous revoir\nQuand les périls vous confondent\nTenez ses bras infaillibles\nDieu soit avec vous jusqu’à nous revoir\n\nDieu soit avec vous jusqu’à nous revoir\nFlottant sur vous sa bannière\nEloignant de vous la crainte\nDieu soit avec vous jusqu’à nous revoir\n\n"},{title:"DIEU TOUT PUISSANT",words:"Dieu tout puissant quand mon cœur considère\nTout l’univers créé par ton pouvoir\nLe ciel d’azur les éclairs le tonnerre\nLe clair matin et les ombres du soir\nDe tout mon être alors s’élève un chant \nDieu tout puissant que tu es grand (2x)\n\nQuand par les bois ou la forêt profonde\nJ’erre et j’entends tous les oiseaux chanter\nQuand sur les monts la source avec son onde\nLivre au zéphire son chant doux et léger\nMon cœur heureux s’écrie à chaque instant \nO Dieu d’amour que tu es grand (2x)\n\nMais quand je songe au sublime mystère \nQu’un Dieu si grand a pu penser à moi\nQue son cher fils est devenu mon frère\nEt que je suis l’héritier du grand roi\nAlors mon cœur redit la nui le jour\nQue tu es bon ô Dieu d’amour (2x)\nQuand mon sauveur éclatant de lumière \nRayonnera de sa gloire éternelle\nEt que laissant les douleurs de la terre\nJe verrai les splendeurs de son beau ciel\nJe redirai alors la nuit le jour\nRien n’est plus grand que ton amour (2x)\n\n"},{title:"DOUCEMENT TENDREMENT",words:"Doucement et tendrement Jésus t’appelle\nAppel pour toi et pour moi\nIl est au portail il attend et veille\n\nChœur :\n\nOh viens à la maison toi qui es si fatigué\nSincèrement et tendrement Jésus t’appelle\nPécheur viens à la maison\n\nPourquoi résistons-nous quand Jésus plaide\nPlaide pour toi et pour moi\nPourquoi donc retardons-nous\nSa miséricorde pour toi et pour moi\n\nLe temps est flottant et les moments passent\nPassent pour toi et pour moi\nLes ombres sont rassemblées et la nuit vient\nVenant pour toi et pour moi\n\nOh merveilleux amour il a promis\nPromis pour toi et pour moi\nNous avons péché mais il a le pardon\nPardon pour toi et pour moi\n\n"},{title:"ECOUT O CIEL",words:"Ecoute ô ciel et tout ce que tu as\nCombien Jésus-Christ est riche en bonté\nEt toi terre des hommes bétail des champs\nO louez-le car il est bon\n\nChœur :\nCombien il est bon (3x)\nMon Seigneur mon Dieu\nCombien il est bon alléluia\nOui je le louerai toujours\n\nIl nous a créés pour la vie éternelle\nMais nous avons péché et avons tout perdu\nMais dans sa bonté il se décida\nDe nous ramener à la vie\n\nEt comme un agneau il vint ici-bas \nEt versa son sang pour nous racheter\nDepuis ce jour-là nous pouvons crier \nO mort où est ton aiguillon\n\nPareil à un aigle il rode sur nous\nIl voit nos besoins nul n’est en danger\nEt à chaque jour il jette ce cri\nNe craignez rien car je suis là\n\nJe loue et j’adore mon Seigneur Jésus\nPour tout ce qu’il est et qu’il a fait pour moi\nJe ne puis compter ses nombreux bienfaits\nOh combien je veux l’adorer\n\n"},{title:"EN AVANT SOLDATS CHRETIENS",words:"En avant soldats chrétiens\nMarchons comme à la guerre\nAvec la croix de Jésus\nAllons tous en avant\nChrist le maître royal\nMène contre l’ennemi\nTout droit dans la bataille\nRegardons sa bannière\n\nChœur :\nEn avant soldats chrétiens\nMarchons comme à la guerre\nAvec la croix de Jésus allons tous en avant\n\nComme une armée puissante\nDebout église de Dieu\nFrères nous sommes en marche\nSur les traces des saints\nNous ne sommes pas divisés\nTous nous sommes un seul corps\nUn dans l’espoir et doctrine\nUn dans la charité\n\nCouronnes et trônes périront\nLes royaumes tomberont\nMais l’église de Jésus\nDemeurera constante\nMême les portes de l’enfer\nNe prévaudront contre elle\nNous avons la promesse\nDe Christ qui ne peut faillir\n\nPar le signe du triomphe \nFuit l’armée de satan\nOh alors soldats chrétiens\nMarchez vers la victoire\nL’enfer est tout ébranlé\nPar le cri de louange\nOh frères élevez vos voix\nQue votre louange monte\n\n"},{title:"EN SON TEMPS",words:"En son temps (4x)\nIl fait toutes choses merveilleuses\nEn son temps\nIl me montre chaque jour\nRévélant sa volonté\nTout ce qui doit arriver\nEn son temps\n\nEn son temps (4x)\nMa vie est entre ses mains \nEn lui je mets ma confiance\nJe sais tout s’accomplira\nEn son temps\n\n"},{title:"ENGAGEMENT L’UN ENVERS L’AUTRE",words:"Ma sœur ………….\nQue Dieu t’a donnée\nEst une femme vertueuse\nElle fera ton bonheur\n\nRefrain : Elle a quitté son papa\nElle a quitté sa maman\nPour s’attacher à toi \nPrends soin d’elle comme ta fille\n\nMon frère ………………\nEst désormais ton guide\nTu dois faire sa joie \nTout au long de votre vie\n\nRefrain : Il a quitté son papa\nIl a quitté sa maman\nPour s’attacher à toi \nSois lui soumise\n\nDisons alléluia\nChantons alléluia\nCrions alléluia\nPour ce beau mariage\n\n"},{title:"ENLEVES EN UN CLIN D’OEIL",words:"Choeur : Enlevés (2x) en un clin d’œil \n               Nous serons tous enlevés\n              Enlevés (2x) auprès de Jésus\n             Nous serons enlevés\n\nUn beau jour le monde sera confondu\nOn nous cherchera partout disparus\nCar nous qui sommes sauvés\nEt marchons en sainteté\nEn un clin d’œil nous serons enlevés\n\nLa trompette du Seigneur résonnera\nLa voix de l’archange retentira\nJésus viendra sur la nuée\nTous ensemble à sa vue\nEn un clin d’œil nous serons enlevés\n\nNous ne devancerons pas ceux qui sont morts\nIls sortiront de leur tombe d’abord\nMais je vous dis un secret\nEn un clin d’œil nous serons tous enlevés\n\nAlors nous revêtirons des corps nouveaux\nImmortels fils et filles du très-haut\nTriomphant nous chanterons\nMort où est ton aiguillon\nEn un clin d’œil nous serons enlevés\n\n"},{title:"ENTENDS-TU LE CRI",words:"Entends-tu le cri\nLe cri solennel du temps de la fin\nMon frère où seras-tu\nMa sœur où seras-tu pour l’éternité\nIl n’y a plus d’autre appel\nC’est là le dernier\nNous sommes à la fin\n\nEntends-tu le cri\nLe cri solennel du temps de la fin\nMon frère où seras-tu\nMa sœur où seras-tu pour l’éternité\nIl n’y a plus d’autre appel\nC’est là le dernier\nNous sommes à la fin\n\n"},{title:"ENTRE DEUX CHEMINS",words:"Entre deux chemins, large ou étroit\nJe prends celui de Jésus\nMême s’il doit passer par la croix\nJe choisis celui de Jésus\n\nRefrain :\nChemin si raide et si étroit\nPar monts paveaux, rude par endroits\nC’est le chemin du plein salut\nRempli de l’amour de Jésus\n\nLarge est le chemin des faux plaisirs\nJe prends celui de Jésus\nVivre près de Dieu est mon seul désir\nJe choisis celui de Jésus\n\nTon chemin ô monde est si trompeur\nJe prends celui de Jésus\nIl conduit aux plus riches splendeurs\nJe choisis celui de Jésus\n\nMalgré les épines du chemin\nJe prends celui de Jésus\nEt j’y marche tenu par sa main\nMerveilleux chemin de Jésus\n\n"},{title:"ENTRE TES MAINS",words:"Entre tes mains j’abandonne tout ce que j’appelle mien\nOh ne permets à personne Seigneur d’en reprendre rien\nOui prends tout Seigneur (bis)\nEntre tes mains j’abandonne tout avec bonheur\n\nJe n’ai pas peur de te suivre sur le chemin de la croix\nC’est pour toi que je veux vivre je connais j’aime ta voix\nOui prends tout Seigneur (bis)\nSans rien garder je te livre tout avec  bonheur\n\nTu connais mieux que moi-même\nTous les besoins de mon cœur \nEt pour mon bonheur suprême tu veux me rendre vainqueur\nOui prends tout Seigneur (bis)\nJe ne vis plus pour moi-même mais pour mon sauveur\n\nPrends mon corps et prends mon âme\nQue tout en moi soit à toi\nQue par ta divine flemme tout mal soit détruit en moi\nOui prends tout Seigneur (bis)\nPrends mon corps et prends mon âme règne sur mon cœur \n\n"},{title:"ES-TU LAVE DANS LE SANG",words:"As-tu rencontré Jésus qui purifie\nEs-tu lavé dans le sang de l’agneau\nAs-tu pleinement confiance en sa grâce maintenant\nEs-tu lavé dans le sang de l’agneau\n\nChœur :\nEs-tu lavé dans le sang\nDe l’agneau qui purifie les âmes\nTon habit est-il pur aussi blanc comme la neige\n\nMarches-tu chaque jour à côté du sauveur\nEs-tu lavé dans le sang de l’agneau\nDemeures-tu chaque jour dans le crucifié\nEs-tu lavé dans le sang de l’agneau\n\nQuand l’époux viendra ta robe sera-t-elle blanche \nPure et blanche dans le sang de l’agneau\nTon âme va-t-elle s’apprêter pour le beau palais\nEt sois lavé dans le sang de l’agneau\n\nDébarrasse-toi de cet habit de péché\nSois lavé dans le sang de l’agneau \nIl y a une fontaine qui coule pour l’âme impure\nOh ! Sois lavé dans le sang de l’agneau\n\n"},{title:"ESPRIT SAINT DU DIEU VIVANT",words:"Esprit saint du Dieu vivant\nViens demeure en moi\nEsprit saint du Dieu vivant\nViens demeure en moi\nBrise-moi remplis-moi\nTaille-moi remplis-moi\nEsprit saint du Dieu vivant\nViens demeure en moi\n\n"},{title:"ETRE COMME JESUS",words:"Etre comme Jésus (être comme lui oui alléluia)\nEtre comme Jésus\nJe veux être comme Jésus \nDans toute ma vie\nEtre comme Jésus\n\nMarcher comme Jésus\nPrier comme Jésus\nVoir comme Jésus\nParler comme Jésus\nPenser comme Jésus\nAimer comme Jésus\nPrêcher comme Jésus\n\n"},{title:"ETRE COMME LUI",words:"De Bethlehem vint un étranger\nSur terre je désire être comme lui\nMon fidèle sauveur grâce merveilleuse\nSur terre je désire être comme lui\n\nChœur :\nEtre comme Jésus être comme Jésus\nSur terre je désire être comme lui\nDurant mon voyage de la terre au ciel\nJe désire seulement être comme lui\n\nSaint et tranquille soumis et humble\nSur terre je désire être comme lui\nPar grâce donnée héritier du ciel\nSur terre je désire être comme lui\n\n"},{title:"FRUIT DIGNE DE LA REPENTANCE",words:"Ne venez pas seulement\nVous faire rebaptiser\nProduisez des fruits dignes \nDes fruits dignes de la repentance (3x)\n\n"},{title:"GIVE ME OIL IN MY LAMP",words:"Give me oil in my lamp, keep me burning\nGive me oil in my lamp, I’m pray\nGive me oil in my lamp, keep me burning\n\nKeep me burning till the break of the day\nSing Hosanna (3x) to the King of kings\nMets de l’huile dans ma lampe et fais la briller\nMets de l’huile et remplis la bien\nMets de l’huile dans ma lampe et fais la briller\nEt fais la briller jusqu’au matin\n\nChante Hosanna (3x) pour le Roi des rois\nChante Hosanna (3x) pour le Roi \n\n"},{title:"GLOIRE A L’AGNEAU",words:"Gloire gloire gloire à l’agneau\nGloire gloire gloire à l’agneau\nCar il est glorieux et digne de règne  \nL’agneau est souverain\nNous élevons nos voix pour l’adorer\nL’agneau est souverain\n\n"},{title:"GLOIRE A SON NOM",words:"A la croix où mourut mon sauveur\nJ’ai crié pour être purifié\nLà le sang fut appliqué à mon cœur \nGloire à son nom\n\nChœur :\nGloire à son nom (2x)\nLà le sang fut appliqué à mon cœur \nGloire à son nom\n\nJe suis incroyablement sauvé\nDans la douceur Jésus est en moi\nLà à la croix où il m’a reçu\nGloire à son nom\n\nFontaine qui sauve du péché\nJe suis heureux d’y être pongé\nLà Jésus me sauve et me lave\nGloire à son nom\n\nViens à la fontaine riche et douce \nJette ton âme aux pieds du sauveur \nPlonge-t’-y aujourd’hui et sois pur\nGloire à son nom\n\n"},{title:"GRACE ETONNANTE",words:"Grâce étonnante oh quel doux son\nMe sauva malheureux\nJ’étais perdu je suis retrouvé\nAveugle maintenant je vois\n\nCette grâce m’a enseigné la crainte\nElle ôta mes frayeurs\nCombien précieuse parut cette grâce\nAu moment où j’ai cru\n\nA travers les dangers et pièges\nPar où je suis venu\nCette grâce m’a gardé aussi loin\nMe conduira au ciel\n\nNous avons pendant dix mille ans \nBrillé comme le soleil\nNous pouvons louer notre Dieu\nPlus qu’à notre début\n\n"},{title:"GRACE INFINIE",words:"Grâce infinie de notre Dieu\nQui un jour m’a sauvé\nJ’étais perdu, errant de lieu en lieu\nQuand il m’a retrouvé\n\nMa vie fut complètement changée\nAu moment om j’ai cru\nDepuis ce jour, de tous mes dangers\nSa grâce m’a secouru\n\nDans mes épreuves et mes labeurs\nSuffisante est sa grâce\nJe peux toujours compter sur sa faveur\nA chaque heure qui passe\n\nQuand nous aurons pendant mille ans\nCélébré ses louanges\nNous pourrons comme au commerce\nLui offrir nos hommages\n\nQuand nous aurons quitté la terre\nPour la patrie céleste\nNous chanterons avec tous les saints\nGrâce infinie de Dieu\n\n"},{title:"HE CARES FOR YOU",words:"He cares for you (2x)\nThrough sunshine or shadow\nHe cares for you\nIl prend soins de toi (2x)\nDans le jour comme dans la nuit \nIl prend soins de toi\n\n"},{title:"HEUREUX CELUI QUI MARCHE SELON LES VOIES DE CHRIST",words:"Refrain :\nHeureux celui qui prend le nom du Seigneur (bis)\nHeureux celui qui marche dans toutes ses voies\nHeureux celui qui prend le nom du Seigneur\n\nIl y a de la joie dans mon cœur\nQuand on me dit allons à la maison de l’éternel\nJe rentrerai dans ses portes avec des cris de joie\nJe me réjouis car il est mon bonheur\n\nIl est mon bonheur (bis)\nJe me réjouis car il est mon bonheur\nIl est mon bonheur (bis)\nJe me réjouis car il est mon bonheur\n\n"},{title:"HIER AUJOURD’HUI ET POUR TOUJOURS",words:"Oh qu’il est doux le message glorieux la foi proclame\nHier, aujourd’hui et pour toujours Jésus reste le même\nIl sauve encore le pécheur et guérit les malades\nIl console et calme la tempête gloire à son nom\n\nChœur :\nHier, aujourd’hui et pour toujours Jésus reste le même\nTout peut changer mais Jésus ne changera jamais\nGloire à son nom (2x)\nTout peut changer mais Jésus ne changera jamais\n\nLui qui fut l’ami des pécheurs il te cherche aussi\nViens pécheur t’incliner à ses pieds et repens-toi\nLui qui dit je ne condamne pas vas ne pèche plus\nIl te dit ces mots de pardon comme aux jours passés\nEtant sur la terre il guérissait\nPar sa main puissante il ordonnera\nEncore aux peines de nous quitter\nLui dont la vertu guérit la femme qui le touche\nA la foi qui veut sa plénitude il donnera\n\nLui qui pardonna à Pierre il ôta ta peur\nLui qui secouru Thomas il ôtera tes doutes\nLe disciple bien-aimé se reposa sur lui\nIl t’accueillera avec amour dans sa poitrine\n\n"},{title:"HYMNE D’ISRAEL",words:"Refrain :\nSource de vie, de paix et d’amour\nVers toi je crie la nuit et le jour\nEntends ma plainte, sois mon soutien\nCalme ma criante, sois mon seul bien\n\nOh prends mon âme, prends-la Seigneur\nEt que la flamme brûle en mon cœur\nQue tout mon être vibre pour toi\nSois seul mon maître oh divin Roi\n\nDe mal perfide, oh garde-moi\nViens, sois mon guide, chef de ma foi\nQuand la nuit voile tout à mes yeux\nSois mon étoile brille des cieux\n\nVoici l’aurore d’un jour nouveau\nLe ciel se dore d’un feu plus beau\nJésus s’apprête, pourquoi gémir\nLevons nos têtes, il va venir\n\n"},{title:"I LOVE THE OLD TIME",words:"Praise him (3x) in the morning\nPraise him at the montime\nPraise him (3x) when the sun goes down\n\nI love the old time singing/ shouting\nI love the old time way\n\nI love the old time singing, shouting, preaching\npraying\nI love the old time way\n\nI sing about my savior\nEverywhere I go (3x)\nI sing about my savior\nEverywhere I go\nThere’s joy, joy in my soul\n(I get joy in my soul)\n\n"},{title:"I WILL BLESS YOU LORD",words:"I will bless/ praise/ love … you lord (2x)\nO my soul cries out\nFor you my lord \nI will bless/ praise/ love … you lord \n\n"},{title:"IL DEMEURE",words:"Je me réjouis nuit et jour comme je marche en pèlerin\nCar dans toute ma vie je vois la main de mon Dieu\nEt le secret de ma joie ma raison c’est pour cela\nLe consolateur demeure en moi\n\nChœur :\nIl demeure (4x) alléluia il demeure en moi\nMarchant sur l’étroit chemin j’ai la joie le jour la nuit\nLe consolateur demeure en moi\n\nJe vivais dans le péché n’ayant point de paix en moi\nJusqu’au jour je tombai Jésus mourut\nPuis je tombai à ses pieds et une douce paix me remplit\nLe consolateur demeure en moi\n\nIl est avec moi partout il connaît tous mes soucis\nJe suis heureux et libre comme un oiseau\nLe Saint-Esprit me contrôle Jésus satisfait mon âme\nLe consolateur demeure en moi\n\nOui tous mes soucis du monde\nPrirent des ailes dans le désert\nIl y a longtemps je m’étais abandonné\nToute ma nuit tournait en jour et mes fardeaux enlevés\nLe consolateur demeure en moi\n\n"},{title:"IL EST BON, IL EST DOUX",words:"Qu’il est bon, qu’il est doux\nPour des frères d’être ensemble\n\nCar c’est là que Dieu déverse\nSes bénédictions en abondance\n\nQu’il est bon, qu’il est doux\nLe Dieu des Hébreux, le Roi des rois\n\nQuand il parle tout fait silence\nQuand il s’élève tous s’abaissent (se dispersent)\n\nQu’il est beau l’éternel\nLe Dieu des Hébreux\nLe Roi des rois\n\n"},{title:"IL EST L’AGNEAU DE DIEU",words:"Il est l’agneau de Dieu\nIl est le Roi, il est l’agneau de Dieu\n\nSon sang a jailli pour nous\nIl a été frappé pour nous\nIl a pris tous nos péchés\nIl est mort pour nous, oh Jésus le vrai agneau\nC’est lui l’agneau sans tache, sans défaut\n\n"},{title:"IL EST UN ROC SECULAIRE",words:"Il est un roc séculaire\nQue Dieu pour mon cœur lassé\nComme un tutélaire\nAu sein des flots a placé\n\nChœur :\nMon rocher ma forteresse\nMon asile protecteur\nMon recours dans le désert\nC’est Jésus le rédempteur\n\nA mes pieds l’océan gronde\nLe vent siffle autour de moi\nSur Christ mon rocher je fonde\nMon espérance et ma foi\n\nEn vain l’ouragan fait rage\nEt nulle clarté ne luit\nPaisible au sein de l’orage\nJ’attends l’aube après la pluie\nJouet de l’onde agitée\nCet abri l’as-tu trouvé\nViens pauvre âme tourmentée\nAu rocher qui m’a sauvé\n\n"},{title:"IL Y A UNE FONTAINE PLEINE DE SANG",words:"Il y a une fontaine pleine de sang\nTirée des veines d’Emmanuel\nLes pécheurs plongés dans ce flot\nPerdent toutes leurs souillures (3x)\n\n\nLe voleur mourant s’est réjoui \nDe voir cette fontaine\nEt là puis-je comme lui\nLaver tous mes péchés (3x)\n\nL’agneau mourant ton sang précieux \nNe perdra pas sa puissance\nJusqu’à ce que l’église de Dieu \nSoit sauvée de la mort (3x)\n\nPar la foi j’ai vu ce ruisseau\nSortant de tes blessures\nL’amour rédempteur est mon thème\nJusqu’au jour de ma mort (3x)\n\nPuis dans un chant noble et doux\nJe chanterai ta puissance\nLorsque mes lèvres tremblantes \nDescendront au tombeau (3x)\n\n"},{title:"IL M’A LIBERE",words:"Il m’a libéré un jour, il m’a libéré\nIl brisa les barres de ma prison\nUn jour dans la gloire je verrai sa face\nGloire au Seigneur il m’a libéré\n\nHe set me free on day, he set me free\nHe broke the bars of prison for me\nSome day in glory his face I shall see\nGlory be to God he set me free\n\n"},{title:"IL ME FAUT MOURIR PREMIEREMENT",words:"Mon grand ennemi, c’est le propre moi\nIl est lié à ma chair et me pousse à faire le mal\nDepuis que je suis né par la voie de la femme\nUne faiblesse demeure en moi contre ma volonté\n\nMon grand ennemi, c’est le propre moi\nJe veux bien croire la parole mais il me fait douter\nLa plus grande bataille, bataille jamais livrée\nC’est pendant mes pensées tous les jours de ma vie\n\nRefrain : Il me faut mourir premièrement\n            Pour naître en Dieu ainsi est sa gloire\nIl n’y a que la mort qui peut produire la vie\nComme le grain de blé qui est tombé en terre\n\nMon grand ennemi, c’est le propre moi\nPar ma bouche je bénis, par la même bouche je maudis\nComment peut-il se faire que d’une même source\nSortent de l’eau douce et de l’eau amère\n\nMon grand ennemi, c’est le propre moi\nQui peut donc m’aider à m’en débarrasser\nJe voudrais enterrer, enterrer mon vieil homme\nJe voudrais enterrer mon ancien mari\n\nPour engendrer l’épouse, Jésus-Christ devait mourir\nPour enfanter l’église, les apôtres devaient mourir\nPour nous léguer l’évangile, les hommes de Dieu sont morts\nPour laisser même la bible, il fallu des martyrs\n\nJésus avait dit à quiconque veut le suivre \nQu’il renonce à lui-même qu’il se charge de sa croix\nCelui qui ne naît de nouveau, par l’eau et par l’Esprit\nIl ne peut comprendre le royaume des cieux\n\n"},{title:"IL NE M’ABANDONNE JAMAIS",words:"Il ne m’abandonne jamais (2x)\nJésus-Christ ne m’abandonne jamais\nEt quelque soient me chutes, il reste toujours fidèle Jésus-Christ ne m’abandonne jamais\n\nHe never fails me yet (2x)\nJesus-Christ never fails me yet\nAnd everywhere I go \nI want people to know\nJesus-Christ never fails me yet\n\n"},{title:"IL RUISSELLE DE SANG",words:"Le premier à mourir pour le plan de l’Esprit\nFut Jean le baptiste mais il mourut en homme\nPuis le Seigneur Jésus ils l’ont crucifié\nIl prêche que l’Esprit sauverait le pécheur\n\nChœur :\nIl ruisselle de sang il ruisselle de sang\nL’évangile du Saint-Esprit il ruisselle de sang\nLe sang des disciples morts pour la vérité\nL’évangile du Saint-Esprit il ruisselle de sang\n\nIl y avait Pierre et Paul et puis Jean le divin \nIls ont donné leurs vies pour que l’évangile brille\nIls ont mêlé leur sang comme les anciens prophètes\nPour que la parole vraie soit fidèlement prêchée\n\nIls lapidèrent Etienne qui condamnait le mal\nExcitant leur colère ils coupèrent sa tête\nIl mourut, dans l’esprit il rendit son âme\nIl alla rejoindre tous les anciens martyrs\n\nDes âmes sous l’hôtel criant jusqu’à quand pour\nQue le Seigneur punisse tous ceux qui font le mal\nIl y en aura beaucoup dont le sang coulera\nA cause de l’évangile et du flot cramoisi\n\n"},{title:"IL Y A DES EXPLOITS A REALISER",words:"Refrain :\nIl y a quelque chose en plus pour nous\nIl y a des exploits à réaliser\nIl y a quelque chose en plus pour nous\nPour tous les anges de l’âge de l’épouse\n\nC’est le temps de l’achèvement\nC’est la génération qui verra Christ venir dans sa gloire\nTous les anges des âges de Dieu sont venus à Laodicée\nCertainement Dieu fait de grands exploits à l’âge de l’épouse\n\nC’est le temps de la rédemption\nC’est la dernière bataille de la parole contre tous les systèmes \nTous les démons de l’enfer sont sortis à Laodicée\nMais pour nous Dieu fait de grands exploits à l’âge d l’épouse\n\nC’est le temps de l’accomplissement \nDe toutes les promesses de la parole à l’épouse de ce temps\nLes fils de Dieu manifestés produisent les œuvres du père \nEt en font de plus grandes encore à l’âge de l’épouse\n\n"},{title:"IL Y A DE LA PUISSANCE DANS LE SANG",words:"Veux-tu briser tes fardeaux de péché\nLa force est dans le sang (2x)\nVeux-tu avoir la victoire sur le mal\nLa force merveilleuse est dans le sang\n\nChœur :\nIl y a force, merveilleuse force\nDans le sang de l’agneau\nIl y a force, merveilleuse force\nDans le sang précieux de l’agneau\n\nVeux-tu être blanc plus que neige\nLa force est dans le sang (2x)\nVeux-tu vivre et chanter ses louanges\nLa force merveilleuse est dans le sang\n\n"},{title:"IL Y AURA PLUIE DE BENEDICTIONS",words:"Il y aura pluie de bénédictions\nC’est la promesse d’amour\nIl y aura un temps de rafraîchissement \nVenant du sauveur céleste\n\nRefrain : \nPluie de bénédictions  Nous en avons besoin\nDes gouttes tombent autour de nous\nMais nous plaidons pour des pluies\n\nIl y’aura pluie de bénédictions\nUn précieux réveil encore\nSur les collines et les vallées\nLes bruits d’une pluie abondante\n\nIl y aura pluie de bénédictions \nEnvoie-les sur nous Seigneur\nAccorde-nous un rafraîchissement\nViens et honore ta parole\n\nIl y aura pluie de bénédictions \nPuissent-elles tomber aujourd’hui\nComme nous nous confessons à Dieu\nComme nous invoquons Jésus\n\n"},{title:"ILS VIENNENT DE L’EST ET D’OUEST",words:"Ils viennent de l’Est et d’Ouest\nIls viennent de lointain pays\nFête avec le roi\nComme ses invités \nQue ces pèlerins sont bénis\nContemplant son saint visage\nBrillant d’éclats divins\nParticipant de sa grâce \nComme joyaux de sa couronne\n\nChœur : Mon cœur est heureux depuis \n Que Jésus m’a libéré \n Je ne porte plus le fardeau des soucis\n  Son joug est doux pour moi\n Mon âme était noire comme nuit\n Mais les ténèbres ont fuit\n Maintenant je crie victoire \n Car Jésus m’a libéré\n\nJe regarde le grand trône blanc\nLes sauvés se tiennent devant \nMaintenant plus de larmes \nNi chagrin connu\nNi mort dans ce beau pays\nLe sauveur m’a précédé\nMe préparant la voie\nBientôt nous serons ensembles\nDans le temps et l’éternité\n\nLes portails de ce saint lieu\nRestent ouverts la nuit le jour\nRegarde au Seigneur \nQui donne plus de grâce \nDont l’amour prépare la voie\nUne place dans ses belles demeures\nQu’il réserva pour tous\nPour te préparer aux noces \nObéis à cet appel\n\nO Jésus revient bientôt\nAlors nos peines sont finies\nSi notre seigneur venait maintenant\nPour ceux qui sont péché\nAurez-vous alors la joie\nOu le chagrin la peine\nQuand il viendra dans sa gloire\nNous le rencontrerons là-haut\n\n"},{title:"J’ABANDONNE TOUT",words:"Tout à Jésus j’abandonne \nJe lui donne tout librement\nEt je me confie en lui\nJe vivrai dans sa présence\n\nChœur : \nJ’abandonne tout,  j’abandonne tout\nTout à lui mon sauveur béni j’abandonne tout\n\nTout à Jésus j’abandonne \nJe me prosterne à ses pieds\nLes plaisirs du monde oubliés\nJésus prends-moi maintenant\nTout à Jésus j’abandonne\nFais-moi sauveur tout à toi\nLaisse-moi sentir le Saint-Esprit\nSavoir que tu es pour moi\n\nTout à Jésus j’abandonne\nSeigneur je me donne tout à toi\nRemplis-moi de ta puissance \nSur moi laisse tes bénédictions\n\nTout à Jésus j’abandonne\nEt je suis le feu sacré\nOh la joie du salut parfait\nGloire gloire à son nom\n\n"},{title:"J’AI L’ASSURANCE DE MON SALUT",words:"J’ai l’assurance de mon salut\nPar la présence du Seigneur Jésus\nSon sang m’a lavé m’a racheté\nEt l’Esprit-Saint m’a régénéré\n\nChœur :\nC’est mon histoire, c’est là mon chant\nLouez mon sauveur à chaque instant\n\n\nJe veux obéir à mon Seigneur\nDans mon cœur sentir l’appel de son cœur\nVeiller et prier, L’attendre un jour\nMe laisser lier par son amour\n\n"},{title:"J’AI  LONGTEMPS ERRE SANS GUIDE",words:"J’ai longtemps erré sans guide \nAltéré de vrai bonheur\nMais hélas mon cœur avide\nNe trouvait que la douleur\n\nOù sera la main puissante \nQui pourra par son effort \nM’arrêter sur cette pente\nOù déjà je vois la mort\n\nUn sauveur Jésus lui-même\nEut pitié de mon effroi\nIl me dit pécheur je j’aie\nJ’ai versé mon sang pour toi\n\nO Jésus ta main puissante \nM’a sauvé de mon tombeau\nEt tu combles mon attente \nPar le don d’un cœur nouveau\n\nJ’ai saisi cette assurance \nCe pardon qu’il m’apportait \nJ’ai livré sans résistance\nTout mon cœur il le voulait\n\nO Jésus ta main puissante \nM’a sauvé de mon tombeau\nEt tu combles mon attente \nPar le don d’un cœur nouveau\n\nDans mon cœur impur infirme\nJe reçus le Saint-Esprit\nEt ce sceau divin m’affirme \nQue je suis à Jésus-Christ\n\nO Jésus ta main puissante \nM’a sauvé de mon tombeau\nEt tu combles mon attente \nPar le don d’un cœur nouveau\n\nMaintenant de joie en joie\nMon sauveur me fait marcher\nCar la paix que Christ m’envoie\nPersonne ne peut me l’arracher\n\nA ce Jésus qui me donne\nEt sa paix et son amour\nA Jésus je m’abandonne\nDésormais et sans retour\n\n"},{title:"J’AI SAISI MA HARPE",words:"J’ai saisi ma harpe pour chanter mon Dieu\nChanter sa victoire jusque dans les cieux\nLa tristesse n’est plus la joie est revenue\nJ’ai une maison là-haut dans les cieux\nTout près de Jésus\n\n"},{title:"J’AI SOIF DE TA PRESENCE",words:"J’ai soif de ta présence divin chef de ma foi\nDans ma faiblesse immense que ferais-je sans toi\n\nChœur : Chaque jour à chaque heure o j’ai besoin de toi\nViens Jésus et demeure auprès de moi \n\nDes ennemis dans l’ombre rodent autour de moi \nAccablé par leur nombre que ferais-je sans toi\n\nPendant les jours d’orage d’obscurité d’effroi\nQuand faiblit mon courage que ferais-je sans toi\n\nO Jésus ta présence c’est la vie et la paix\nLa paix et la souffrance et la vie à jamais\n\n"},{title:"J’AI TROUVE",words:"J’ai trouvé j’ai trouvé\nOh venez j’ai trouvé ma pierre vivante\nSource de la vie éternelle \n\nNous sommes les enfants du père\nCohéritiers avec le fils\nNous sommes sujets de son royaume\nNous sommes une famille\nNous sommes un\nSouriant souriant\nLe chrétien croit tout\nEt accepte tout\nSouriant souriant\nLe chrétien prend tout\nPar le bon bout\n\n"},{title:"J’AIME CE VIELI EVANGILE",words:"Nous sommes les soldats\nLes vrais soldats de la foi \nAyant combattu le bon combat\nRentrant dans la victoire\n\nDes braves soldats choisis par le divin\nDonnant leurs vies pour sa gloire\nLes seuls capables d’en être digne (2x)\n\nRefrain : Oh oui j’aime ce vieil évangile\nOui j’aime ce vieil évangile\nCar il nous donne la vie\n       Car il nous donne la victoire\n\nUne petite troupe de très peu de nombre\nMais très puissante dès la naissance\nOn les appelle les fils de l’héritage (2x)\nEpuisés par les coups féroces et violents\ndes méchants\nEt le corps ruisselant de sang\nMarchant vers le grand roi (bis)\n\nDéfilant vers le ciel, les membres brisés\nEt chantant un hymne céleste\nDevant tous les anges, le grand roi s’est levé \nPour les féliciter (bis)\n\nRefrain : Oh oui j’aime les 7 tonnerres\nOui j’aime les 7 tonnerres\n                Car ils nous donnent la victoire\nCar ils nous donnent la joie\n\n"},{title:"J’AIME JESUS",words:"J’aime Jésus (3x)\nQu’il soit loué amen\nJ’aime sa parole (3x)\nQu’il soit loué amen\nJ’aime sa grâce\nQu’il soit loué amen\nJ’aime son châtiment\nQu’il soit loué amen\nJ’aime sa justice\nQu’il soit loué amen\n\nQu’il soit loué alléluia\n          Soit loué amen\nQu’il soit loué alléluia\n           Soit loué amen…\n\n"},{title:"J’AIME TA VIE",words:"J’aime ta vie, Seigneur\nJ’aime ta vie\nJ’aime ta vie, alléluia\nQuand tu viendras dans ta gloire\nSouviens-toi de moi Seigneur\nQuand tu prendras tes élus\nSouviens-toi de moi Seigneur\n\n"},{title:"J’ETENDS MON SAUVEUR",words:"Où il me conduit je suivrai (3x)\nJe ferai route avec lui\n\nJ’entends mon sauveur qui appelle (3x)\nPrends ta croix, viens et suis-moi\n\nJ’irai avec lui au jardin (3x)\nJ’irai sur le chemin\n\nA travers le jugement (3x)\nJ’irai sur le chemin\n\nIl me donnera gloire et grâce (3x)\nIl fera route avec moi\n\n"},{title:"J’IRAI JUSQU’AU BOUT",words:"Mon Seigneur je marche dans la lumière\nQui brille sur moi, lumière du ciel\nJ’ai dit adieu aux folies du monde\nJ’ai commencé en Jésus j’irai a bout\n\nChœur :\nJ’irai au bout, oui au bout \nJ’irai avec les méprisés du Seigneur\nPeu importe ce que me font les autre\nJ’irai au bout, oui au bout \n\nIl y en a beaucoup qui ont commencé\nMais refusent de marcher avec la lumière\nCertains l’acceptent car elle est nouvelle\nMais peu seulement espèrent aller jusqu’au bout\n\nJ’aimerais marcher avec Jésus seul\nComme Jacob, avoir une pierre pour pilier\nEtre à chaque instant devant sa face\nAu lieu de reculer et manquer le but\n\nFrère maintenant peux-tu porter ta croix\nAbandonne le monde, compte-le comme rebus\nTout ce que tu as, vends et donne aux pauvres\nAlors vas jusqu’au bout avec Jésus-Christ\n\n"},{title:"JE CHANTERAI COMME DAVID A CHANTE",words:"When the spirit of the Lord \nComes upon my heart (soul)\nI will /danse/ praise/ sing… like David dansed\nI will danse (3x) like David dansed\n\nQuand l’esprit du Seigneur \nViendra dans mon cœur (âme)\nJe danserai/ louerai/ chanterai comme David a dansé\nJe danserai (3x) comme David a dansé\n\n"},{title:"JE CONNAIS EN QUI J’AI CRU",words:"Je ne sais pourquoi dans sa grâce\nDieu se révéla à moi\nNi pourquoi Christ dans son amour\nM’a racheté pour lui\n\nChœur :\nMais je connais en qui j’ai cru\nJe suis persuadé qu’il est capable\nDe garder ce que je lui soumets\nJusqu’au jour de son retour\n\nJe ne sais comment le Seigneur\nM’avait donné la foi\nNi comment croyant sa parole\nJ’ai la paix dans mon cœur\n\n\n\nJe ne sais comment son esprit\nConvainc tous les pécheurs\nRévélant Jésus la parole \nIl crée la foi en lui\n\nJe ne sais quel bien ou quel mal\nPeut m’être réservé\nDes jours de joie ou de tristesse \nAvant de voir sa face\n\nJe ne sais quand mon sauveur vient\nEn plein jour ou la nuit\nSi je vais monter les airs\nOu descendre avec lui\n\n"},{title:"JE DESIRE LE VOIR",words:"Voyageant dans le pays je chante en marchant\nGuidant les âmes au calvaire au flot cramoisi\nMon âme percée par les flèches dedans et dehors\nMon sauveur me conduit par lui je vaincrai\n\n\nChœur : Je désire le voir contempler sa face\n      Et chanter pour toujours sa divine grâce\n Dans les rues de gloire élever ma voix \n Les soucis sont passés oh joie éternelle\n\nAu service de mon Seigneur\nMême dans la nuit sombre\nJe m’accrocherai à lui il va m’éclairer\nEt les piège de satan peuvent troubler mon âme\nMais mon sauveur prend la direction de tout\n\nDu fin fond de la vallée je regarde en haut\nJe contemple mon sauveur dirigeant la guerre\nIl me prend de la vallée par sa tendre main\nIl me guide je peux voir je marche en avant\n\nQuand devant moi les flots montent\nDu fond de l’abîme\nMon Seigneur dirige ma barque il prend soin de moi\nIl me conduit tendrement à travers ce monde\nOh il est mon ami et je l’aime tant\n\n"},{title:"JE L’AIME",words:"Je l’aime, je l’aime\nParce qu’il m’aima le premier\n\nEt acquit mon salut\nSur le bois du calvaire\n\n"},{title:"JE LE PEUX JE LE VEUX ET JE LE CROIS",words:"Je le peux, je le veux et je le crois (3x)\nJésus-Christ me guérit\nJe me confie en mon Seigneur (3x)\nJésus-Christ me guérit\n\n"},{title:"JE LE RECONNAITRAI",words:"Debout quelque part dans l’ombre tu trouveras Jésus\nIl est celui qui se soucie et comprend\nOh debout quelque part dans l’ombre tu le trouveras \nTu le reconnaîtras par les empruntes de clous\n\nChœur : oh je le reconnaîtrai \nComme rédempteur par son côté\nOh je le reconnaîtrai\nPar les empruntes de clous dans ses bras\n\nLorsque mon travail terminé je passe la marée haute\nQuand je vois le matin brillant et glorieux\nJe reconnaîtrai mon sauveur quand j’atteindrai l’autre rive\nSon sourire sera le premier à m’accueillir\n\nOh mon cœur trésaille de joie quand je vois sa face bénie\nEt l’éclat doux et rayonnant de ses yeux\nMon cœur le louera pour sa bonté amour et grâce\nQui préparent pour moi une maison au ciel\n\nOh mes bien-aimés dans la gloire m’invitent à venir\nJe me rappelle de notre séparation\nDans le doux vallon d’Eden ils chanteront à la maison\nJe désire en premier rencontrer mon Dieu\nA travers les portes de la cité dans une robe\nIl me conduira là où il n’y a pas de larmes\nDans le chant joyeux des anges oh je me mêlerai\nJe désire en premier rencontrer mon Dieu\n\n"},{title:"JE LEVE LES YEUX VERS LES CIEUX",words:"Je lève les yeux vers les cieux\nD’où me viendra le secours\nLe secours me vient de l’éternel des armées \nQui a fait les cieux et la terre\nJe bénirai Jésus (2x)\nJe bénirai de tout mon cœur \nJe bénirai de tout mon cœur Jésus\nAlléluia mon Dieu est bon (2x)\nAlléluia mon Dieu est bon, il est bon pour moi\nJ’exalterai Jésus (2x)\nJ’exalterai de tout mon cœur \nJ’exalterai de tout mon cœur Jésus\n Alléluia mon Dieu est bon (2x)\nAlléluia mon Dieu est bon, il est bon pour moi \n\n"},{title:"JE M’APPROCHE DE TOI",words:"Je m’approche de toi (2x)\nSource de vie source de vie\nMon sauveur bénis-moi\n\nFais silence en mon cœur (2x)\nViens et me parle viens et me parle\nO mon divin sauveur\n\nRends-moi conforme à toi (2x)\nDivin modèle divin modèle\nMets ton image en moi\n\nRends-moi bouillant Seigneur (2x)\nPour ton service pour ton service\nQue je parte en vainqueur \n\nEt quand tu reviendras (2x)\nDans tes milices dans tes milices\nTu me retrouveras\n\n"},{title:"JE M’ENVOLERAI",words:"Un beau matin quand tout sera fini\nJe m’envolerai\nA la maison où habite mon Jésus\nJe m’envolerai\n\nChœur :\nJe m’envolerai oh gloire \nJe m’envolerai\nA la, à la fin alléluia tout à l’heure\nJe m’envolerai\nJuste encor un peu de temps et alors\nJe m’envolerai\nDans ce pays où la joie demeure\nJe m’envolerai\n\nQuand l’ombre de ma vie sera partie\nJe m’envolerai\nComme un oiseau libéré de prison\nJe m’envolerai\n\n"},{title:"JE NE CHANCELLE PAS",words:"Jésus est mon sauveur \nJe ne chancelle pas\nSon amour est ma faveur\nJe ne chancelle pas\nJuste comme un arbre planté\nPrès des eaux\nJe ne chancelle pas\nChœur : \nJe ne chancelle je ne chancelle pas (2x)\nJuste comme un arbre planté près des eaux\nJe ne chancelle pas\n\nCaché dans le Christ\nJe ne chancelle pas\nCaché dans son amour \nJe ne chancelle pas\nJuste comme un arbre planté\nPrès des eaux\nJe ne chancelle pas\n\nSi je le crois toujours\nJe ne chancelle pas\nIl ne lâche pas\nJe ne chancelle pas\nJuste comme un arbre planté\nPrès des eaux\nJe ne chancelle pas\n\nGloire alléluia \nJe ne chancelle pas\nAncré dans Jéhovah\nJe ne chancelle pas\nJuste comme un arbre planté\nPrès des eaux\nJe ne chancelle pas\n\nQue l’enfer m’assaille\nJe ne chancelle pas\nJésus ne me lâche pas\nJe ne chancelle pas\nJuste comme un arbre planté \nPrès des eaux\nJe ne chancelle pas\n\nQue l’enfer m’assaille\nJe ne chancelle pas\nJésus ne me lâche pas\nJe ne chancelle pas\nJuste comme un arbre planté \nPrès des eaux\nJe ne chancelle pas\n\nJe suis dans son amour\nJe ne chancelle pas\nJe me confie en lui\nJe ne chancelle pas\nJuste comme un arbre planté \nPrès des eaux\nJe ne chancelle pas\n\nJe mange ta parole\nJe ne chancelle pas\nIl est bien le guide\nJe ne chancelle pas\nJuste comme un arbre planté \nPrès des eaux\nJe ne chancelle pas\n\n"},{title:"JE NE VIEILLIRAI JAMAIS",words:"Je serai rajeuni à l’enlèvement\nJe serai rajeuni dans le millénium\nJe serai rajeuni dans l’éternité\n\nChœur :\nJe ne vieillirai jamais alléluia (2x)\nAlléluia alléluia amen (2x)\nJe ne vieillirai jamais alléluia \n\nNous attendons la trompette du ciel\nRéjouissons-nous et donnons-lui la gloire\nCar les festins de l’agneau sont approchés\n\nCe sera sur la montagne de Sion\nLa ville sainte bâtie pour l’épouse\nC’est la ville qui descend du ciel\n\nHeureux sont les appelés au festin\nQui ont cru la parole de leur temps\nLa révélation complète de Jésus-Christ\n\nIl n’y aura pas la lumière du soleil\nIl n’y aura pas la lumière de la lune\nIl n’y aura pas la lumière de ce monde\n\n"},{title:"JE RETAURERAI DIT LE SEIGNEUR",words:"Epouse as-tu le souvenir\nDe ton parcours sous la première pluie\nOù tu étais, ayant en ta position la domination \nEt toutes choses sous tes pieds\n\nMais tu es tombé\nTu tombas de cette communion\nTu te demandes où est ton erreur\nAinsi tu pleures \nTu as soif de cette dernière pluie et tu combats\nPour retrouver ta puissance\n\nEt le Gazam est venu dans ta vie, et mangea\nLa sauterelle est venue dans ta vie, et mangea\nLe Jelek et le Hasil mangeaient\n\nMais Dieu a promis, il a promis\n\nJe restaurerai dit le Seigneur (bis)\nJéhovah a promis cette promesse \nJe restaurerai dit le Seigneur\n\nLa puissance descendra sur vous \nAprès que le Saint-Esprit se soit déversé\nEt vous serez mes témoins \nMes témoins au travers du monde\nEt toute ma gloire sera vue des hommes\nCar je viendrai à vous sous la forme de la première et \nDe la dernière pluie et chacun témoignera \nQue vous avez l’évidence que Jésus-Christ est ressuscité\n\nJe viendrai dans votre lieu secret\nJe vous visiterai\nDans ma pensée vous êtes un puissant peuple de valeur\nOui j’ai promis\n\nJe restaurerai dit le Seigneur (bis)\nJéhovah a promis cette promesse \nJe restaurerai dit le Seigneur\n\n"},{title:"JE SAIS C’ETAIT LE SANG",words:"Je sais c’était le sang (3x) pour moi\nLorsque j’étais perdu\nA la croix il mourut\nJe sais c’était le sang pour moi\nJe sais c’était la grâce (3x) pour moi\nLorsque j’étais perdu\nA la croix il mourut\nJe sais c’était la grâce pour moi\n\nL’amour de Jésus (3x) pour moi\nLorsque j’étais perdu\nA la croix il mourut\nL’amour de Jésus pour moi\n\nSi Christ n’était pas mort (3x) pour moi\nJe serai toujours errant\nEt n’ayant pas de vie\nSi Christ n’était pas mort pour moi\n\nIl reviendra bientôt (3x) pour moi\nIl a préparé la place pour son épouse\nIl reviendra bientôt pour moi\n\n"},{title:"JE SAIS QUE TU ES LA",words:"Je sais que tu es là tendre père\nJe sais que tu es là (bis)\nMon âme te bénit (3x)\nCar je sais que tu es là\n\n"},{title:"JE SERAI LA-HAUT",words:"Quand mon sauveur appelle, je répondrai\nQuand il m’appellera, j’entendrai\nQuand le sauveur appelle, je répondrai \nJe serai là écoutant mon nom\n\nChœur :\nJe serai là, là-haut\nJe serai là, là-haut \nJe serai là, écoutant mon nom\n\nSi mon cœur est droit, quand il m’appellera\nSi mon cœur est droit, j’entendrai\nSi mon cœur est droit, quand il m’appellera\nJe serai là écoutant mon nom\n\nSi ma robe est blanche, quand il m’appellera \nSi ma robe est blanche, j’entendrai\nSi ma robe est blanche, quand il m’appellera\nJe serai là, écoutant mon nom\n \n"},{title:"JE SUIS COUVERT PAR SON SANG",words:"I’m covered (3x) by his blood\nWalking by faith, living in love\nI’m covered (3x) by his blood\nJesus has rescued me\n\nJe suis couvert (3x) par son sang\nMarchant par la foi, vivant par amour\nJe suis couvert (3x) par son sang\nJésus m’a délivré (sauvé)\n\n"},{title:"JE SUIS HEUREUX",words:"Je suis heureux, Jésus m’a libéré\nJe suis heureux, Jésus m’a libéré\nJe suis heureux, Jésus m’a libéré\nGloire alléluia Jésus, m’a libéré\n\nJe suis heureux, Jésus m’a enlevé\nJe suis heureux, Jésus m’a enlevé\nJe suis heureux, Jésus m’a enlevé\nGloire alléluia Jésus, m’a enlevé\n\nJe crie victoire sur ma route au ciel\nJe crie victoire sur ma route au ciel\nJe crie victoire sur ma route au ciel\nGloire alléluia sur ma route du ciel\n\nJe te dis que si tu crois\nTu verras la gloire de Dieu\nTu verras la gloire de Dieu\n\n"},{title:"JE SUIS RACHETE",words:"Je suis racheté\nPar le sang de l’agneau\nEt je le verrai très bientôt\nTous mes péchés sont sous le sang\nMerci Jésus\n\nSi tu veux de l’amour\nEt encore plus \n\nCrie : alléluia, gloire à Dieu\nTous mes péchés sont sous le sang\nMerci Jésus tu peux parler de moi\n\nComme tu le voudras\nJe parlerai de toi sur mes genoux\nTous mes péchés sont sous le sang\nMerci Jésus\n\nEt ce n’est pas tout\nIl y a encore plus \nJe me suis repenti de mes péchés\nA la rivière je fus baptisé\n Tous mes péchés sont sous le sang\nMerci Jésus\n\nTu peux m’appeler un tonneriste\nTu peux m’appeler un branhamiste\nTu peux m’appeler un fanatique\nJe t’appellerai toujours mon frère \nTous mes péchés sont sous le sang\nMerci Jésus\n\n"},{title:"JE SOUS L’AUTEL",words:"Je suis maintenant sous l’autel (3x)\nQui fut dressé pour moi\n\nJésus brise toute chaine (3x)\nCar il me libère\nJe ne doute pas, mon sauveur (3x)\nCar il me purifie\n\nJe le louerai pour toujours (3x)\nCar il me libère\n\nJe me pose sur sa promesse (3x)\nQui me fut donnée\n\nJe donnerai à Dieu la gloire (3x)\nCar il me libère\n\nSur l’autel du côté du Jourdain\nDans les doux champs d’Eden\nOù fleurit l’arbre de vie\nIl y a du repos pour moi\n\n"},{title:"JE TE DONNE MON CŒUR",words:"Je te donne mon cœur, je te donne mon âme\nJe vis pour toi seulement\nChaque coup que je prends\nChaque moment de ma vie \nFais ton chemin en moi\n\n"},{title:"JE VAIS A LA TERRE PROMISE",words:"Aux bords du Jourdain je me tiens\nMes yeux se portent au loin\nVers le beau pays de Canaan\nOù se trouve mon trésor\n\nChœur :\nJe vais à la terre promise (2x)\nOh qui veut venir avec moi\nJe vais à la terre promise\n\nSur ces larges pleines étendues\nBrille un jour éternel\nLà Dieu le fils règne à jamais\nEt dissipe la nuit\n\nNi souffle ni vent ni froideur\nSur ce rivage saint \nLa mort la pleine la maladie\nN’y sont jamais senties\nQuand j’attendrai ce bel endroit\nEtre à jamais béni\nQuand je verrai la face du père\nMe reposer en lui\n\n"},{title:"JE VAIS MONTER SUR LA MONTAGNE",words:"Je vais monter sur la montagne\nC’est là que l’on rencontre Dieu\nC’est là que la joie nous inonde\nEt que pour nous s’ouvrent les cieux\nJe vais laisser les joies du monde\nEt me tenir tout près de Dieu\nJe vais me coucher sur la montagne \nC’est là que l’on est heureux\n\nRefrain : Si le chemin est rocailleux\nSi le caillou me blesse un peu\nLe Saint-Esprit me guidera\nEt Jésus me consolera\n\nJe vais monter dans le zodiac \nC’est là que l’on rencontre Dieu\nC’est là que l’Esprit nous envahit\nEt que pour nous s’ouvre la bible\nJe vais laisser les joies du monde\nEt me tenir tout près de Dieu\nJe vais monter dans le zodiac\nC’est là que l’on est heureux\n\nRefrain : Sur ce chemin Job monta\nLà le voyant me précéda\nAvec lui je vais m’élever \nJusqu’à la plus haute étoile\n(Jusqu’à la plus haute constellation)\nJe vais monter sur la montagne\nC’est là que l’on rencontre Dieu\nC’est là que la joie nous inonde\nEt pour nous s’ouvrent les cieux\nJe vais laisser les joies du monde\nEt me tenir tout près de Dieu\nJe vais me monter sur la montagne \nC’est là que l’on est heureux\n\nRefrain : Sur le chemin Jésus monta\nLà mon sauveur me précéda\nAvec lui je vais m’élever \n              (Juste dans la présence de Dieu)\n  \nJe vais monter dans la pyramide\nC’est là que l’on rencontre Dieu\nC’est là qu’il y a la révélation\nEt que pour nous tout devient claire\nJe vais laisser le pentecôtisme\nEt me tenir tout près de Dieu \nJe vais monter dans la pyramide \nC’est là que l’on est heureux\n\n\nRefrain : Sur le chemin Branham monta\nLà le prophète me précéda\nAvec lui je vais m’élever \nJuste dans la présence de Dieu\n             (Juste dans la pierre de faîte)\n\n"},{title:"JE VEUX ECOUTER TA PAROLE",words:"Je veux écouter ta parole\nJe veux marcher selon ta parole\nJe veux vivre au dépend d’elle\nSeigneur Jésus ô aide-moi\n\nChœur :\nDans ton amour aide-moi\nDans ta bonté aide-moi\nDans ton pardon aide-moi\n\nOuvre-moi le cœur, pour t’écouter\nSouviens-toi de moi selon ta promesse \nFaçonne mon cœur selon ton modèle\nDans l’humilité aide-moi\n\n"},{title:"JE VEUX ETRE COMME JESUS",words:"Je veux être comme Jésus (2x) toute ma vie (bis)\nQuand il marcha là-bas, là-bas en Galilée\nIl priait sans cesse, vraiment il priait sans cesse\nBien qu’il était la parole faite chair\nA combien bien plus forte raison moi un homme né dans le péché\n\nJe voudrais prier comme Jésus (2x) toute ma vie  (bis)\nQuand il marcha là-bas, là-bas en Galilée\nIl s’humilia vraiment il s’humilia\nIl est allé jusqu’à laver les pieds de ses disciples\nA combien bien plus forte raison moi un homme né dans le péché\n\nJe voudrais m’humilier comme Jésus (2x) toute ma vie (bis)\n\nQuand il marcha là-bas, là-bas en Galilée\nIl dit aux pharisiens, aux saducéens, aux hérodiens\nQui d’entre vous me convaincra de pécher\nUne seule parole que je n’ai pas accomplie\n\nJe voudrais parler comme Jésus (2x) toute ma vie  (bis)\n\nQuand il marcha là-bas, là-bas en Galilée\nOn ne le vit point avec des copines\nNi dans des buvettes en train de prendre l’alcool\nSa vie rendait témoignage qu’il était le fils de Dieu\n\nJe voudrais vivre comme Jésus (2x) toute ma vie  (bis)\n\nEn ces temps de la fin, il a marché à Kentucky\nNous l’avons vu s’identifier dans la vie de William Branham\nOh notre modèle\nNous devons être immergés de la vie de cet homme du Kentucky\n\nJe veux être comme Jésus (2x) toute ma vie (bis)\n\n"},{title:"JE VEUX QU’ON ME PARLE DE MON JESUS",words:"Refrain :\nJe veux qu’on me parle de mon Jésus\nJe veux qu’on me parle du Seigneur\nJe veux qu’on me parle de la maison\n\nJe veux qu’on me parle du beau palais\nOù je vais aller un certain jour\nEt quand j’atteindrai la céleste cité\nQu’on me parle encore de mon doux Jésus\n\nEn voyageant dans ce bas monde\nJe vais au pays de la gloire\nParfois ma misère est profonde\nMais un jour j’aurai la victoire\n\nJe suis heureux d’avoir un sauveur\nOui Jésus m’a sauvé par grâce\nJe le suivrai malgré mes douleurs\nCar bientôt je verrai sa face\n\nEn laissant ce monde il a promis\nDe revenir prendre les siens\nSi sa promesse est ton point d’appui\nTu t’en iras avec les saints\n\n"},{title:"JESUS BRISE TOUTE CHAINE",words:"Je suis maintenant sous l’autel (3x)\nQui fut dressé pour moi\n\nJésus brise toute chaîne (3x)\nCar il me libère\n\nJe ne doute pas mon sauveur (3x)\nCar il me purifie\n\nJe le louerai pour toujours (3x)\nCar il ma libère\n\nJe me pose sur sa promesse (3x)\nQui me fut donnée\n\nJe donnerai à Dieu la gloire (3x)\nCar il me libère\n\nSur l’autre côté du Jourdain\nDans es doux champs d’Eden\nOù fleurit l’arbre de vie\nIl y a du repos pour moi\nJe crierai alléluia (3x) \nCar il me libère\n\n"},{title:"JESUS-CHRIST NOTRE SAUVEUR",words:"Jésus-Christ notre sauveur prépare\nPour nous la cité de la nouvelle Jérusalem (2x)\n\nChœur :\nLa terre est vieille avec ses montagnes\nLa terre est vieille, elle n’est plus si belle\nJésus-Christ notre sauveur prépare\nPour nous la nouvelle Jérusalem\n\nAnnonçant aux habitants de cette vieille terre\nQue bientôt sur les nuées paraîtra le Christ (2x)\n\nLes élus seront enlevés dans les nuées\nLes condamnés seront dans la fournaise ardente (2x)\n\n"},{title:"JESUS DOIT-IL PORTER SA CROIX TOUT SEUL",words:"Jésus doit-il porter sa croix tout seul \nEt le reste du monde vivre dans l’insouciance\nIl y aune croix pour chacun\nIl y a une croix pour moi\nJe porterai cette croix consacrée\nJusqu’à ce que la mort me libère\nAlors j’irai dans l’enlèvement\n\n"},{title:"JESUS, LE BON SACRIFICE",words:"Refrain : Qu’il est bon, qu’il est bon\nLe sacrifice de Golgotha (4x)\n\nDieu se réconcilie avec l’humanité pécheresse\nIl a donné son unique fil pour notre salut\nIl nous a donné le Christ \nPour le rachat de l’humanité\n\nFrères et sœurs allons-y\nDans la maison de notre Dieu\nElevant des mains pures\nParlons à notre Dieu\n\nAccepte qu’il te transforme\nBaptise-toi au de Jésus\nFais partie du corps de Christ\nEn recevant s on Esprit\n\n"},{title:"JESUS M’A LIBERE",words:"Je suis heureux, Jésus m’a libéré (3x)\nGloire alléluia Jésus m’a libéré\n\nJe suis heureux, Jésus m’a enlevé (3x)\nGloire alléluia Jésus m’a enlevé\n\nJe crie victoire sur ma route au ciel (3x)\nGloire alléluia sur ma route au ciel\n\n"},{title:"JESUS PAR TON SANG PRECIEUX",words:"Refrain : Blanc, plus blanc que neige (2x)\nLavé dans le sang de l’agneau\nJe serai plus blanc que la neige \n(Mon cœur est plus blanc que la neige)\n\nJésus par ton sang précieux\nEnlève mon iniquité\nRegarde-moi du haut des cieux\nDis-moi que tu m’as pardonné\nJ’ai longtemps erré cœur rebelle\nMais j’entends ta voix qui m’appelle\nAu pied de ta croix maintenant\nTout confus, brisé, je me rends\n\nOh ! Le fardeau de mon péché\nDieu très saint est trop grand pour moi\nJe veux en être délivré\nA cette heure oh ! Révèle-toi\nJésus, viens sois ma délivrance\nSeul tu peux calmer ma souffrance\nAu pied de ta croix maintenant\nTout confus, brisé, je me rends\n\nO Jésus ton sang précieux \nA lavé mon iniquité\nOui tu m’as répondu des cieux\nTon amour m’a tout pardonné\nJe te contemple et je puis croire\nQu’en toi j’ai complète victoire\nAu pied de ta croix maintenant\nJe me relève triomphant\n\n"},{title:"JESUS PAYA TOUT",words:"J’entends le Seigneur dire\nMalgré ta moindre foi\nFaible enfant veille et prie\nTrouve en moi tout ton tout\n\nChœur :\nJésus paya tout et je lui dois tout\nDe mes péchés cramoisis\nIl m’a lavé comme neige\n\nSeigneur vraiment je trouve\nQue seulement ta puissance \nPeut épurer un lépreux\nEt fondre un cœur de pierre\n\nComme je n’ai rien de bon\nJe réclame ta grâce\nJe blanchirai mes robes\nDans le sang de l’agneau\n\n"},{title:"JESUS PRIT MES FARDEAUX",words:"Jésus prit mes fardeaux\nIl les roula dans la mer (3x)\nMaintenant j’ai la paix\nQue lui-même m’a donné\nTous mes péchés, mes fardeaux\nIl les roula dans la mer\n\nJesus took my burdens \nAnd rolled them in the sea (3x)\nNow I have the peace\nThat he has given me\nAll my sins and burdens\nHe rolled them in the sea\n\n"},{title:"JESUS QUITTA LE TRONE DE SON PERE",words:"Jésus quitta le trône de son père\nEt descendu ici-bas sur la terre\nIl accepta la crèche pour berceau\nLui Roi des rois, fils du Dieu très haut\n\n\n\nChœur :\nOh ! Oui, c’est vrai ! Je sais que c’est vrai !\nIl est écrit, cela suffit\nQue Jésus m’aime mon bonheur suprême\nLa bible me le dit\n\nEn touts les lieux portant la délivrance\nFaisant le bien guérissant la souffrance\nIl pardonnait au pécheur repentant\nIl bénissait jusqu’au petit enfant\n\nIl fut cloué sur la croix méprisable\nLui juste et saint mourut pour moi coupable\nPour me sauver, son sang fut répandu \nC’est pourquoi j’aime le Seigneur Jésus\n\nPlus que vainqueur il sortit de la tombe\nGloire à l’agneau sauveur du monde\nIl règne au ciel intercède pour moi\nEt vient bientôt me chercher je le crois\n\n"},{title:"JESUS REVIENDRA POUR MOI",words:"Je sais que Jésus reviendra le matin\nJe sais que Jésus reviendra pour moi\n\nJe sais (3x) qu’il reviendra le matin\nJe sais (3x) qu’il reviendra pour\n\nI know that Jesus is coming in the morning\nI know that Jesus is coming in the morning\n\nI know (3x) he is coming in the morning\nI know (3x) he is coming in the morning\n\n"},{title:"JESUS SE TIENT PRES DE TOI",words:"Jésus se tient près de toi\nDis-lui ce que tu veux\nJésus se tient près de toi\n\nJesus is on the main line\nTell him what you want\nJesus is on the main line now\n\n"},{title:"JESUS SOIT AVEC VOUS",words:"Jésus soit avec vous à jamais \nVous guidant avec sagesse\nVous entourant de tendresse\nVous remplissant toujours de sa paix\n\n\nChœur :\nAvec vous toujours avec vous\nOui qu’il soit toujours avec vous\nAvec vous toujours avec vous\nJusqu’au jour du dernier rendez-vous\n\nAvec vous au milieu du danger\nA l’heure de la détresse\nQu’il soit votre forteresse\nQu’il soit toujours votre bon berger\nAvec vous toujours etc…\n\nAvec vous quand votre ciel est bleu\nComme le jour de la tempête\nOù vous retrouverez la paix de Dieu\nAvec vous toujours etc…\n\nEnfin dans le dernier des contrats\nPour échanger cette terre\nContre la maison du père\nQue Jésus vous porte ses bras\nAvec vous toujours etc…\n\n"},{title:"KEMBO ALLELUIA",words:"Kembo alléluia\nElonga na yahwé\nKembo alléluia\nElonga na yesu\n(Gloire alléluia au Dieu des vivants)\n\nNa rendez-vous yalola kolo\nKanisela besse tokota\n(Au rendez-vopus du ciel je ne veux pas manquer)\n\nBaé nami pembo kolo opembola bango\nNe m’oublie pas quand tu vas appeler mes frères)\n\n\n\nMo kolo yoko zonga\nKolo okanisso bisso\n(Ce jour là, je veux être présent)\n\nQue Dieu soit loué…\n\n"},{title:"L’AMOUR DE DIEU EST PLUS GRAND",words:"L’amour de Dieu est plus grand que langue\nOù plumes ne peuvent l’exprimer \nIl va au-delà de la haute étoile\nIl atteint les plus bas enfers\nLe coupable sentant son poids\nDieu donna son fils unique\nEn son enfant réconcilié et pardonné de son péché\n\nChœur :\nOh l’amour de Dieu est insondable\nSi riche sans mesure et fort\nIl en durera dans l’éternité\nLe chant des saints et des anges\n\nQuand le temps passera les trônes\nEt les royaumes terrestres tombent\nQuand les hommes ici-bas refusent de prier\nSur les monts les montagnes appellent\nGrâce infinie race d’Adam\nLe chant des saints et des anges\n\nSi les océans étaient tous remplis\nD’encre les cieux parchemin \nSi chaque tige était une plume\nEt tout homme écrivain\nDécrire l’amour de Dieu sécherait les océans\nLe rouleau ne peut contenir le tout\nQuoique étendu d’un bout à l’autre\n\n"},{title:"L’AMOUR DE DIEU EST SI MERVEILLEUX",words:"L’amour de Dieu est si merveilleux (3x)\nOh quel amour \nIl est si haut qu’on ne peut le surmonter\nIl est si bas qu’on ne peut le renverser\nIl est si large qu’on ne peut le contourner\nOh quel amour\n\n"},{title:"L’AMOUR M’A SAUVE",words:"Je m’enfonçais dans le mal loin du pays de paix\nEt totalement souillé plus moyen d’en sortir\nMais le maître de la mer mon cri il entendit\nDes eaux il me fit sortir je suis sauvé\n\nChœur :\nL’amour m’a sauvé (2x)\nQuand rien d’autre ne pouvait l’amour m’a sauvé\n\nJe lui donne tout mon cœur et je m’attache à lui\nJe vivrai dans sa présence en chantant ses louanges\nAmour si puissant si vrai \nMérite mes meilleurs chants\nUn service agréable lui appartient\n\nVous qui êtes en danger regardez à Jésus\nIl vous sauvera des vagues par son amour divin\nIl est le maître de la mer les flots lui obéissent\nIl veut être le sauveur soyez sauvés\n\n"},{title:"L’ANCIEN EVANGILE",words:"C’est encore l’ancien Saint-Esprit\nEt satan ne peut l’approcher\nC’est pourquoi les peuples le craignent\nC’est bon, c’est bon pour moi\n\nChœur :\n\nDonne-moi l’ancien évangile (3x)\nIl est bon pour moi\n\nIl mettra fin à tes mensonges\nIl te sauvera de la mort\nIl fera fuir le démon\nC’est bon c’est bon pour moi\n\nIl est bon je n’en veux pas d’autre\nCar il me fait aimer mon frère\nIl met toutes choses à découvert\nC’est bon c’est bon pour moi\n\nIl était bon pour nos pères\nIl fut pour Paul et Sillas\nIl fut bon pour frère Branham\nC’est bon c’est bon pour moi\n\nC’est le vieux bateau de Sion (3x)\nEmbarquez embarquez\n\nOui j’ai un père dans l’au-delà (3x)\nLà de l’autre côté\n\nEt un bon jour j’irai le voir (3x)\nLà de l’autre côté\n\nQuelle belle rencontre ça sera (3x)\nLà de l’autre côté\n\nCe beau jour peut être demain (3x)\nLà de l’autre côté\n\n"},{title:"L’HOMME DE GALILEE",words:"Dans une crèche il y a longtemps\nPour moi c’est évident\nUn bébé naquit sauveur du pécheur\nJean le vit sur le rivage l’agneau plus que jamais\nO Christ le crucifié du calvaire\nChœur : Oh que j’aime cet homme de Galilée\nParce qu’il a fait beaucoup pour moi\nIl pardonna mes péchés\nMe remplit du Saint-Esprit\nOh que j’aime cet homme de Galilée\n\nLe publicain vint prier au temple ce jour-là\nIl cria Seigneur aie pitié de moi\nSes péchés furent pardonnés\nUne douce paix le remplit\nIl dit venez voir l’homme de Galilée\n\nLe boiteux pouvait marcher et le muet parler\nCela fut proclamer avec amour\nEt l’aveugle pouvait voir je sais c’était évident\nC’est la puissance de l’homme de Galilée\n\nNicodème vint de nuit découvrir le chemin\nIl demanda au fils de l’homme que faire\nCelui-ci dit ces mots tu dois naître de nouveau\nPar l’Esprit de cet homme de Galilée\n\nIl avait dit les péchés de la femme au puits\nComment les cinq maris elle avait eus\nSes péchés furent pardonnés\nUne douce paix la remplit\nElle dit venez voir l’homme de Galilée\n\n"},{title:"L’UN D’ENTRE EUX",words:"Il y a presque partout des peuples\nDont les cœurs enflammés\nPar le feu purifiant descendu à la pentecôte\nEt il brûle maintenant dans tout mon cœur\nA son nom la gloire je suis heureux de dire \nJe suis l’un d’entre eux\n\nChœur : Je suis l’un d’entre eux, l’un d’entre eux\nJe suis heureux de dire \nJe suis l’un d’entre eux (2x)\n\nBien ces peuples ne portent\nPas la renommée du monde\nBaptisés au nom de Jésus, ils reçurent la pentecôte\nEt ils parlent maintenant de son immuable puissance \nJe suis heureux de dire, je suis l’un d’entre eux\n\nAssemblés dans la chambre haute\nIls priaient tous en son nom\nIls étaient tous baptisés du Saint-Esprit pour le service\nCe qu’il a fait pour eux en son jour\nIl peut le faire ce jour\nJe suis heureux de dire, je suis l’un d’entre eux\n\nViens mon frère chercher cette bénédiction\nQui te rendra pur et fera sonner\nLes cloches de joie dans ton cœur \nElle enflammera ton âme\nElle brûle en moi gloire à son nom\nJe suis heureux de dire, je suis l’un d’entre eux\n\n"},{title:"LA CITE DE PAIX, DE JOIE DE VIE ET D’AMOUR",words:"Oh la nouvelle, la nouvelle Jérusalem\nOh la belle, la belle Jérusalem\n\nLa cité de paix, la cité de joie\nLa cité de vie, la cité d’amour\nJe serai enlevé par l’Esprit de Dieu\nEn Jésus-Christ seul \nJe connaîtrai la cité\n\nJe serai citoyen de la ville céleste\nLa ville véritablement bâtie en or\n\nLa cité qui a pour architecte Yahvé Dieu\nLa cité dans laquelle nous ne verrons \nPlus jamais, plus jamais de mort \nCar le malheur ne sera plus\nNous n’entendrons plus ni cris ni douleurs\nCar la détresse n’existera plus\n\nC’est la récompense de tous ceux qui persévèrent\nLa récompense de tous les saints\nAinsi chacun recevra sa récompense\nLa couronne de la gloire et la vie éternelle \nEn Jésus notre persévérance\nEn Jésus notre espérance\nNous serons tous rassemblés dans la cité\nLa cité de paix, la cité de joie\nLa cité de vie, la cité d’amour\n\nAuteur : fr. ANDO Jean-Marie\n\n"},{title:"LA FOI EST BASEE SUR LE PARDON",words:"Chœur :\nLa foi est basée sur le pardon (2x)\nLa foi est basée sur le pardon \n\nSi j’ai offensé quelqu’un, qu’il vienne me voir\nJe suis disposé à lui demander pardon\nSi quelqu’un m’a offensé, moi j’irai le voir\nEtant disposé à lui demander pardon\n\nPierre demanda à Jésus combien de fois\nFaut-il pardonner pour la même faute\nJésus-Christ lui répondu\nTu dois pardonner 70 fois 7 fois pour la même faute\n\nCelui qui n’a jamais péché dans sa vie\nQu’il jette la pierre sur cette femme\nPour demander pardon il faut s’humilier\nIl faut descendre, il faut se renier\n\nCelui qui a en lui un cœur de chair\nN’hésitera pas à demander pardon\nCelui qui a été une fois pardonné\nIl saura aussi pardonner aux autres\n\nDieu m’a pardonné sans rien mériter\nComment ne puis-je pas pardonner autres\nSeigneur apprend-moi à pardonner aux autres\nPour que tu acceptes mon sacrifice\n\n"},{title:"LA FORCE EST EN CHRIST",words:"Je suis fort, fort ! Oui plus que vainqueur\nPar le sang de Jésus \nJe suis fort, fort ! Oui plus que vainqueur\nPar le sang de Jésus mon sauveur\n\nVeux-tu briser du péché le pouvoir\nLa force est en Christ (2x)\nSi dans ton cœur tu veux le recevoir\nLa force est dans le sang du Christ\n\nVeux-tu braver et la mort et l’enfer\nLa force est en Christ (2x)\nJésus d’un mot fait tomber tous les fers\nLa force est dans le sang du Christ\n\nVeux-tu marcher toujours pur triomphant\nLa force est en Christ (2x)\nPour te garder Jésus est tout puissant\nLa force est dans le sang du Christ\n\nVeux-tu du ciel l’approcher chaque jour\nLa force est en Christ (2x)\nAvec Jésus demeurer pour toujours\nLa force est dans le sang du Christ\n\n"},{title:"LA VICTOIRE EST MIENNE",words:"La victoire est mienne (2x)\nLa victoire aujourd’hui est mienne\nJe dis à satan, arrière de moi\nLa victoire aujourd’hui est mienne\n\n"},{title:"LA VIEILLE CROIX RUGUEUSE",words:"Sur une colline lointaine se tenait une vieille croix\nEmblème de souffrance et de honte \nEt j’aime  cette vieille croix\nOù le très cher mourut pour un monde de pécheurs perdus\n\nChœur : \nAinsi j’aimerai cette vieille croix\nJusqu’à ce que je dépose mes armes \nJe vais m’attacher à la vieille croix\nEt l’échanger contre une couronne\n\nCette vieille croix  rugueuse si méprisée du monde\nElle a une attraction pour moi\nLe cher agneau de Dieu abandonnant sa gloire\nPour la porter là au calvaire\n\nOh dans cette vieille croix rugueuse teinte de sang divin \nUne merveilleuse beauté je vois\nC’est cette vieille croix que mon Jésus mourut\nPour mon pardon ma pureté\n\nEt à cette vieille croix je serai si fidèle\nJoyeux je porterai sa honte\nUn jour il m’appellera à sa maison lointaine\nPour être toujours dans sa gloire\n\n"},{title:"LA VOIX DU SEIGNEUR M’APPELLE",words:"La voix du  Seigneur m’appelle\nPrends ta croix, viens et suis-moi\nJe réponds sauveur fidèle \nMe voici je suis à toi\n\n\nChœur : Jusqu’au bout je veux e suivre\nDans les bons les mauvais jours\nA toi pour mourir et vivre\nA toi Jésus pour toujours\n\nMais le chemin du calvaire\nEst étroit et périlleux \nC’est un chemin solitaire\nDifficile et ténébreux\n\nIl faut quitter ceux qu’on aime\nSavoir être mal jugé\nEndurer l’injure et même\nDu monde être méprisé\n\nOui perdre sa propre vie\nConsentir à être rien \nN’avoir qu’une seule envie\nAimer Jésus le seul bien\n\nJésus donne grâce et gloire\nPour le suivre pas à pas\nAvec lui joie et victoire\nPaix et bonheur ici-bas\n\n"},{title:"LAISSEZ-LES",words:"Si le monde te privait de son argent et son or \nEt que tu dois vivre avec le monde frais\nRappelle-toi dans sa parole, il nourrit même les oiseaux\nAbandonne tes fardeaux à ton Seigneur\n\nChœur : \nLaissez-les laissez-les\nAbandonne tes fardeaux à ton Seigneur\nSi tu crois sans défaillir, il saura te secourir\nAbandonne tes fardeaux à ton Seigneur\n\nSi ton corps endure la peine, sans recouvrir la santé\nTon âme presque noyée dans le désespoir\nJésus sait ce que tu sens il peut sauveur et guérir\nAbandonne tes fardeaux à ton Seigneur\n\nQuand tes ennemis t’assaillent\nTon cœur commence à faiblir\nN’oublie pas Dieu répond à la prière\nIl fera une voie pour toi te conduira sûrement\nAbandonne tes fardeaux à ton Seigneur\n\nSi ta jeunesse t’abandonne et tu commence à vieillir\nTon corps se courbe sous le poids de soucis\nIl ne te quittera pas il ira avec toi au bout\nAbandonne tes fardeaux à ton Seigneur\n\n"},{title:"LE BATEAU DE LA VIE VIENT",words:"Nous voguons sur le flot du temps\nNous n’allons plus traîner\nLà les nuages sombres deviennent jour brillant\nAinsi prenons courage car nous ne sommes pas seuls\nLe bateau de la vie vient rassembler les jouaux\n\nChœur :\n\nMon frère prend courage nos peines finiront\nSur la rive dorée nous rencontrerons nos bien-aimés\nIci pèlerins étrangers nous cherchons une cité\nLe bateau de la vie vient rassembler les joyaux\nSatan me tente quelques fois disant que c’est en vain\nDe marcher au nom de Jésus et d’être un chrétien\nMais le maître nous dit : je vais vous secourir\nSi vous vous confiez en moi je vous conduirai au ciel\n\nLe bateau de la vie vient je le vois par la foi\nComme il s’avance sur les eaux venant nous secourir\nEt nous débarquer sur le port avec nos bien-aimés\nSoyez prêts crie le capitaine, le bateau vient\n\nOh c’est le moment de monter à bord pendant qu’il passe\nEt si tu traînes à côté du marras pour toujours\nLe prix du billet est payé le capitaine t’invite\nPrends place à bord de ce bateau qui conduit au pays\n\n"},{title:"LE DIEU D’ELIE EST ICI",words:"Est ici le Dieu d’Elie est ici maintenant\nEst ici pour accomplir tout ce que nous croyons\nRegarde, l’épouse est bien dans son accoutrement\nEst ici le Dieu d’Elie est ici\n\n"},{title:"LE DIEU PUISSANT C’EST JESUS",words:"Le Dieu puissant c’est Jésus, c’est le prince de paix\nLe père de l’éternité et le roi de beauté\nLe merveilleux en sagesse par lui toute chose est faite\nLa plénitude divine en Jésus incarnée\n\nChœur :\n\nTout est en lui, tout est en lui\nLa plénitude divine est toute en lui\nTout est en lui, tout est en lui\nJésus est Dieu tout puissant et tout est en lui\n\nEmmanuel Dieu en nous Jéhovah l’éternel\nL’esprit omniprésent qui remplit l’univers\nLe grand sacrificateur et l’agneau des pécheurs\nL’auteur de la rédemption que béni soit son nom\n\nLe commencement et la fin l’alpha et l’oméga\nLa vraie parole incarnée l’ami des rachetés\nLa sagesse et perfection ma justice et puissance\nTout ce dont j’ai besoin je le trouve en sa main\n\nLe Dieu que nous attendions sera la gaie chanson \nDe l’Israël revenu quand paraîtra Jésus\nIl viendra pour délivrer notre agneau immolé\nLa plénitude est en lui le vainqueur de la nuit\n\n"},{title:"LE FEU DIVIN BRULE NE MON CŒUR",words:"Le feu divin brûle en mon cœur (4x)\nJ’aime Jésus de tout mon cœur (4x)\nMon cœur le loue avec ferveur (4x)\nAlléluia gloire à son nom 4x)\nJésus revient pour me chercher (4x)\nOh reçois-le de tout ton cœur (4x)\n\n"},{title:"LE GRAND JOUR S’EST LEVE",words:"Le grand jour s’est levé\nDieu nous a visités\nVoici des temps nouveaux\nChantons gloire à l’agneau\nNous recevons de Christ\nCe qu’il avait promis \nLe saint esprit est là\n\nChœur : Le Saint-Esprit est là (bis)\nLa mort a disparu\nDieu donne un plein salut \nEt sur le monde entier\nLe grand jour s’est levé\nLe Saint-Esprit est là\n\nL’obscurité s’enfuit\nLa lumière a jailli\nChassé des cœurs troublés\nToute incrédulité\nCar Christ le Roi des rois\nRépand l’Esprit de foi\nLe Saint-Esprit est là\nLe grand consolateur\nVient remplir tous nos cœurs\nL’Esprit de vérité \nNous met en liberté\nIl ouvre les prisons\nRépand la guérison \nLa Saint-Esprit est là\nQue tous les rachetés\nChantent leur liberté\nChrist a tout accompli\nIl a donné l’Esprit\nVoyez trembler l’enfer\nCar dans tout l’univers\nLa Saint-Esprit est là\n\n"},{title:"LE GRAND MEDECIN EST ICI MAINTENANT",words:"Le grand médecin est ici maintenant\nLe compatissant Jésus\nRéconforte les cœurs brisés \nEcoute la voix de Jésus\n\nChœur : Le plus chant des séraphins\nLe plus doux nom pour les mortels\nLe plus doux cœur jamais chanté\nJésus béni soit Jésus\n\n\nTes nombreux péchés pardonnés\nEcoute la voix de Jésus\nVas en paix sur la route du ciel\nPorte une couronne avec lui\nToute gloire à l’agneau immolé\nJe crois maintenant en Jésus\nJ’aime le nom béni du sauveur\nJ’aime le nom de Jésus\n\nSon nom dissipe mes fautes et mes peurs\nPas autre nom mais Jésus\nCombien mon âme se réjouit \nD’entendre ce nom de Jésus\n\n"},{title:"LE JOUR DE LA REDEMPTION EST PROCHE",words:"Les nations se disloquent Israël se réveille\nLes signes que les prophètes prédirent \nLes jours des gentils comptés encombrés d’horreur\nBientôt sera l’éternité\nChœur : Le jour de la rédemption est proche \nLes cœurs des hommes sont effrayés\nSoit rempli de l’Esprit, ta lampe pleine et claire\nRegarde ta rédemption est proche\n\nLe figuier bourgeonne Jérusalem est restaurée\nSa vie nationale détrônée elle appelle aujourd’hui\nSa dernière pluie tombe reviens ô toi dispersée\n\nLes cieux ébranlés beaucoup sont tombés\nSur ce qui se passe dans le ciel\nL’église est la puissance qui secoue cette heure\nLe jour de la rédemption est proche\n\nLes faux prophètes trompent ils nient la vérité\nCelle de Jésus-Christ notre Dieu\nIls ont rejeté la révélation\nNous suivons les pas des apôtres\n\n"},{title:"LE MIRACULEUX EST ICI",words:"Le miraculeux est ici (+sieurs fois)\nIl est descendu il est fort\nIl est descendu il est puissant\nMon Dieu est fort moi je suis fort\nDans sa présence point de miracle\n\n"},{title:"LE MYSTERE DU MARIAGE",words:"Il est grand le mystère, il est grand\nIl est grand le mystère du mariage\nCe mystère fait de l’homme et la femme\nUne seule chair oh merveille de Dieu\n\nIl est grand le mystère, il est grand\nIl est grand le mystère du mariage\n\n"},{title:"LE NOM DE JESUS EST SI DOUX",words:"Le nom de Jésus est si doux \nDe Dieu désarmant le courroux\nIl nous appelle au rendez-vous \nPrécieux nom de Jésus\n Chœur : \nJésus béni soit ton nom\nJésus oh merveilleux don\nJésus suprême rançon \nSoit adoré pour toujours\nJ’aime ce nom dans le chagrin\nIl me soutien sur le chemin\nSa musique est un son divin\nPrécieux nom de Jésus\n\nJ’aime le nom de mon sauveur \nCar lui seul connais tout mon cœur\nLui seul me rend plus que vainqueur \nPrécieux nom de Jésus\n\nEt si parfois je succombai \nSi dans le mal je suis tombé\nSon nom puissant m’a relevé\nPrécieux nom de Jésus\n\nEt lorsqu’avec lui je serai \nEt lorsqu’enfin je le verrai\nAlors sans fin je redirai \nPrécieux nom de Jésus\n\n"},{title:"LE PARFAIT BONHEUR",words:"Aux pieds de la  croix sanglante\nOù tu t’es donné pour moi\nMon âme émue et tremblante\nO Jésus se livre à toi\n\nChœur :\nLe parfait bonheur (2x)\nC’est de mettre tout mon être\nA tes pieds Seigneur\n\nMe voici pour ton service \nJe ne garde rien pour moi\nSur l’autel du sacrifice \nJe place ma foi\n\nPrends Seigneur ma vie entière\nMets sur moi ton divin sceau\nFais d’un fils de la poussière\nUn enfant du Dieu très haut\n\nA la gloire aux biens du monde\nJe renonce pour Jésus \nQue le Saint-Esprit\nDe ta joie et de ta paix\n\nSi ma faiblesse est bien grande\nTa force est plus grande encore\nO Jésus qu’elle me rende\nFidèle jusqu’à la mort\n\n"},{title:"LE SEIGNEUR M’AIME",words:"Le Seigneur m’aime bonheur suprême\nLe Seigneur m’aime il est amour\n\nChœur :\nJe redirai toujours le Seigneur m’aime\nLe Seigneur m’aime il est amour\n\nDans la misère la plus amère\nDans la misère j’étais plongé\n\nDans la souffrance sans espérance \nDans la souffrance je gémissais\n\nPortant ma peine ma lourde chaîne\nPortant ma peine je gémissais\n\nDieu dans sa grâce prit à ma place\nDieu dans sa grâce prit son cher fils\n\nEn sacrifice à la justice\nEn sacrifice il vint s’offrir\n\nLe poids immense de mon offense\nLe poids immense tomba sur lui\n\nSur le calvaire loin de son père\nSur le calvaire il vint mourir\n\nIl purifie toute ma vie\nIl purifie avec son sang\n\nA toit la gloire et la victoire\nA toi la gloire agneau de Dieu\n\n"},{title:"LE SIGNAL DE LA VICTOIRE",words:"Le signal de la victoire déjà brille aux cieux\nLa couronne de la gloire paraît à nos yeux\n\nChœur :\nJe viens combattez encore dit Jésus à tous\nOui mon sauveur je t’implore je lutte à genoux\n\nQuand l’ennemi plein de rages redouble ses coups \nNous ne perdons pas courage Christ est avec nous\n\nSuivons amis la bannière du sauveur en croix\nEt que notre armée entrée se range à sa voix\n\nLe signal de la victoire déjà brille aux cieux\nLa couronne de la gloire paraît à nos yeux\n\n"},{title:"LES BONTES DE L’ETERNEL",words:"Les bontés de l’éternel\nNe sont pas épuisées\nSes compassions ne sont pas à leur terme \nElles se renouvellent chaque matin\n\n"},{title:"LES ECLUSES DES CIEUX SONT OUVERTES",words:"Les écluses des cieux sont ouvertes\nLes bénédictions pleuvent ce soir\nIl y a joie, joie dans mon cœur\nCar Jésus a tout arrangé\nJe lui ai donné ma robe de péché\nIl m’a donné sa robe blanche\nEt il me nourrit de sa manne\nC’est ce qui me rend tout Heureux\n\n"},{title:"LES JOURS DE NOCES ARRIVENT",words:"Les jours de noces arrivent apprête-toi\nSoit fidèle afin que tes vêtements soient propres\nQuand les élus seront rassemblés ! Jésus passera\nEt celui qui n’est pas vêtu\nOui Jésus le mettra dehors\n\n"},{title:"MA FOI REGARDE A TOI",words:"Que ta grâce infinie fortifie mon cœur \nInspire mon zèle comme tu mourus pour moi\nPuisse mon amour pour toi\nPur chaud inchangé être un feu vivant\n\nMa foi regarde à toi, toi agneau du calvaire \nSauveur divin écoute-moi quand je prie\nOte tous mes péchés\nO laisse-moi dès ce jour être à toi\n\nDurant ma sombre vie les épreuves m’environnent\nSois mon seul guide que l’ombre se dissipe\nEssuie mes tristes larmes\nNe me laisse pas m’égarer loin de toi\n\n"},{title:"MA RICHESSE ET MA GLOIRE",words:"Ma richesse et ma gloire et ma félicité\nO Jésus c’est de croire que tu m’as racheté\nRempli de confiance en toi divin sauveur\nJe vis dans l’espérance et je marche sans peur\n\nRien n’ôte les souillures rien ne guérit les cœurs \nSinon les meurtrissures et le sang du sauveur\nOui Jésus seul nous lave de toute iniquité\nEt procure à l’esclave l’heureuse liberté\n\nOh que toute la terre apporte à ce sauveur\nSes péchés sa misère il fait grâce au pécheur\nSon amour insondable lui fit quitter le ciel\nPour pardonner au coupable un salut éternel\n\n"},{title:"MERVEILLEUX MERVEILLEUX",words:"Mon cœur chante aujourd’hui de joie et d’allégresse\nJésus sauve satisfait bannit ma tristesse\nPlus de condamnation j’ai la paix en moi\nJésus est merveilleux puissant pour sauver\n\nChœur :\nMerveilleux merveilleux Jésus est pour moi\nConseiller Dieu puissant prince de la paix\nIl me sauve il me garde du péché honteux\nMerveilleux est mon rédempteur gloire à lui\n\nMoi esclave libéré de la condamnation\nJésus donne liberté aussi un plein salut\nTous les péchés passés me sont pardonnés\nMon nom est écrit dans le livre du ciel\n\nAvec mon Seigneur vivant dans la sainte union\nJour après jour je vis dans la douce communion\nOh sa grâce a gravé un changement en moi\nQuand mon âme reçu plein et libre salut\n\n"},{title:"MES YEUX ONT VU TON SALUT",words:"Refrain : Oh mes yeux ont vu ton salut\nLaisse-moi me reposer en paix\n\nComme Siméon oh ce vieillard\nQuand il a vu le messie, il s’est écrié\nOh mes yeux ont vu ton salut\nOh laisse-moi me reposer en paix\n\n\nL’élection pour chaque chrétien \nC’est de reconnaître le messager de son temps\nPour moi j’ai cru en William Branham\nOh laisse-moi me reposer en paix\n\n\nJe t’ai vu apparaître \nLe 28 Février dans le ciel de l’Arizona\nToi le grand juge oh le juge des nations\nOh laisse-moi me reposer en paix\n\n\nComme Abraham moi je t’ai vu\nDiscerner les pensées des cœurs des hommes\nTu as pris un corps de chair William Branham\nTu t’es incarné marché dans ce monde\nEt a manifesté le signe du messie\nMoi je t’ai vu, toi le fils de l’homme\nOh laisse-moi me reposer en paix\n\n\nLa génération qui verra Israël\nRetourner dans sa chère patrie\nNe passera pas pour que le messie puisse venir\nOh laisse-moi me reposer en paix\n\n\nLe mystère du tombeau ouvert\nDans la chambre du roi tu l’as relevé\nMoi je l’ai vu, la face du maître\nOh laisse-moi me reposer en paix\n\n"},{title:"MISERICORDE INSONDABLE",words:"Miséricorde insondable\nDieu peut-il tout pardonner\nAbsoudre un si grand coupable\nEt mes péchés oubliés\n\nChœur :\n\nJésus je viens je viens à toi\nTel que je suis je viens à toi\nJésus je viens je viens à toi\nTel que je suis prends-moi\n\nLongtemps j’erre loin de ta face\nProvoqué son saint courroux\nFermé mon cœur à sa grâce\nBlessé les saints devant tous\n\nO Jésus à toi je cède \nJe veux être libéré\nDe tout péché qui m’obsède\nEtre à jamais délivré\n\nAlléluia plus de doute\nMon fardeau est enlevé\nPour le ciel je suis en route\nHeureux pour l’éternité\n\n"},{title:"MOI JE SAIS EN QUI J’AI CRU",words:"Si un ange venait m’annoncer\nUn autre évangile\nQue celui qui fut prêché\nJe lui dirai arrière de moi\n\nEt toi\n\nOui je sais en qui j’ai cru \nCar lui je l’ai connu\nIl est toujours avec moi\nTout au long du chemin\n\nTout au long du chemin (3x) avec moi\nTout au long du chemin\n\n"},{title:"MOMENT SI DOUX DE LA PRIERE",words:"Moment si doux de la prière\nOù Dieu m’élevant jusqu’à lui\nSe révélant à moi comme un père\nComme un sauveur, comme un appui\n\nO oui je t’aime heure bénie\nJe te désire avec ardeur\nCar déjà souvent dans la vie\nTu m’as sauvé du tentateur\n\nC’est toi doux moment de prière\nQui me transporte jusqu’aux cieux\nOù Jésus mon ami, mon frère \nLui-même présente mes vœux\n\nDéjà souvent dans la tristesse\nTu fus ma force et mon espoir\nPour qui te recherche sans cesse\nJasais il n’est de ciel trop noir\n\n"},{title:"MON DIEU PLUS PRES DE TOI",words:"Mon Dieu plus près de toi plus près de toi\nC’est le mot de la foi plus près de toi\nDans le jour où l’épreuve déborde comme un fleuve\nGarde-moi près de toi plus près de toi\n\nPlus près de toi Seigneur plus près de toi\nTiens-moi dans ma douleur plus près de toi\nAlors que la souffrance fait son œuvre en silence\nToujours plus près de toi Seigneur tiens-moi\n\nPlus près de toi toujours plus près de toi\nDonne-moi ton secours soutiens ma foi\nQue satan se déchaîne ton amour me ramène\nToujours plus près de toi plus près de toi\n\nMon Dieu plus près de toi dans le désert\nJ’ai vu ton ciel ouvert plus près de toi\nPèlerin bon courage ton chant brave l’orage\nMon Dieu plus près de toi plus près de toi\n\n"},{title:"MON PERE, MON PERE",words:"Mon père, mon père char d’Israël\nEt sa cavalerie alléluia\n(Chantons mon père, chantons mon Dieu ou mon Roi)\nMon père, mon père char d’Israël\nEt sa cavalerie alléluia\n\n(Elie alla)\nElie alla dans la gloire\nSur un char de feu alléluia\n(Oh ! Chantons mon père)\nMoi aussi j’irai dans la gloire sur un char de feu alléluia\n\n"},{title:"MON SEUL APPUI C’EST L’AMI CELESTE",words:"Mon seul appui c’est l’ami céleste\nJésus seul (2x)\nLes gens s’en vont, cet ami me reste\nJésus seul (2x)\n\nChœur :\nCet ami connaît mes alarmes\nSon amour guérit ma douleur\nSa main essuie toutes mes larmes\nDoux sauveur (2x)\n\nTout mon désir, c’est de le connaître \nJésus seul (2x)\nEt que sa paix remplisse mon âme\nJésus seul (2x)\nDans le danger toujours il me garde\nJésus seul (2x)\nDans mes soucis, à lui je regarde\nJésus seul (2x)\n\nJe le suivrai cet ami, ce guide\nJésus seul (2x)\nA travers ce grand désert aride\nJésus seul (2x)\n\n"},{title:"NE CRAINS PAS LA TEMPETE",words:"Ne crains pas la tempête\nReste tranquille la paix viendra\nRelève donc la tête, regarde à Jésus il pourvoira\nQu’importe l’adversaire, reste tranquille, il est vaincu\nAu nom puissant de Jésus, l’ennemi s’enfuit\nTu es vainqueur\n\n"},{title:"NE DE LA POUSSIERE",words:"Né de la poussière et d’éternité\nJ’ai vu la lumière, elle m’a racheté\nEt le cœur avide de vraie liberté\nJ’ai suivi ce guide nommé vérité\n\nRefrain : Il est la vérité le chemin et la vie \n                On ne vient au père que par lui\n\nTon regard s’étonne, tu ne comprends pas\nUn roi qui pardonne, ça n’existe pas \nUn roi qui s’incline devant ses sujets\nCouronné d’épines, à toi de juger\n\n"},{title:"NE M’OUBLIE PAS O DOUX SAUVEUR",words:"Ne m’oublie pas ô doux sauveur\nEcoute ma voix\nQuand tu répondras aux autres\nNe m’oublie pas\nChœur :\nSauveur, sauveur écoute ma voix\nQuand les autres tu appelles\nNe m’oublie pas\n\nLaisse-moi près de ton trône grâce \nTrouver du repos\nA genoux dans la contrition \nOtes tous mes doutes\n\nComptant seulement sur tes mérites\nJe cherche ta face\nGuéris mon esprit affligé\nSauve-moi par ta grâce\n\nToi fontaine de mon réconfort\nPlus que vie pour moi\nQui d’autre sur terre ou là au ciel\nAi-je à part toi\n\n"},{title:"NE ME PRESSE PAS",words:"Ne me presse pas, ma sœur de te laisser\nEt retourne loin de toi\nNe me presse pas, mon frère de te quitter\nEt retourne loin de toi\n\nRefrain : où tu iras j’irai\nTa demeure sera ma demeure\nTon peuple sera mon peuple \nEt ton sera mon Dieu\n\n\n"},{title:"NE T’INQUIETE DE RIEN",words:"Ne t’inquiète de rien\nEt n’ai aucune crainte\nLa joie vient le matin\nLes problèmes ne durent qu’un temps\nIl y a un ami nommé Jésus\nQui essuiera toutes les larmes\nJe sais que je réussirai\nJe sais que je l’atteindrai\nPeu m’importe ce qu’il y a sur mon chemin\nMa vie est dans ses mains\n\n"},{title:"NON JAMAIS TOUT SEUL",words:"Ne crains rien je t’aime, je suis avec toi\nPromesse suprême qui soutient ma foi\nLa sombre vallée, n’a plus de terreur\nL’âme consolée, je marche avec mon sauveur\n\nChœur :\nNon jamais tout seul (2x)\nJésus mon sauveur ma garde jamais ne me laisse tout seul\nNon jamais tout seul (2x) Jésus mon sauveur ma garde\nJe ne suis jamais tout seul\n\nL’aube matinière ne luit qu’aux beaux jours\nJésus ma lumière m’éclaire toujours\nQuand je perds de vue l’astre radieux\nA travers la nuée Jésus me montre les cieux\n\nLes dangers accourent subtils inconnus\nDe près ils m’entourent plus près est Jésus\nQui dans le voyage me redit c’est moi\nNe crains rien courage je suis toujours avec toi\n\n"},{title:"NOTRE SEIGNEUR REVIENT",words:"J’attends la venue du glorieux \nEt grand de millénium\nQuand notre Seigneur béni\nViendra pour chercher son épouse\nOh mon cœur est rempli de ravissement\nComme je veille et prie\nCar notre Seigneur revient bientôt sur terre\n\n\n\nChœur :\nOh notre Seigneur revient bientôt sur terre (bis)\nSatan sera lié pour mille ans \nNous ne serons plus tentés\nQuand Jésus reviendra de nouveau sur terre\n\nLe retour de Jésus sera la consolation du monde\nLa connaissance du Seigneur \nRemplira la terre et les cieux\nDieu prendra toutes nos maladies\nEt séchera nos larmes car notre\nSauveur sera de retour sur terre\n\nLes rachetés du Seigneur viendront à Sion avec joie\nSur la sainte montagne \nRien ne causera le chagrin et la paix et\nL’amour parfait régnera dans tous \nLes cœurs quand Jésus viendra de nouveau sur terre\n\nLe péché la mort et le chagrin de ce monde \nCesseront dans le règne glorieux avec Jésus\nPendant mille ans de paix\nLa terre entière gémit pour ce jour de douce délivrance\nPour que notre Jésus retourne sur terre\n\n"},{title:"NOUS MARCHONS VERS SION",words:"Laissons voir notre joie\nNous qui aimons le Seigneur\nChantons un doux chant mélodieux (2x)\nEnvironnant le trône, environnant le trône\n\nChœur :\n\nNous montons vers Sion\nMagnifique, magnifique Sion\nNous montons tout droit vers Sion\nLa merveilleuse cité de Dieu\n\nCeux qui ne veulent pas chanter\nN’ont pas connu notre Dieu\nMais les enfants du roi céleste (2x)\nExprimeront leur joie, Exprimeront leur joie\n\nLa colline de Sion nous produit\nLes délices sacrés\nAvant de fouler les domaines célestes (2x)\nOu marcher sur les rues d’or (2x)\n\nPuissent nos chants abonder \nEt chaque larme être séchée\nNous marchons sur les traces d’Emmanuel (2x)\nVers les beaux pays d’en haut (2x)\n\n"},{title:"NOUS NOUS RASSEMBLERONS A LA RIVIERE",words:"Pourrons-nous à la rivière, sortant du trône de Dieu\nNous rassemblés sur la rive, où les anges ont marché\n\nChœur :\n\nOui nous allons nous rassemblés à la merveilleuse rivière\nAvec tous les saints à la rivière qui coule du trône de Dieu\n\nAu sein de cette rivière le roi sauveur est à nous\nNous serons là, plus de peine sous la gloire du trône\n\nAu bord de cette rivière nous nous baignons de son eau \nNous marcherons en adorant, ce sera un jour doré\n\nArrivés à la rivière déposons tous nos fardeaux\nLa grâce nous délivrera, nous donnera une couronne\n\nTout près de cette rivière, reflétant la face de Dieu\nLes saints qui sont immortels chanteront leurs chants de gloire\n\nBientôt à cette rivière le voyage est fini \nEt tous nos cœurs se réjouiront de la mélodie de paix\n\n"},{title:"NOUS PARTIRONS",words:"Nous partirons, nous partirons\nNous partirons, oh oui nous partirons\nJe peux entendre les pas au sommet des murets\nEt je sais, je sais, je sais nous partirons\n\nAvance mon frère, avance ma sœur \nC’est le temps du départ\nAvance un peu plus près où le fleuve coule\nAvance mon frère avance\n\n"},{title:"NOUS PRENONS CHEMIN",words:"Nous prenons chemin pour aller au ciel\nEst-ce que tu pourras y aller aussi\nDes gens par milliers parcourent le ciel\nIls sont vêtus en habits blancs\nEt toi mon frère pose-toi la question \nEst-ce que tu pourras y aller aussi\n\n"},{title:"NOUS SOMMES DE CHRIST",words:"Nous ne sommes pas du ramassis des peuples (4x)\n\nNous nous venons de l’autre rive du Jourdain\nDu sein de la source d’eau de la vie\nSon nom c’est de Jésus\nLa parole de vie, la manne cachée\nVainqueur de Golgotha\n\nPèlerin sur terre, étranger sur cette terre\nNous retournerons bientôt d’où nous venons\nCitoyens d’un pays lointain\n D’un pays au-delà des étoiles\n\nUn peuple acquis, une nation sainte\nUn sacerdoce royal\nNous nous dirigeons vers la cité\nLa nouvelle Jérusalem\n\n"},{title:"NOUS SOMMES ENSEMBLE DE NOUVEAU",words:"Nous sommes ensemble de nouveau\nPour louer le Seigneur\nNous sommes ensemble de nouveau\nD’un commun accord \nQuelque chose de bon va se passer\nQuelque chose de bon est en réserve\nNous sommes ensemble de nouveau pour le louer\n\n"},{title:"OH ! QUEL BEAU SOLEIL DANS MON AME",words:"Dans mon âme un beau soleil brille\nSon rayon doux et joyeux\nRépand un éclat qui scintille\nC’est le sourire de Dieu\n\nRefrain :\n\nOh !  Quel beau soleil dans mon âme\nIl resplendit illuminant tout\nA ses rayons mon cœur s’enflamme\nEt je vais chantant partout\n\nMon cœur était plein de ténèbres\nQuand parut un jour nouveau\nAu loin fuyez ombres ténèbres\nDevant un soleil si beau\n\nOh ! Mon âme éclate en louange\nPour tout le soleil à lui\nJe serai parmi les phalanges\nQui loueront Dieu jour et nuit\n\n"},{title:"OH VOYEZ COMBIEN LE MONDE NOUS ENVIE",words:"Oh voyez combien le monde nous envie\nLa joie et la paix inondent notre cœur\nC’est la gloire du Seigneur qui vivifie\nEt qui nous remplit de son parfait bonheur\n\nRefrain :\n\nAlléluia ! (3x) Mes péchés sont pardonnés\nOui c’est Jésus qui nous sauve \nEt qui veut faire autant pour toi que pour moi\n\nAujourd’hui, mon frère ma sœur c’est jour de grâce\nN’hésite pas mais donne ton cœur à ton sauveur\nTu verras comme il est bon d’être avec Jésus\nIl te remplira de son parfait bonheur\n\nAujourd’hui, il vous appelle les brebis perdues\nJésus-Christ est le berger le bon berger\nIl veut vous sauver, aussi vous guérir\nSoyez prêts à recevoir notre Seigneur\n\n"},{title:"ON ME DIT SOUVENT",words:"On me dit souvent que Jésus n’était pas noir\nOn me le répète qu’il était tout blanc \nJésus était Juif, qu’il n’était pas de ma race\nMais moi tout ce que je sais, il mourut pour moi\nJe ne l’ai point vu, il vécut il ya 2000 ans\nMais l’on me demande pourquoi je l’adore\nEt moi je réponds,  je le connais par la foi\nJe le sens dans tout mon cœur et je l’aime bien\n\nRefrain : \nIl est dans mon cœur et fait briller toute ma vie\nEt si moi je vis, c’est lui qui le veut\nMême si je tombe c’est Jésus qui me relève \nIl me tient la main, je l’aime (bis)\n\nBeaucoup me disent que cet homme de l’an 33\nAvait bien promis qu’il devait revenir\nBientôt 2000 ans, quand est-ce qu’il reviendra\nMon âme le témoigne, ça s’accomplira\n\nRefrain : \nMême mon âme témoigne qu’il vient bientôt\nCet homme de Jésus, Jésus de la croix\nIl est au-dessus, nul ne peut le dépasser \nIl me tient la main je l’aime (bis)\n\nJ’étais dans l’abime et je vis passer Bouddha\nAprès Mahomet, c’était Confucius\nJ’ai vu des prophètes qui pouvaient rien pour moi \nMais lorsque j’ai vu Jésus, Jésus m’a sauvé \n\nRefrain : \nJe dirai partout et je crierai avec joie\nLe nom de Jésus, Jésus de la croix\nIl est au-dessus, nul ne peut le dépasser \nIl me tient la main je l’aime (bis)\n\n"},{title:"ON NE SE MOQUE PAS DE DIEU",words:"Ne vous-y trompez pas \nOn ne se moque pas de Dieu \nCe qu’un homme aura semé, semé\nIl le moissonnera\n\n"},{title:"OU CHERCHEZ-VOUS LE BONHEUR",words:"Où cherchez-vous le bonheur\nDans ce monde où tout passe\nAvez-vous en votre cœur pour Jésus une place \nA votre porte il se tient sans se lasser il revient\nPour Jésus votre sauveur n’avez-vous point de place\n\nRefrain : \nN’avez-vous point de place (2x)\nPour Jésus votre sauveur n’avez-vous point de place\n\nS’il a souffert c’est pour vous oh merveilleuse grâce\nLorsqu’il luttait à genoux ; Dieu lui voilant la face\nAh de sa sublime croix, n’entendez-vous pas la voix\nQui vous dit pauvre pécheur : n’as-tu point de place\nAh pour l’homme de douleur n’avez-vous point de place\n\nSi le monde a votre cœur, croyez-vous donc qu’il fasse\nJusqu’au bout votre bonheur vous savez que tout passe\nOh pendant qu’il est encore temps\nEcoutez les doux accents de la voix du grand vainqueur\nEt faites lui donc une place\nN’avez-vous point de place pour Jésus le grand vainqueur \nN’avez-vous point de place\n\n"},{title:"OUVREZ-VOUS PORTES DU TOMBEAU",words:"Ouvrez-vous portes du tombeau\nJésus parait, oh ! Qu’il est beau\nIl revit ô merveille\nLe péché, l’enfer et la mort\nEn vain ont uni leurs efforts\nMon rédempteur s’éveille\n\nIl vit, je sais qu’il vit (dans mon cœur)\nJe sais que mon rédempteur vit\n\nVoyez ses mains, se pieds percés\nEt les stigmates qu’ont laissés\nLe fouet, le fer, l’épine\nC’est pour moi qu’il a tant souffert\nIl m’a délivré de l’enfer\nO charité divine\n\nJe n’ai plu rien à redouter\nDe mon sauveur puis-je douter\nSa promesse est formelle\nAu tombeau mon corps descendra\nMais Christ le ressuscitera\nPour la vie éternelle \n\n"},{title:"PAR LA FOI NOUS MARCHERONS",words:"Celui qui met en Jésus\nUne pleine confiance\nJamais ne chancelle plus\nComplète est sa délivrance\n\nChœur :\nPar la foi, nous marcherons\nPar la foi, nous triomphons \nPar la foi, mon rédempteur\nMe rendra plus que vainqueur\n\nDans les jours d’adversité\nQuand tu sens gronder l’orage\nRegarde en sécurité\nA Christ et reprends courage\n\nQuand satan veut te troubler\nEnlever ton espérance \nTon passé te reprocher \nQue Christ soit ton assurance\n\nPar la foi je marcherai \nEn comptant sur tes promesses\nPar lui je triompherai\nEn tout tant de mes détresses\n\n"},{title:"PARLE-MOI SEIGNEUR",words:"Parle-moi (2x)\nC’est là ma prière \nJe t’écoute ô père \nPrêt à t’obéir (2x)\n\n"},{title:"PARLONS DE JESUS",words:"Parlons de Jésus\nIl est le Roi des rois \nLe Seigneur des seigneurs\nDurant l’éternité\nLe grand « Je suis »\nLa porte, la vie la vérité\nParlons de Jésus chaque jour\n\nParlons de Jésus\nLe lys de la vallée\nLa rose de Saron \nL’étoile du matin\nLe commencement la fin\nL’alpha et l’oméga\nParlons de Jésus chaque jour\n\nParlons de Jésus\nIl est le fils de Dieu \nLe fils de David \nLe fils d’Abraham c’est le fils de l’homme\nLa parole incarnée\nParlons de Jésus chaque jour\nParlons de Jésus\nIl est Jéhovah-Jiré\nC’est le rocher frappé\nLa colonne de feu\nLe grand Melchisédek\nIl guérit et il sauve\nParlons de Jésus chaque jour\n\nParlons de Jésus\nLe grand Emmanuel\nC’est lui le pain de vie\nC’est la manne cachée\nL’ami des rachetés \nVainqueur de Golgotha\nParlons de Jésus chaque jour\n\n"},{title:"PELERIN SUR CETTE TERRE",words:"Pèlerin sur cette terre, je m’avance vers le ciel\nVers le pays de lumière, séjour éternel\nIci d’ennemis sont nombres, je me vois environné\nMa route souvent bien sombre mais je suis aimé\n\nAimé d’un plus tendre père, qui m’a conduit en chemin\nEt dirigé ma carrière par sa sûre main\nPendant l’épreuve et l’orage dans l’angoisse et la douleur\nSa voix me dit : « prends courage je suis ton sauveur »\nTravailler pour un tel maître, le servir en l’attendant\nApprendre à mieux le connaître en le contemplant\nQuel bonheur incomparable, est heureux mon sort\nOui mon sauveur adorable, me conduit au port\n\nLe port c’est le doux rivage où cesseront nos pleurs\nOù de Jésus-Christ l’image ravira nos cœurs \nQu’importe donc la souffrance Jésus vient levons les yeux\nBien près est la délivrance, il s’ouvre les cieux\n\n"},{title:"PLEIN DE ZELE O FILS DE DIEU",words:"Plein de zèle ô fils de Dieu\nParle de lui parle de lui\nViens témoigne en tous les lieux, parle de lui\n\nChœur :\n\nMène les âmes à la croix où le sang fut répandu\nQu’elles écoutent et fassent leur choix pour Jésus\n\nPrêche le nom de Jésus\nParle de lui parle de lui\nEn lui seul est le salut parle de lui\n\nApporte la bonne nouvelle\nParle de lui parle de lui\nTon salaire sera étendu parle de lui\n\n"},{title:"PLUS DE VIEILLESSE",words:"J’ai entendu parler d’un pays si lointain\nC’est une belle maison pour l’âme\nEdifiée par Jésus là nous ne mourrons point\nLà-bas nous ne vieillirons jamais\n\nChœur :\n\nPlus de vieillesse plus de vieillesse\nLà-bas nous ne vieillirons jamais\n\nDans la belle cité nous n’allons plus errer\nNous serons bientôt dans la douceur\nNous louerons le roi dans les âges éternels\nDans ce pays nous ne mourrons point\n\nNotre tâche accomplie nous serons couronnés\nEt nos troubles et nos épreuves passés\nEt nos peines finies nos voix seront mêlées\nA celles de nos frères qui sont partis\n\n"},{title:"PLUS TARD NOUS SAURONS",words:"Tentés éprouvés\nNous nous demandons \nPourquoi c’est ainsi le long du jour\nPendant qu’il y en a d’autres comme nous\nQuoique dans le mal impunis\n\nChœur :\nPlus tard nous saurons ce qu’il en est\nPlus tard nous comprendrons le pourquoi\nCourage mon frère vit dans la lumière\nNous comprendrons tout dans peu de temps\n\nQuand la mort vient \nEt prend nos biens aimés \nLaissant notre maison solitaire\nNous nous demandons pourquoi les méchants\nProspèrent jour après jour ici-bas\n\nFidèles jusqu’à la mort dit le maître\nNotre labeur est bientôt fini\nLes peines du chemin seront oubliées\nQuand nous franchirons\nLa belle porte\n\nNous voyons Jésus venant dans la gloire\nComme il vient de sa maison céleste\nNous le rencontrerons dans ce beau palais\nNous comprendrons tout dans peu de temps \n\n"},{title:"POUR CE BEAU JOUR",words:"Alléluia ! Alléluia ! Amen ! (3x)\nAmen chante mon cœur\n\nPour ce beau jour\nPour ce soleil, merci\nPour ton pardon\nPour tant de paix\nEt tant d’amour, merci\nMerci Jésus, mon Dieu\n\nPour ta parole\nTon pain de vie, merci\nPour les amis, nos frères \nNos sœurs, merci\nEt pour la joie\nQue tu mets dans nos cœurs\nMerci Jésus, mon Dieu\n\nJésus je t’aime\nEt je proclame ton nom\nComme David je chanterai pour toi\nJ’élèverai vers toi des louanges\nAmen chante mon cœur\n\nO père je suis né de nouveau, merci\nA la croix Jésus m’a guéri, merci\nEt pour ta main que tu poses sur moi\nAmen, alléluia\n\n"},{title:"POUR L’AMOUR QUE TU M’AS DONNE",words:"Pour le message que tu m’as donné\nJe te louerai\nEt pour la grâce que tu m’as donnée\nJe te louerai\n\nChœur :\n\nJe te louerai Seigneur Jésus\nTu m’as tout donné, tu m’as tout donné\n\nPour le Saint-Esprit que tu m’as donné\nJe te louerai, je te louerai\n\nPour le prophète que tu m’as donné\nJe te louerai, je te louerai\n\nEt pour l’amour que tu m’as donné\nJe te louerai, je te louerai\n\nPour le pasteur que tu m’as donné\nJe te louerai, je te louerai\n\nPour cette église que tu m’as donnée\nJe te louerai, je te louerai\n\n"},{title:"POUR MOI C’EST L’AMOUR",words:"Si riche, si profond, insondable\nC’est l’amour divin, l’amour rédempteur\n\nChœur :\n\nPour moi (2x)\nIndigne et sauvé par grâce\nPour moi (2x)\nL’amour rédempteur\n\nImmuable, incomparable, inexprimable\nC’est l’amour divin, l’amour rédempteur\nComme c’est beau comme c’est doux\nDe fredonner de l’agneau divin\nL’amour rédempteur\n\n"},{title:"POURQUOI LA PETITE BETHLEHEM",words:"Bethlehem Ephrata, toi la moindre des villes de Juda (2x)\nMais de toi sortira celui qui dominera\nMais de toi sortira celui qui règnera sur Israël\nBethlehem Ephrata, toi la moindre des villes de Juda (2x)\nMais de toi sortira celui qui dominera\nMais de toi sortira celui qui règnera sur Israël\n\nEt moi je me demande pourquoi seulement \nLa petite Bethlehem\nJe me demande pourquoi seulement\nLa petite Bethlehem de Juda\nIl y avait Silo, ce haut lieu d’adoration\nIl y avait Guilgal, cet autre lieu d’adoration\nMême Kades Barnéa cet autre lieu d’adoration\nMais n’oublie pas Jérusalem la ville sainte de l’éternel\n\nJe me demande pourquoi seulement\nLa petite Bethlehem de Juda \n\nIl y avait les catholiques \nLes luthériens, les presbytériens\nIl y avait les pentecôtistes \nLes adventistes du septième jour\nJe me demande pourquoi seulement\nWilliam Marrion Branham\nJe me demande pourquoi…\n\nBethlehem Ephrata\nLa maison du pain de vie\nBethlehem Ephrata\nLa maison de l’eau de vie\nBethlehem Ephrata\nQui est mon âme prédestinée\nBethlehem Ephrata, toi mon âme prédestinée\nJésus-Christ notre Seigneur \nEtant le pain descendu du ciel\nEtant la fontaine d’eau vive\nNe pouvant que naître à Bethlehem\nNe pouvant que naître dans ton âme\nMon bien-aimé \n\nJ’ai compris pourquoi seulement\nLa petite Bethlehem\nJ’ai compris pou quoi seulement\nLa petite Bethlehem de Juda\n\n"},{title:"POURQUOI RESTER TOUJOURS DANS LA NEGLIGENCE",words:"Est-ce que j’ai fait de tout mon mieux pour Jésus\nQui mourut sur la croix cruelle\nJe pense à son grand sacrifice au calvaire\nJe sais qu’il s’attend ô meilleur de moi\n\n\nChœur :\nPourquoi rester toujours dans la négligence\nAlors que le Seigneur est à la porte\nEst-ce que j’ai fait de tout mon mieux pour Jésus\nSachant qu’il a tout fait pour moi\n\nMes années gaspillées sont si nombreuses\nLes heures passées pour Christ si peu\nA cause de mon manque d’amour pour Jésus\nJe sais que son cœur doit être brisé\n\n"},{title:"PRENDS LE NOM DE JESUS AVEC TOI",words:"Prends le nom de Jésus avec toi\nEnfant de tristesse et de peine\nIl va te procurer la joie\nPrends-le partout où tu vas\n\nChœur :\nPrécieux nom, nom si doux\nEspoir de la terre joie du ciel\nPrécieux nom, nom si doux\nEspoir de la terre joie du ciel\nPrends toujours le nom de Jésus\nComme bouclier face aux pièges\nQuand les tentations surviennent\nMurmure ce nom en priant\nOh le précieux nom de Jésus\nFait frémir nos âmes de joie\nLorsque ses bras nous reçoivent\nEt nous chantons ce cantique\n\nEn son nom, nous nous inclinons\nNous prosternant à ses pieds \nRoi des rois nous le couronnerons\nQuand le voyage est fini\n\n"},{title:"PRES DE LA CROIX GARDE-MOI",words:"Près de la croix garde-moi\nLa fontaine précieuse\nGratuite et guérissante\nCoule du calvaire\n\nChœur :\n\nA la croix, à la croix \nSoit ma gloire à jamais\nEt que mon âme enlevée\nTrouve  repos là-haut\n\nPrès de la croix tout tremblant\nAmour grâce m’ont trouvé\nLà l’étoile du matin \nRayonne autour de moi\n\nPrès de la croix ô agneau\nMontre-moi ses scènes \nAide-moi à marcher toujours\nAvec tes ombres sur moi\n\nPrès de la croix j’atteindrai\nVeillant croyant toujours\nJusqu’au jour où j’atteindrai \nL’autre rive dorée\n\n"},{title:"QU’IL FAIT BON A TON SERVICE",words:"Qu’il fait bon à ton service\nJésus mon sauveur\nQu’il est doux le sacrifice\nQue t’offre mon cœur \n\nChœur :\nPrends ô Jésus prends ma vie\nElle est toute à toi\nEt dans ta grâce infinie\nDu mal garde-moi\n\nMon désir mon vœux suprême\nC’est ta volonté\nRien je ne veux et je n’aime\nQue ta volonté\n\nComme l’ange au vol rapide\nJe veux te servir\nLes yeux fixés sur mon guide\nToujours obéir\n\nTravail douleur et souffrance\nNon je ne crains rien\nToi Jésus mon espérance\nVoilà mon seul bien\n\nEnsemble donc vers la gloire\nMarchons en avant\nChantant l’hymne de victoire\nToujours triomphant\n\n"},{title:"QUAND DIEU TE FAIT PASSER PAR LE FEU",words:"Quand Dieu te fait passer par le feu\nAu feu pour t’éprouver\nCe n’est que pour te purifier\nNe t’étonne donc pas devant ce chemin-là\nL’obéissance s’apprend ainsi \n\nChœur :\nLève les yeux vers lui\nCar il t’a précédé\nPar la souffrance il fut accompli\nAu feu il est passé glorieux fut son sort\nCar il obéit jusqu’à la mort \n\nReçois le châtiment, sois en reconnaissant\nDe l’amour de Dieu c’est le garant \nNe perds donc pas courage\nAvec toi ta peine et ta douleur\n\n"},{title:"QUAND JE VERRAI LE SANG",words:"Christ rédempteur est mort sur la croix\nPour le pécheur il paya son dû\nAspergez votre âme avec le sang\nEt je passerai par-dessus vous\n\nChœur :\n\nQuand je verrai le sang (3x)\nJe passerai par dessus-vous \n\nPauvres pécheurs Jésus sauvera\nIl fera tout ce qu’il a promis\nLavez-vous dans la fontaine ouverte\nEt je passerai par-dessus vous\n\nLe jugement vient et tous y seront\nChacun recevra juste son dû\nCachez-vous dans le sang qui blanchit\nEt je passerai par-dessus vous\n\nGrande compassion amour divin\nAmour bienveillant fidèle et sincère\nTrouvez paix et abri sous le sang\nEt je passerai par-dessus vous\n\n"},{title:"QUAND L’APPEL RETENTIRA",words:"Quand la trompette du Seigneur sonnera\nEt le temps n’est plus et viendra l’éternel matin radieux\nQuand les sauvés de la terre\nS’assembleront sur l’autre rive\nQuand l’appel retentira je serai là\n\nChœur :\nQuand l’appel retentira là-haut\nQuand l’appel retentira là-haut\nQuand l’appel retentira \nQuand l’appel retentira je serai là\n\nEn ce beau matin quand les morts en Christ ressusciteront \nPartageant la gloire de sa résurrection\nQuand les élus se rassembleront dans leur maison du ciel\nQuand l’appel retentira je serai là\n\nAinsi travaillant pour notre maître du matin au soir\nParlons de son merveilleux amour et soin \nAlors quand la vie s’en va et notre tâche accomplie\nQuand l’appel retentira je serai là\n\n"},{title:"QUAND LES SAINTS ENTRERONT",words:"Je suis pèlerin étranger\nErrant dans ce monde pécheur\nSur ma route vers la cité\nLorsque les saints entreront\n\nChœur :\n\nQuand les saints enteront\nLorsque les saints entreront\nSeigneur je veux être de ce nombre\nLorsque les saints entreront\n\nJe sais je verrai mon sauveur\nSi ma vie est libérée \nLe Seigneur m’ouvrira les portes\nLorsque les saints entreront\n\nAssemblés autour du trône\nEt les portes sont fermées\nJe crierai gloire et gloire\nLorsque les saints entreront\n\nJ’attends que le chariot descende\nEt je monterai à bord\nSur les nuées j’irai au ciel\nLorsque les saints entreront\n\n"},{title:"QUE LES ADORATEURS SE LEVENT",words:"Refrain :\n\nQue les adorateurs se lèvent\nQue les fils et filles le chantent\nJ’abandonne tous mes droits\nJ’abandonne tout au roi\n\nSeigneur tu as fait un chemin dans ma vie\nEt je veux être à tes côtés tenant ta main\nQue ton royaume vienne et qu’il vive en moi\nC’est ma prière \nC’est mon désir\n\nSeigneur je l’entends si fort\nLe chant des rédemptés\nComme les saints de toute nation\nSe levant pour chanter\nEt nos cœurs célèbrent un jour  nouveau\nEt les anges avec nous \nC’est notre chant pour notre roi\n\n"},{title:"QUE POURRAI-JE DIRE A MON SEIGNEUR",words:"Que pourrai-je dire à mon Seigneur\nCe que je peux dire, merci Seigneur\n\n\nWhat shall I say to the Lord\nAll I have to say, thank you Lord\n\n"},{title:"QUE TU ES GRAND",words:"Seigneur mon Dieu quand dans mon étonnement \nJe considère le monde fait par tes mains\nJe vois les étoiles et j’entends les tonnerres\nTa puissance plane à travers tout l’univers\n\nChœur :\n\nMon âme chante ô toi mon Dieu\nQue tu es grand (2x)\n\nQuand dans les bois dans la forêt je marche\nAlors j’entends le doux chant des oiseaux\nQuand je regarde du bout de la montagne\nJ’entends les ruisseaux et je sens la brise\n\nQuand je pense que Dieu n’épargna pas son fils\nIl l’envoya mourir oh ! Quel mystère\nQue sur la croix joyeux portant mon fardeau\nSaignant mourut pour ôter mon péché\n\nQuand Christ viendra avec pleine acclamation\nIl me prendra joyeux sera mon cœur\nJe vais m’incliné dans l’humble adoration\nEt proclamer mon Dieu que tu es grand\n\n"},{title:"QUEL AMI FIDELE ET TENDRE",words:"Quel ami fidèle et tendre nous avons en Jésus-Christ\nToujours prêt à nous entendre à répondre à notre cri\nIl connaît nos défaillances nos chutes de chaque jour\nSévère en ses exigences il est riche en son amour\n\nQuel ami fidèle et tendre nous avons en Jésus-Christ\nToujours prêt à nous comprendre quand nous sommes en souci\nDisons-lui toutes nos craintes ouvrons-lui tout notre cœur \nBientôt ses paroles saintes nous rendront le vrai bonheur\n\nQuel ami fidèle et tendre nous avons en Jésus-Christ\nToujours prêt à nous défendre quand nous presse l’ennemi\nIl nous suit dans la mêlée nous entoure de ses bras\nEt c’est lui qui tient l’épée qui décide des combats\n\nQuel ami fidèle et tendre nous avons en Jésus-Christ\nToujours prêt à nous apprendre à vaincre en comptant sur lui\nS’il nous voit vrai et sincère à chercher la sainteté\nIl écoute nos prières et nous met en liberté\n\nQuel ami fidèle et tendre nous avons en Jésus-Christ\nBientôt il viendra nous prendre pour être au ciel avec lui\nSuivons donc l’étroite voie en comptant sur son secours\nBientôt nous aurons la joie de vivre avec lui toujours\n\n"},{title:"QUEL BONHEUR DE MARCHER AVEC LUI",words:"Quel bonheur de marcher avec lui (2x)\nIl conduira tous les pas de mon voyage ici-bas\nQuel bonheur de marcher avec lui\n\nAlléluia gloire à son saint nom (2x)\nLe Seigneur m’a racheté dans son sang il m’a lavé\nAlléluia gloire à son saint nom \n\nAlléluia Jésus me guérit (2x)\nIl guérit parfaitement me libère entièrement\nAlléluia Jésus me guérit\n\nAvec lui montons toujours plus haut (2x)\nNe craignons pas le danger Jésus est le bon berger\nAvec lui montons toujours plus haut\n\nPlus haut sera le divin repos (2x)\nAvec Jésus dans les cieux nous serons toujours heureux\nPlus haut sera le divin repos \n\n"},{title:"QUEL JOUR CA SERA",words:"Il y a un jour qui vient\nOù les maux ne seront plus\nNi les nuages dans le ciel\nPlus de larmes à jamais\nTout est paix et pour toujours\nSur l’autre rive parée d’or\nO quel jour ! Glorieux jour ça sera\n\nChœur :\n\nOh quel beau jour ça sera \nQuand je verrai mon Jésus\nEt je vois de face \nCelui qui m’a sauvé par sa grâce\nQuand il me prend par la main\nMe conduit à la terre promise\nO quel jour ! Glorieux jour ça sera\n\nIl n’y aura ni chagrin\nNi fardeau à porter\nNon plu la maladie \nDouleurs et séparations\nPour toujours je serai avec\nCelui qui est mort pour moi\nO quel jour ! Glorieux jour ça sera\n\n"},{title:"QUEL MERVEILLEUX AMOUR",words:"Dieu dut aimer un pécheur tel que moi\nIl a changé ma tristesse en joie\nJ’étais sans repos il m’a ramené \nQuel est ce merveilleux amour\n\nChœur :\n\nOh quel merveilleux amour (2x)\nQue Dieu aima un pécheur tel que moi\nQuel est ce merveilleux amour\n\nEt Christ s’est donné librement pour moi\nCela coûta sa mort sur le calvaire\nLa langue humaine n’a pas de noble thème\nComme cet amour qui me sauva\n\nPour un perdu condamné comme moi\nLe père pourvut et le sauveur mourut\nC’est le rachat pour un esclave indigne\nQui longtemps méprisa la grâce\n\nEt maintenant il m’a pris comme un fils\nMe dit que je ne suis pas serviteur\nLes cloches sonnent non loin du pays\nSes bras sont largement ouverts\n\n"},{title:"QUEL MESSAGE PROFOND",words:"Oh quel message profond (3x)\nAu temps du soir\n\nIl pénètre mon cœur\nIl me transforme\nIl me rend prêt pour l’enlèvement\nOh quel message profond \nAu temps du soir\n\nLà-bas à Tucson\nL’ange est descendu\nIl a révélé les 7tonnerres\nOh quel message profond \nAu temps du soir\n\n"},{title:"QUEL SAUVEUR MERVEILLEUX",words:"Quel sauveur merveilleux je possède\nIl s’est sacrifié pour moi\nEt sa vie innocente il céda\nPour mourir sur l’infâme bois\n\nChœur :\n\nAttaché à la croix pour moi (2x)\nIl a pris mon péché \nIl m’a délivré\nAttaché à la croix pour moi\n\nIl renonce à la gloire céleste\nPour le plan rédempteur de Dieu\nHumble et d’apparence modeste\nOh ! Quel prix pour me libérer\n\nMaltraité pour indigne vie\nBrisé pour mon iniquité\nIl prit sur lui mes maladies\nEt mourut pour me rendre heureux\n\nLe salut accompli pour ses frères\nLe sauveur fut reçu au ciel\nIl revient oh ! Profond mystère\nMon bonheur sera éternel\n\n"},{title:"QUEL TEMOIGNAGE",words:"Souvent je me demande\nSi j’ai bien vécu parmi les hommes \nJe me demande si je suis fidèle\nA la mission que Dieu m’a confiée\n\nLe jour de ma mort qu’est-ce qu’on dira de moi\nQuels sont les fruits qu’aura laissés ma vie\nCar tous mes gestes et toutes mes paroles \nSont des ondes tout autour de la terre\n\nLe jour de ma mort qu’est-ce qu’on dira de moi\nQuel témoignage aura laissé ma vie\nCar tous mes gestes et toutes mes paroles \nSont surveillés par les oreilles du monde\n\nJe sais que par ma vie\nJ’influence la vie de quelqu’un d’autre\nJe me questionne si mon influence \nAmène les autres plus près de Dieu\n\nLe jour de ma mort qu’est-ce qu’on dira de moi\nQuels sont les fruits qu’aura laissés ma vie\nCar tous mes gestes et toutes mes paroles \nSont des ondes tout autour de la terre\n\nLe jour de ma mort qu’est-ce qu’on dira de moi\nQuel témoignage aura laissé ma vie\nCar tous mes gestes et toutes mes paroles \nSont surveillés par les oreilles du monde\n\nChaque fois je m’interroge\nJe fais souvent l’inventaire de ma vie\nPour ne pas être une pierre d’achoppement\nSur la route de mon prochain\n\nLe jour de ma mort qu’est-ce qu’on dira de moi\nQuels sont les fruits qu’aura laissés ma vie\nCar tous mes gestes et toutes mes paroles \nSont des ondes tout autour de la terre\n\nNe vous demandez pas qu’est-ce qu’on dira de moi\nQuel témoignage aura laissé ma vie\nTous mes gestes et toutes mes paroles\nSont contrôlés par mon entourage\n\nJe prie Dieu pour mes empruntes\nQui seront suivies par ceux qui viendront après moi\nSoient les empruntes qui ont été laissées \nPar Jésus-Christ au travers du sang\n\nLe jour de ma mort qu’est-ce qu’on dira de moi\nQuels sont les fruits qu’aura laissés ma vie\nCar tous mes gestes et toutes mes paroles \nSont des ondes tout autour de la terre\n\nNe vous demandez pas qu’est-ce qu’on dira de moi\nQuel témoignage aura laissé ma vie\nTous mes gestes et toutes mes paroles\nSont contrôlés par mon entourage\n\nMon Dieu puissent mes erreurs être d’un tremplin\nPour tous mes frères qui puissent s’en servir\nPour bien marcher j’aime les retrouver \nTous dans la gloire\n\nLe jour de ma mort qu’est-ce qu’on dira de moi\nQuels sont les fruits qu’aura laissés ma vie\nCar tous mes gestes et toutes mes paroles \nSont des ondes tout autour de la terre\n\nLors de mon voyage \nQu’est-ce qu’on dira de moi\nQuel témoignage aura laissé ma vie\nTous mes gestes et toutes mes paroles\nOnt de l’influence sur les autres\n\nLe jour de ma mort qu’est-ce qu’on dira de moi\nQuel témoignage aura laissé ma vie\nCar tous mes gestes et toutes mes paroles \nSont surveillés par les oreilles du monde\n\n"},{title:"QUI ME RELEVE DANS MES CHUTES",words:"Qui me relève dans mes chutes\nC’est Jésus-Christ\nQui combat pour moi dans mes luttes\nC’est Jésus-Christ\nJésus a parlé je veux croire\nQue je puis lutter pour sa gloire\nCar mon bouclier la victoire \nC’est Jésus-Christ\n\nJe vais à mon père et ma voie\nC’est Jésus-Christ\nJe suis bien heureux et ma joie\nC’est Jésus-Christ\nEt si même dans la souffrance\nMon cœur me parle d’espérance\nEn Jésus-Christ\n\nSauvé je ne me glorifie\nQu’en Jésus-Christ\nPour la terre le ciel ma vie\nC’est Jésus-Christ\nBientôt à Dieu choses mortelles \nLoin de vous je prendrai des ailes\nVers les demeures éternelles \nVers Jésus-Christ\n\n"},{title:"RAMENE SEIGNEUR",words:"Ramène, ramène Seigneur\nRamène ces beaux jours\nCes jours heureux\nQuand le Saint-Esprit\nTombait en tout lieu\nEt l’amour de Dieu \nEtait dans les cœurs\nRamène ces jours heureux\n\n"},{title:"REEL REEL, IL EST SI REEL POUR MOI",words:"Christ est réel, il est réel pour moi\nOh oui je l’aime \nIl me donne la victoire\nCar beaucoup de gens doutent \nJe ne peux vivre sans lui\nC’est pourquoi je l’aime tant\nIl est réel pour moi\n\n"},{title:"REMPLIS-MOI CHAQUE JOUR D’AMOUR",words:"Laisse-moi marcher Seigneur\nPar où tu es passé\nChemin conduisant vers le ciel\nPartout donnant la joie\nAux gens abandonnés\nRemplis-moi chaque jour d’amour\n\nChœur :\n\nRemplis-moi chaque jour d’amour\nComme je marche avec la colombe\nLaisse-moi marcher toujours \nAvec chants et sourires\nRemplis-moi chaque jour d’amour\n\nGarde-moi à côté \nDe mon sauveur et guide\nNe me laisse pas dans les ténèbres\nGarde-moi du courroux\nEt satisfait mon âme\nRemplis-moi chaque jour d’amour\n\nBientôt la fin de course \nJe ne voyagerai plus\nJ’habiterai la demeure céleste\nQue je chante roi béni\nSur la route vers la rive\nRemplis-moi chaque jour d’amour\n\n"},{title:"RENCONTRE DANS LES AIRS",words:"Tu as entendu Moïse dans les roseaux\nEt sur le courageux David et sa fronde\nTu as entendu l’histoire des songes de Joseph\nEt de Jonas et de la baleine que tu chantes\nIl y en a encore beaucoup, beaucoup dans la bible\nJ’aimerais les rencontrer je le déclare\nBientôt le Seigneur nous les fera rencontrer\nA cette rencontre dans les airs\n\nChœur :\nOui il y aura une rencontre dans les airs\nDans la douceur très bientôt je vais te rencontrer, rencontrer là-bas\nDans la maison au-delà du ciel tu vas écouter\nDes chansons jamais écoutées\nCa sera glorieux je déclare et le fils de Dieu sera\nLe seul guide à cette rencontre dans les airs\n\nA cette rencontre beaucoup de choses n’y seront\nCar le banc des affligés ne sera plus\nIl n’y aura plus de sermons prêchés aux pécheurs\nCar le pécheur aura rejeté l’appel\nEt il n’y aura plus de deuil sur nos bien-aimés\nTous nos fardeaux se dissiperont\nA cette rencontre dans les airs\n\nLes incrédules seront tous ensemble perdus\nTous les sceptiques seront absents en ce jour\nPlus de mécontents là-bas pour nous déranger\nLes Acans seront occupés ailleurs\nLà les saints de Dieu auront son sceau sur leurs fronts\nVêtus d’habits réservés aux rachetés\nCeux qui ont les habits des noces seront perdus\n\n"},{title:"RESTE AVEC NOUS SEIGNEUR",words:"Reste avec nous Seigneur le jour décline\nLa nuit s’approche et nous menace tous\nNous implorons ta présence divine\nReste avec nous Seigneur reste avec nous\n\nEn toi nos cœurs ont salué leur maître\nEn toi notre âme a trouvé son époux\nA la lumière, elle se sent renaître \nReste avec nous Seigneur reste avec nous\n\nDans nos combats si ta main nous délaisse\nSatan vainqueur nous tiendra sous ses coups\nQue ta puissance arme notre faiblesse\nReste avec nous Seigneur reste avec nous\n\nSous ton regard la joie est sainte et bonne \nPrès de ton cœur les pleurs même sont doux\nSoit que ta main nous frappe ou nous couronne\nReste avec nous Seigneur reste avec nous\n\n"},{title:"RIEN MAIS LE SANG",words:"Qu’est-ce qui peut laver mon péché\nRien que le sang de Jésus\nQu’est-ce qui peut me parfaire encore\nRien que le sang de Jésus\n\nChœur : Oh ! Précieux est le flot\n Qui me rend plus blanc que neige\nJe ne connais d’autre source\nRien que le sang de Jésus\n\nPour ma pureté, je ne vois\nRien que le sang de Jésus\nLa plaidoirie pour mon pardon\nRien que le sang de Jésus\n\nRien ne peut expier le péché\nRien que le sang de Jésus\nNi rien de bon que j’ai pu faire\nRien que le sang de Jésus\n\nC’est mon espoir et ma paix\nRien que le sang de Jésus\nC’est aussi ma justice\nRien que le sang de Jésus\n\nEt maintenant comment je vaincrai \nRien que le sang de Jésus\nComment j’atteindrai ma maison\nRien que le sang de Jésus\n\nGloire, gloire ainsi je chante\nRien que le sang de Jésus\nToute ma louange j’apporte\nRien que le sang de Jésus\n\n"},{title:"RIEN QUE TA VOLONTE SEIGNEUR",words:"Quelque fois sa volonté est contraire à nos désirs\nMais toutefois nous ne voulons que sa volonté, sa volonté\n\nRefrain : Ainsi moi je désire que sa volonté, sa volonté\nSa volonté moi je désire dans ma vie \nIl prie Moïse et le plaça dans une corbeille imbibée de bitume\nEt le mis sur le lac plein de crocodiles, plein de crocodiles\nBien que bizarre c’était la manière de Dieu de le protéger\nMais à la fin du chemin il fut le plus grand libérateur\nD’Israël, d’Israël\n\nLe général Naaman vint en Israël pour guérir de sa maladie\nElisée demanda qu’il aille se plonger \nDans les eaux du Jourdain, les eaux sales du Jourdain\nMais comme cela était contraire à son désir, il résista\nMais quand il l’appliqua, il découvrit \nLa gloire du Seigneur, la gloire du Seigneur\n\nJob perdit tout ce qu’il avait, ses biens et tous ses enfants\nMais il affirma l’éternel a donné, l’éternel a repris\nQue son nom soit béni\n\nJoseph fut arraché à l’affection de son père\nEt vendu à des marchands\nMais à la fin du chemin\nIl fut le plus grand intendant de Pharaon\n\nMême Jésus descendant le chemin de la rédemption\nIl voulut se dérober de sa volonté, sa volonté\nMais Dieu lui envoya un messager \nQui descendit du ciel pour le fortifier\nEt il s’exclama, non pas ma volonté mais sa volonté, sa volonté\n\n\nChœur :\n\nRoc séculaire frappé pour moi\nSur le calvaire je viens à toi\n\nOh purifie lave Seigneur\nEt sanctifie mon pauvre cœur \nMa main tremblante ne t’offre rien\nTa croix sanglante est mon seul bien\n\nDans la détresse sois mon berger\nMa forteresse dans le danger\nEt qu’à toute heure que chaque jour\nMon cœur demeure en ton amour\n\n"},{title:"ROCHER D’AGES",words:"Rocher d’âges fendu pour moi\nLaisse-moi me cacher en toi\nLaisse l’eau avec le sang \nQui coulèrent de tes plaies\nEtre un double remède\nSauver et me rendre pur\n\nQue mes larmes coulent à jamais\nEt mon zèle ne s’éteigne point\nCela n’ôte pas mon péché \nC’est toi seul qui dois sauver\nDans ma main rien n’est porté\nJe m’attache à ta croix \n\nAlors que j’aspire ce souffle\nQuand mes yeux se fermeront\nEt quand je me lèverai\nJe te verrai sur ton trône\nRocher d’âges fendu pour moi\nLaisse-moi me cacher en toi\n\n"},{title:"SEIGNEUR JE REVIENS",words:"J’étais errant loin de Dieu maintenant je reviens\nLe péché longtemps j’ai suivi Seigneur je reviens\n\nChœur : \nJe reviens je reviens pour ne plus errer\nOuvre tes larges bras d’amour Seigneur je reviens\n\nOui j’ai perdu beaucoup d’années maintenant je reviens\nJe me repens avec des larmes Seigneur je reviens\n\nFatigué du péché Seigneur maintenant je reviens\nJe me confie en ton amour Seigneur je reviens\n\nMon âme malade, mon cœur touché maintenant je reviens\nRedonne la force restaure l’espoir Seigneur je reviens\n\nMon seul espoir, ma seule prière maintenant je reviens\nEst que Jésus mourut pour moi Seigneur je reviens\n\nJe sais j’ai besoin de son sang maintenant je reviens\nOh ! Lave-moi plus blanc que la neige Seigneur je reviens\n\n"},{title:"SEIGNEUR TIENS MA MAIN",words:"Chœur :\nSeigneur tiens ma main (2x)\nOh sauveur incomparable\nToujours tiens ma main\n\nQuand je craindrai sur la route\nSeigneur tiens ma main\nQuand m’assaillira le doute\nSeigneur tiens ma main\n\nQuand le péché m’environne\nSeigneur tiens ma main\nLa victoire tu nous la donnes\nSeigneur tiens ma main\n\nDans la nuit d’épreuves amères\nSeigneurs tiens ma main \nConduis-moi vers la lumière \nSeigneur tiens ma main\n\nJe verrai ta main meurtrie\nPour toujours là-haut\nDans la céleste patrie \nOh divin agneau\n\n"},{title:"SEULE TA VOIE, SEIGNEUR",words:"Seule ta voie Seigneur rien que ta voie \nTu es le potier je suis l’argile\nFaçonne-moi par ta volonté\nPendant que j’attends faible et soumis\n\nSeule ta voie Seigneur rien que ta voie\nCherche et sonde-moi maître aujourd’hui\nPlus blanc que neige lave-moi maintenant\nComme je m’incline dans ta présence\n\nSeule ta voie Seigneur rien que ta voie\nBlessé fatigué aide-moi je prie\nToute puissance est sûrement à toi\nTouche et guéris-moi divin sauveur\n\nSeule ta voie Seigneur rien que ta voie\nTiens tout mon être influencé\nDonne-moi ton Esprit jusqu’à ce que\nJe verrai vivant en moi\n\n"},{title:"SOUFFLE SUR MON AME",words:"Souffle sur mon âme (2x)\nDoux Esprit, souffle sur mon âme\nMon repos est total\nQuand je suis, à tes pieds\nDoux Esprit souffle sur mon âme\n\n"},{title:"SOUFFLE SUR MOI SEIGNEUR",words:"Souffle sur moi seigneur\nRemplis-moi de ta vie\nPour que je marche dans tes voies\nLes yeux fixés sur toi\n\n"},{title:"SOUS LE SANG, LE PRECIEUX SANG",words:"Sous le sang, le précieux sang\nSous le sang, le précieux sang\nJe reçois un vrai cœur nouveau \nSous le précieux sang\n\n"},{title:"SUR CHRIST LE ROCHER",words:"Sur christ le rocher, je me tiens \nTout autre terrain est sable mouvant (2x)\n\nMon espoir n’est bâti sur rien\nD’autre que le sang de Jésus-Christ\nJe n’ose me fier à d’autres issues\nMais me reposer sur son nom \n\nQuand les nuées semblent cacher sa face \nJe compte sur la grâce immuable \nDevant chaque haute, impétueuse vague\nMon ancre tient au-delà du voile\n\nSon serment, son alliance et son\nSang restent mon soutien, lorsque je sombre\nQuand au-delà de moi tout cède\nIl est alors mon seul espoir \n\nIl vient au son de la trompette\nOh puis-je être trouvé en lui\nHabillé de sa seule justice\nIrréprochable devant son trône\n\n"},{title:"SUR LE CHEMIN VA SANS PEUR",words:"Sur le chemin va sans peur\nCar Jésus est devant toi\nJésus le Christ le rédempteur\nOh suis-le oh suis-le par la foi\n\nEt si tu tombes en chemin\nRegarde à Jésus ton roi\nIl est dans les lieux très saints\nEt il prie et il prie pour toi\n\nMaintenant saisis la main de ton sauveur\nCar lui seul peut donner l’éternel bonheur\nIl a donné sa vie sur la croix\nOh suis-le oh suis-le par la foi\n\n"},{title:"SUR LES AILES D’UNE COLOMBE",words:"Noé a bien navigué\nSur les eaux bien des jours \nIl chercha la terre ferme\nDe plusieurs manières\nDes ennuis il en a bien eu \nMais non pas du ciel\nDieu lui donna son digne (2x)\nSur les ailes d’une colombe\n\nChœur :\n\nSur les ailes d’une colombe \nBlanche comme neige\nDieu envoya son amour doux et pur\nUn signe du ciel (2x)\n\nJésus notre sauveur \nVint un jour sur la terre\nNé dans une étable\nDans une crèche de paille\nIci-bas rejeté\nMais non pas du ciel\nDieu nous donna son signe (2x)\nSur les ailes d’une colombe\n\nBien qu’ayant beaucoup souffert \nDe plusieurs manières\nJ’ai craint pour la guérison \nLa nuit comme le jour\nMais la foi ne fut pas oubliée\nPar le père du ciel\nIl me donna son signe (2x)\nSur les ailes d’une colombe\n\n"},{title:"SURELY THE PRESENCE OF THE LORD IS IN THIS PLACE",words:"Surely the presence of the Lord is in this place\nI can feel this mighty power and his grace \nI can feel the bwish of angel’s wings\nI see glory on each face\nSurely the presence of the Lord is in this place\n\nSûrement la présence du Seigneur est dans ce lieu\nJe peux sentir sa grande puissance et sa grâce\nJe peux sentir le battement des ailes des anges\nJe vois la gloire sur chaque visage\nSûrement la présence du Seigneur est dans ce lieu\n\n"},{title:"TANT QUE JE VIVRAI",words:"Tant que je vivrai\nSur la terre des hommes \nJe louerai ton nom\nA toi la gloire\nA toi la louange\nOh Dieu de Moïse\nJe louerai ton nom\n\n"},{title:"TEL QUE JE SUIS SANS RIEN A MOI",words:"Tel que je suis sans rien à moi\nSinon ton sang versé pour moi\nEt ton invitation à moi\nAgneau de Dieu je viens, je viens\nTel que je suis sans traîner\nDébarrasser mon âme de tout \nA toi dont le sang lave chaque tâche\nAgneau de Dieu je viens, je viens\n\nTel que je suis tu vas laver\nPardonner recevoir aider\nCar à ta promesse je crois\nAgneau de Dieu je viens, je viens\n\nTel que je suis ton grand amour\nA du renverser chaque barrière\nMaintenant pour être à toi seul\nAgneau de Dieu je viens, je viens\n\n"},{title:"TIENS L’IMMUABLE MAIN DE DIEU",words:"Le temps est plein de transitions\nRien sur terre n’est immuable\nEspère en des choses éternelles\nTiens l’immuable main de Dieu\n\nChœur :\nTiens l’immuable main de Dieu (2x)\nEspère en des choses éternelles \nTiens l’immuable main de Dieu \n\nConfie- toi en l’éternel Dieu\nQuoi qu’apportent les années\nSi tes amis t’abandonnent\nAttache-toi toujours à lui\n\nNe convoite pas les richesses vaines\nQui pourrissent rapidement\nCherche les trésors célestes\nQui ne passeront jamais\n\nQuand s’achève ton voyage\nSi tu étais fidèle à Dieu\nTon âme ravie dans la gloire \nTrouvera une belle demeure\n\n"},{title:"TOI MA PORTION ETERNELLE",words:"Toi ma portion éternelle\nPlus qu’un ami ou la vie\nPendant mon pèlerinage\nLaisse-moi marcher avec toi\nPrès de toi près de toi\nPrès de toi près de toi\nPendant mon pèlerinage\nLaisse-moi marcher avec toi\n\nMa prière ne sera jamais\nPour les plaisirs de ce monde\nOui je souffrirai avec joie\nLaisse-moi marcher avec toi\nPrès de toi près de toi\nPrès de toi près de toi\nOui je souffrirai avec joie\nLaisse-moi marcher avec toi\n\nPuisses-tu me conduire par-dessus\nLa mer houleuse de la vie\nPuis-je alors entrer dans la gloire\nAvec toi oh mon Seigneur\nPrès de toi près de toi\nPrès de toi près de toi\nPuis-je alors entrer dans la gloire\nAvec toi oh mon Seigneur\n\n"},{title:"TON AMOUR NOUS ENVIRONNE",words:"Ton amour nous environne ô Seigneur\nTa gloire resplendit tout comme le soleil\nTon amour nous environne ô Seigneur\nC’est pourquoi nous espérons tous en toi\n\nChœur :\n\nGloire à ton nom auteur de notre joie\nGloire à ton nom toi qui nous a sauvés\nGloire à ton nom toi qui nous aime tant \nC’est pourquoi nous espérons tous en toi\n\nTu n’as rien à caché à ton épouse\nLes mystères de Dieu sont tous révélés\nNous voyons le 7ème sceau ouvert\nC’est pourquoi nous attendons tous le départ\n\nTu n’as pas exigé de garantie\nAvant de manifester ton amour\nTon bras de paix tu l’as tendu vers nous\nPour nous arracher du feu de l’enfer\n\n"},{title:"TON NOM LE PLUS BEAU DES POEMES",words:"Je le glorifierai et je l’exalterai\nJe le proclamerai sur la terre des hommes\nJe parlerai de toi je porterai ma croix\nToujours je chanterai\nJésus ce nom qui sauve\n\nTon  nom le plus beau des poèmes\nTon  nom la plus belle chanson\nTon  nom il est celui-là que j’aime\nTon  nom est le plus grand des noms\n\nTon  nom humilié sur la terre\nTon  nom méprisé par les siens\nTon  nom c’est le nom des chrétiens\nTon  nom est le plus grand des noms\n\nTon  nom resplendissant d’étoiles\nTon  nom soleil brillant d’amour\nTon  nom a déchiré le voile\nTon  nom brillera pour toujours\n\n"},{title:"TOUS ACCLAMENT LE NOM DE JESUS",words:"Tous acclament le nom de Jésus\nQue les anges s’inclinent\nApportez le diadème royal couronnez le Seigneur (2x)\n\nVous la race élue d’Israël\nRachetée de la chute\nAcclamez-le car il vous sauve couronnez le Seigneur (2x)\n\nPécheurs vous qui n’oublierez\nLes souffrances de Jésus\nJetez vos trophées à ses pieds couronnez le Seigneur (2x)\n\nVous tous les hommes et les tribus\nSur la face de la terre\nAttribuez-lui la majesté couronnez le Seigneur (2x)\n\nNous nous inclinons là-haut ensemble à ses pieds\nChantons à ses pieds\nChantons le cantique éternel couronnons le Seigneur (2x)\n\n"},{title:"TOUT CONCOURT POUR MON BIEN",words:"Chœur :\n\nTout concourt pour mon bien\nParce que j’aime Jésus\nTout travaille pour mon bien\nParce que j’aime le Roi\n\nEt quand l’épreuve m’arrive\nElle travaille pour mon bien\nJ’apprends à bien combattre\nPuisque j’aime Jésus\n\nEt quand dans les douleurs\nElle travaille pour mon bien\nElle fortifie ma foi\nPuisque j’aime Jésus\n\nEt quand le vent me bouge\nIl travaille pour mon bien\nJ’enfonce mes racines\nParce que j’aime Jésus\n\nEt quand la mort me frappe\nElle travaille pour mon bien\nJe vais me reposer\nAuprès du Roi des rois\n\nEt quand la tentation vient \nElle travaille pour mon bien\nJ’apprends à faire la guerre\nPuisque j’aime le Roi\n\nPour chaque circonstance\nElle travaille pour mon bien\nC’est le dessein de Dieu\nParce que j’aime le Roi\n\n"},{title:"TOUT S’ACCOMPLIRA",words:"Refrain : Tout s’accomplira (2x)\nLa parole nous est prêchée, ça s’accomplira \nLa promesse nous ai donnée, la pluie va tomber\nL’ange puissant marche avec nous, ça s’accomplira\nLes miracles et prodiges s’accompliront sans problème\n\n"},{title:"TOUT VIENT DE TOI",words:"Tout vient de toi ô père très bon \nNous t’offrons les merveilles de ton amour\n\nVoici Seigneur ton peuple assemblé\nJoyeux de te célébrer\n\nVoici le fruit de tous nos travaux\nL’offrande d’un cœur nouveau\n\nVoici la joie de notre amitié\nL’amour nous a rassemblés\n\nVoici l’effort des hommes \nD’esprit servent dans l’univers\n\nMerci Seigneur ta vie est en nous\nMerci pour ton amour\n\nLouange à toi ô père très bon\nA toi toute adoration\n\n"},{title:"TRACE MON CHEMIN POUR CANAAN",words:"Trace mon chemin pour Canaan (3x)\nTrace mon chemin Seigneur pour Canaan\nMon frère, ma sœur allons-y ensemble (3x)\nTrace mon chemin Seigneur pour Canaan\nSi tu ne vas pas, ne m’empêche pas (3x)\nTrace mon chemin Seigneur pour Canaan\n\n"},{title:"TRAVAILLONS JUSQU'A CE QUE JESUS VIENNE",words:"Terre de repos je te désire\nQuand viendra-t-il le temps\nOù je déposerai mes armes\nPour vivre en paix chez toi\n\nChœur :\n\nNous travaillons jusqu’à ce que Jésus vienne\nJusqu’à ce que Jésus vienne\nNous travaillerons nous irons à la maison\n\nEn Jésus-christ je me repose\nIl m’a tranquillisé\nEt je m’appuie sur sa poitrine\nIl me conduit au ciel\n\nJ’ai racheté la face du sauveur \nJe ne vais plus errer\nAvec lui je défie la mort\nJ’arriverai au ciel\n\n"},{title:"TROUVE UN MOMENT",words:"Lorsqu’on a perdu courage\nSous un labeur accablant\nEt si tu sens gronder l’orage\nO prier trouve un moment\n\nChanter :\nTrouve un moment dès l’aurore\nUn autre au milieu du jour\nEt le soir un instant encore\nO prier avec amour\n\nEt si parfois ton pied chancelle\nSous un fardeau trop pesant\nPour avoir une ardeur nouvelle\nSois en prière un moment\n\nEt si tu pouvais prier sans cesse\nSans effort tout simplement \nTu seras fort dans ta faiblesse \nEn priant à tout moment\n\n"},{title:"TU M’AS AIME",words:"Tu m’as aimé\nTu m’as sauvé \nTu m’as choisi\n\nJ’étais en route pour le monde \nTu m’as aimé et tu mourus à la croix\nTu m’as aimé oh ! Et j’ai reçu ton message\n\nJ’étais en route pour l’enfer\nTu m’as aimé et par ton précieux sang\nTu m’as aimé oh ! Tu m’as marqué\nDe ton sceau, tu m’as choisi\n\nAlléluia ! Gloire à Dieu \nTu m’as aimé, j’ai dans mon cœur \nL’assurance que tu m’as aimé\nEt tu viendras me chercher\nTu m’as choisi\n\n"},{title:"TU N’AS QU’A FAIRE UN PAS",words:"Un parfait sauveur est ici\nQu’il t’invite à te jeter dans ses bras\nUn salut parfait il t’acquiert le mérite\nTu n’as qu’à faire un pas \nChœur :\nUn seul pas, un seul pas et Jésus te recevra\nUn seul pas jette-toi dans ses bras\nCeux qu’il affranchit sont entièrement libres\nTu n’as qu’à faire un pas\n\nC’est lui c’est seul qui peut briser les chaînes\nDes esclaves de satan tes larmes à ses pieds\nNe sont jamais vaines Jésus pardonne à l’instant\n\nPense aux nombreux pas qu’il fait à ta rencontre\nIl marcha jusqu’au trépas et pour le livrer\nA l’amour qui te manque il ne te reste qu’un pas\n\n"},{title:"TU PEUX NAITRE DE NOUVEAU",words:"Tu peux naître de nouveau tu peux tout recommencer\nBalayer ta vie passée et repartir à zéro, et repartir à zéro\nAvec Jésus pour berger\n\nTu peux boire de cette eau source de la pureté\nSource de la vérité tu peux boire de cette eau\nTu peux boire de cette eau source de l’éternité\n\nTu peux être pardonné pour tous tes péchés passés\nCar Jésus a tout payé tu peux recevoir la paix\nTu peux recevoir la paix source de la liberté \nEt repartir à zéro avec Jésus pour berger\n\n"},{title:"UN HOMME NOUVEAU",words:"Refrain :\nSeigneur Jésus, j’ai un vœu dans mon cœur \nFais de moi un homme nouveau pour te servir\nDis seulement un mot, Seigneur dis seulement un mot\nJe serai un homme nouveau à ton service\n\nLe fou de Gadara, la terreur de la région \nPrisonnier de démons, retenu dans les sépulcres\nIl te rencontra Seigneur dès ce moment-là\nCe fut un homme changé à ton service\n\nMarie de Magdala, la femme prostituée\nCondamnée par la loi et rejetée de ces semblables\nElle te rencontra Seigneur dès ce moment-là\nCe fut une femme changée pour ton honneur\n\nSeigneur Jésus-Christ sauveur divin\nRédempteur de l’épouse, Jéhovah Jiré\nJéhovah Shama, pourvois pour âme\nBaptise-moi de ton feu \nJe serai un homme nouveau à ton service\n\nSeigneur Jésus-Christ, c’est le vœu de mon cœur \nSauveur divin voici le vœu de mon cœur \nPourvois pour mon âme, baptise-moi de ton feu\nJe serai un homme nouveau à ton honneur\n\n"},{title:"UN JOUR JESUS VINT",words:"Et maintenant, c’est en chantant\nQue je suis le chemin du salut\nUn jour Jésus-Christ \nVint habiter mon cœur\nOh ! Quel heureux jour\n\nUn jour Jésus-Christ \nVint habiter mon cœur\nOh ! Quel bonheur \nParfait bonheur \nIl chassa la nuit \nEssuya tous mes pleurs\nOh ! Quel heureux jour\n\n"},{title:"UNE COURONNE JESUS LA DONNE",words:"Une couronne, Jésus la donne \nAux soldats de la croix\nPour toi, pour moi\nPour tous ceux qui combattront en son nom\nJésus donnera cette couronne (2x)\n\n"},{title:"VERS JESUS LEVE LES YEUX",words:"Si tu savais combien les plaisirs du monde \nOnt déchiré, brisé bien de cœurs\nTu viendrais vers Jésus dont la grâce abonde\nDe son amour tu verrais la douleur\n\nChœur :\n\nVers Jésus lève les yeux \nContemple son visage merveilleux\nEt les choses de la terre pâliront peu à peu\nSi tu lèves vers Jésus les yeux\n\nChrist est pour moi un sauveur admirable\nUn conseiller, un ami merveilleux\nSon nom béni n’a rien de comparable\nSur cette terre ni là-haut dans les cieux\n\nTout près de moi quand je suis dans la crainte\nIl se tient à m’encourager\nSon cœur aimant me guide sans contrainte\nPour avancer au travers des dangers\n\n"},{title:"VERS TOI MONTE NOTRE HOMMAGE",words:"Vers toi monte notre hommage fils de Dieu puissant sauveur\nQui demeure d’âge en âge le refuge du pécheur\nChœur :\n\nLoué soit ton amour loués soient à jamais\nJésus ta gloire et tes bienfaits\nLoué soit ton amour loués soient à jamais \nTon nom ta gloire et tes bienfaits\n\nDe toi vient la délivrance tu payas notre rançon\nC’est en toi qu’est l’espérance la paix et la guérison\n\nOh qu’heureux sous ta bannière est le temps racheté\nQui marche dans ta lumière vers la céleste cité\nPar ta divine parole tu l’enseigne tu l’instruis\nEt par l’Esprit qui conseille  sûrement tu le conduis\n\nBientôt glorieuse attente tu combleras tous nos vœux\nSur la nuée étincelante tu viendras à nous des cieux\n\n"},{title:"VIENS ADORER",words:"Viens adorer le Seigneur (2x)\nEt que ton cœur soit comme un océan d’amour\nViens adorer le Seigneur\nViens élever le Seigneur (2x)\nEt que ton cœur soit comme un océan d’amour\nViens élever le Seigneur\nViens magnifier le Seigneur (2x)\nEt que ton cœur soit comme un océan d’amour\nViens magnifier le Seigneur \n\n"},{title:"VIENS DINER",words:"Jésus a dressé la table pour nourrir\nLes saints de Dieu\nIl invite ses élus : viens au repas\nDe sa manne il nous nourrit\nSubvient à tous nos besoins\nIl est bon de souper avec Jésus-Christ\n\nViens dîner le maître t’appelle viens dîner\nOui festoyer à sa table tout le temps\nLui qui rassasia la foule \nEt changea de l’eau en vin\nToi qui a faim il t’appelle viens dîner\n\nL’agneau prendra son épouse\nPour qu’elle soit à ses côtés\nL’armée céleste sera là viens au repas\nCe sera un glorieux jour\nTous les saints vêtus de blanc\nAvec Jésus festoyons éternellement\n\n"},{title:"VIEUX PELERIN",words:"Vieux pèlerin qui vagabonde\nJe suis partout un étranger\nMais je suis sûr qu’en l’autre monde\nDieu va m’offrir où me loger\n\nRefrain : Je vais là-bas revoir mon père\nFini pour moi de cheminer\nA l’autre bord de la rivière\nMaison à moi je vais trouver\n\nJ’achèverai bientôt ma route\nJ’entends tout proche le Jourdain \nLa mort n’a rien que je redoute\nJ’y laisserai tous mes chagrins\n\nRefrain : Je vais là-bas revoir mon père\nPrès de lui enfin me consoler\nA l’autre bord de la rivière\nA la maison me reposer\n\nVoici la fin de mes souffrances\nEt le repos de mon vieux corps\nVoici venu la récompense \nPar dieu promise à mes efforts\n\nRefrain : Je vais là-bas parmi les anges\n En oubliant mes soucis\nPasser mon temps à sa louange\nDire à Jésus sans fin merci\n\n"},{title:"VILLES DE PERLES ET DE LUMIERES",words:"Villes de perles et de lumières\nExistant sur des hauteurs\nPersonne n’a encore sur terre \nPu contempler sa splendeur\n\nChœur :\nLà l’amour de Dieu éclaire\nCe sera l’agneau lui-même\nDans cette veille hospitalière\nLa nouvelle Jérusalem\n\nC’est là où la gloire abonde \nOù l’arbre de vie fleurit\nOù le fleuve de vie inonde \nDe ses flots ce saint abri\n\nQuand les pèlerins arrivent\nEpuisés de grand désert\nAux fontaines des eaux vives\nQuel repos leur est offert\n\nOh ! Quelle joie bientôt sur terre\nQuand Jésus sera présent\nCette ville aux mille lumières \nCe sera éternellement\n\n"},{title:"VIVANT IL M’AIMA",words:"Vivant il m’aima, mourant il me sauva\nEnseveli, il emporta mes péchés au loin\nRessuscitant il me justifia \nA jamais pour être libre\n\nUn jour il va venir oh quel jour glorieux (2x)\n\nQuand la bataille sera terminée\nLes bibles seront fermées \nNous déposerons nos armes\nEt nous voyagerons comme des pensées dans les airs\n\nRefrain : Moi je sais, je sais, je sais qu’il est vivant\nJe sais que mon rédempteur est vivant \nEt que le Saint-Esprit c’est le sceau de la promesse ô ô\n\n\nAbraham passa 25 ans d’épreuves \nAvant de voir le fils de la promesse\n\nMoïse passa dans le désert avant de voir la colonne de feu\nDaniel passa dans la fosse aux lions \nAvant de voir l’ange de l’éternel\nLes 3 hébreux passèrent dans la fournaise \nAvant de voir le fils de fils de Dieu\nJob perdit tout ce qu’il avait \nAvant de dans une vision la résurrection\n\nLa nouvelle naissance est une réalité\nLe baptême de l’Esprit est une réalité\nLe millénium est une réalité\nLe 7ème sceau est une réalité\nL’enlèvement, c’est une réalité (2x)\n\n"},{title:"VOICI LE JOUR QUE DIEU A CREE",words:"Voici le jour (2x) que Dieu a créé (2x)\nIl est pour nous (2x) un sujet de joie (2x)\nVoici le jour que Dieu a créé \nIl est pour nous un sujet de joie\nVoici le jour (2x) que Dieu a créé\n\n"},{title:"VOUS RECEVREZ UNE PUISSANCE",words:"Vous recevrez une puissance\nVenant du père, et vous serez mes témoins \nDans cette ville \nDans ce beau pays \nSur ce continent \nDans le monde entier\nLes cœurs sont touchés\nLes malades guérissent\nGlorifions Jésus\nC’est l’Esprit saint qui agit\nDans cette ville \nDans ce beau pays\nSur ce continent\nDans le monde entier\nL’Esprit en nous, l’Esprit sur nous\nGlorifions Jésus\nC’est l’Esprit saint qui agit…\n\n"}];

