import React from 'react';
import { CardList } from './components/card-list/card-list.component';
import { LoaderBox } from './components/loader-box/loader-box.component';
import { SearchBox } from './components/search-box/search-box.component';
import SoundInterface from './interfaces/sound.interface';

import SoundsList from './data/sounds/sound.data';

export default class App extends React.Component {
  private postEndpoint = "https://jsonplaceholder.typicode.com/posts";

  state: { postData: Array<SoundInterface>,filteredData: Array<SoundInterface>,isLoading:boolean } = {
     postData: [],
     filteredData:[],
     isLoading:true, 
    };
  


  componentDidMount() {
    this.formatData(SoundsList);
  }


  //Data formatter
  formatData = (response: SoundInterface[]) => {
    const data:Array<SoundInterface>=response;
    this.setState({
      postData: data,
      filteredData:data,
      isLoading:false,
    });
  }

  onSearch=(e:React.ChangeEvent<HTMLInputElement>)=>{
    const searchText=e.target.value;
    const filteredData=this.state.postData.filter((data)=>data.title.toLowerCase().includes(searchText.toLowerCase()));
    this.setState({
      filteredData
    })
  }



  //Rendering the ui
  render() {
    return (
this.state.isLoading?<LoaderBox />:
      <div>
        <SearchBox onChange={this.onSearch} />
      <CardList itemsList={this.state.filteredData} />
      </div>
    );
  }
}