import React,{useState} from 'react';
import Popup from 'reactjs-popup';
import { Card } from '../card/card.component';
import './sound-popup.styles.scss';
    
const PopPupCard=(props:any)=> {
    const [open, setOpen] = useState(false);
    const [fontSize,setFontSize]=useState(19);
  const closeModal = () => setOpen(false);

  //Increase font size
  const increaseFontSize=()=>{
    const newFontSize=fontSize+3;
    setFontSize((old)=>newFontSize);
  };
  //Descrease font size
  const decreaseFontSize=()=>{
    const newFontSize=fontSize-3;
    setFontSize((old)=>newFontSize);
  };

  return (
    <div>
        <Card onClick={() => setOpen(o => !o)} data={props.data} />
      <Popup open={open} closeOnDocumentClick onClose={closeModal}>
        <div className="modal">
          <a className="close" onClick={closeModal}>
            &times;
          </a>
            <div className="modal-content">
                <div className="control-font-size">
                    <button onClick={decreaseFontSize} style={{border:"none", fontSize:25,marginRight:3,backgroundColor:'transparent'}}>-</button>
                    <button onClick={increaseFontSize} style={{border:"none",fontSize:25,backgroundColor:'transparent'}}>+</button>
                </div>
            <h5 style={{marginBottom:15}}>{props.data.title}</h5>
              {
                  props.data.words.split("\n\n").map((i:string,key:number) => {
                    return <div style={{marginTop:i.includes('Chœur')|| i.includes('\n') ?20:'',fontSize:fontSize}} key={key}>{
                        props.data.words.split("\n").map((i:string,key:number) => {
                            return <div style={{marginTop:i.includes('Chœur')|| i.includes('\n') ?20:'',fontSize:fontSize}} key={key}>{i}</div>;
                        })
                    }</div>;
                })
              }
            </div>
        </div>
      </Popup>
    </div>
  );
}

export default PopPupCard;